<template>
    <!--表单操作的抽屉-->
    <div class="fromEditdrawer">
        <el-drawer
                :title="title"
                :visible.sync="is_show"
                direction="rtl"
                :size="width"
                :before-close="cancel">
            <div class="con">
                <hForm :form_des="form_des" @sure="sure" @cancel="cancel"></hForm>
            </div>

                <!--                <el-button @click="cancel">取 消</el-button>-->
                <!--                <el-button type="primary" @click="sure">确 定</el-button>-->

        </el-drawer>
    </div>


</template>

<script>
    import hForm from './hForm'

    export default {
        name: "fromEditdrawer",
        components:{hForm},
        data() {
            return {
                is_show: false,
            }

        },
        props: {
            title: {
                type: String,
                default: '标题'
            },
            width: {
                default: '50%'
            },
            form_des:{
                type:Array,
                default(){
                    return [];
                }
            }
        },
        methods: {
            /*展现*/
            show() {
                this.is_show = true;
            },
            /*隐藏*/
            hide() {
                this.is_show = false;
            },
            /*取消*/
            cancel() {
                this.hide()

            },
            /*确认*/
            sure(val) {

                this.hide();

                this.$emit('sure',val)

            }
        }
    }
</script>

<style scoped lang="less">

    .fromEditdrawer{

    }
    .con{

        padding: 0 40px;
    }
</style>