<template>
    <div class="msTransaction">
        <headMenus
                @onSearch="onSearch"
                @onRefresh="onRefresh"

        ></headMenus>

        <hTable
                :list="list"
                :total="total"
                :table_des="table_des"
                :page_len="params.page_len"
                :currentPage="params.page_num"
                @currentChange="currentChange"
                @tableChoose="tableChoose"
                @sortChange="sortChange"
                :loading="loading"
        ></hTable>
        <formEditModal
                ref="formEditModal"
                title="搜索"
                :form_des="form_des"
                @sure="sureSearch"
                @datetimeRangeChange="datetimeRangeChange"
        ></formEditModal>


    </div>


</template>

<script>
    import headMenus from '../../_components/headMenus'
    import hTable from '../../_components/hTable'
    import formEditModal from '../../_components/formEditModal'
    import tableFromMix from '../../_commom/mixins/tableFromMix'

    import pageEvent from "../../_commom/lib/pageEvent";

    import coneHttpDeal from '../../_commom/connectorHttp/coneHttpDeal'
    import utils from "../../_commom/lib/utils";
    import coneHttpShop from "../../_commom/connectorHttp/coneHttpShop";

    export default {
        name: "msTransaction",
        mixins: [tableFromMix],

        components: {headMenus, hTable, formEditModal},
        data() {
            return {
                params: {

                    shop_uuid: '',
                    trade_type: '',
                    trade_code: '',
                    min_money: '',
                    max_money: '',
                    start_time: '',
                    end_time: '',

                    order_field: null,
                    order_sort: '',
                    page_num: 1,
                    page_len: 20,

                },
                table_des: [
                    {sortable: false, label: '#', prop: '_index', width: '50'},
                    {
                        sortable: false, label: '交易类型', prop: 'trade_type', width: '',
                        render(scope) {
                            let row = scope.row;
                            let obj = {
                                '10':'订单',
                                '20':'充值',
                                '30':'退款',
                            };
                            return obj[row.trade_type+''];
                        }
                    }
                    , {sortable: false, label: '交易码', prop: 'trade_code', width: ''}
                    , {sortable: false, label: '交易金额/分', prop: 'trade_money', width: ''}
                    ,
                    {
                        sortable: false, label: '交易状态', prop: 'trade_state', width: '',
                        render(scope) {
                            let row = scope.row;
                            let obj = {
                                '0':'等待交易',
                                '10':'交易成功',
                                '-10':'交易失败',
                            };
                            return obj[row.trade_type+''];
                        }
                    }
                    , {sortable: false, label: '交易备注', prop: 'remark', width: ''}
                    , {sortable: 'custom', label: '添加时间', prop: 'add_time', width: 'auto'}

                ],
      /*          /!*搜索字段*!/
                search_form_params:{
                    trade_type:'',
                    trade_code:'',
                    start_time:'',
                    end_time:'',
                },*/
                /*搜索字段表单配置*/
                form_des: [
                    {
                        field: 'shop_uuid', type: 'searchSelect', label: '商户', value: "",
                        handler:'getShopList', //远程事件
                        placeholder:'请输入搜索',
                        options: [
                            // {name:'苹果',value:'1'},
                            // {name:'香蕉',value:'2'}
                        ],

                    },
                    {
                        field: 'trade_type', type: 'radio', label: '交易类型', value: '',
                        options: [
                            {name:'全部',value:''},
                            {name:'等待交易',value:'0'},
                            {name:'交易成功',value:'10'},
                            {name:'交易失败',value:'-10'},

                        ]
                    },
                    {field: 'trade_code', type: 'text', label: '关联结算交易编号', value: '',},
                    {field: 'min_money', type: 'number_positive', label: '涉及金额最大值', value: '',},
                    {field: 'max_money', type: 'number_positive', label: '涉及金额最小值', value: '',},
                    {
                        field_type:'composite',
                        type: 'datetimeRange',
                        field:'start_time',
                        label: '时间范围',
                        field_arr:[
                            {field: 'start_time',   value: ''},
                            {field: 'end_time',   value: ''}
                        ]
                        // rules:[
                        //     {trigger: 'blur',required: true ,message: '时间不为空',}
                        // ],
                        // field:'start_time,end_time',
                        // // value: '',
                        // type: 'datetimeRange', label: '时间范围',
                        // fields: {
                        //     field_arr:['start_time','end_time'],
                        //     value_arr:['','']
                        // }
                    }
                    // {field: 'start_time,end_time', type: 'datetimeRange', label: '时间范围', value: '',}


                ],

            }

        },
        methods: {

            /*获取数据列表*/
            getList(params1) {


                this.loading = true;

                let params =params1|| this.params;
                return new Promise((resolve, reject) => {
                    coneHttpDeal.ManageShopTradeList(params)
                        .then(res => {
                            let data = res.data;


                            this.total = data.total / 1;
                            let rows = data.rows;

                            let page_len = params.page_len,
                                page_num = params.page_num;
                            let list = this._disposeList(rows, page_num, page_len);
                            this.loading = false;


                            // console.log('列表数据', this.list)
                            resolve(list)
                        })

                })
            },

            /*时间范围选择*/
            datetimeRangeChange(val){
                this.search_form_params.start_time=val[0];
                this.search_form_params.end_time=val[1];

                // this.addDataFrom(this.form_des,this.search_form_params);
                // console.log('this.form_des',this.form_des)

            }






            /*获取商户列表*/
            ,getShopList(val){

                 coneHttpShop.getMemberShopList({name:val.value,page_num:1,page_len:utils.getConfig('select_page_len')})
                    .then(res=>{

                        let list =res.data.rows;
                        let options = list.map(v=>{
                            return {name:v.name,value:v.uuid}
                        })
                        utils.editObjField(val.from_des,{ field: 'shop_uuid'}, {options});

                    });



            }

        },
        mounted() {
            this.getList()
                .then(list => {
                    this.list = list
                })
            pageEvent.$off('getShopList',null);
            pageEvent.$on('getShopList',this.getShopList);
            // this.$refs.formEditModal.show()
        }
    }
</script>
<style lang="less" scoped>
    @import "../../css/common";

</style>