<template>
    <div class="shopList h100 posi-relative">
        <!--        <areaChoose></areaChoose>-->
        <headMenus
                @onSearch="onSearch"
                @onRefresh="onRefresh"
                @handleAdd="handleAdd"
                @handleEdit="handleEdit"
                @handleDel="handleDel"
                @handleDetail="handleDetail"
                @handleAddSup="handleAddSup"
                @handleExamine="handleExamine"
                @handleReset="handleReset"
        ></headMenus>
        <!--        <areaChoose></areaChoose>-->
        <hTable
                :list="list"
                :total="total"
                :table_des="table_des"
                :page_len="params.page_len"
                :currentPage="params.page_num"
                @currentChange="currentChange"
                @tableChoose="tableChoose"
                @sortChange="sortChange"
                :loading="loading"
        ></hTable>
        <formEditModal
                ref="formEditModal"
                title="搜索"
                width="30%"
                :form_des="form_des"
                @sure="sureSearch"
        ></formEditModal>
        <formEditModal
                ref="addSuperAdmin"
                title="新增超级管理员"
                width="30%"
                :form_des="super_admin_from_des"
                @sure="sureAddSuperAdmin"
        ></formEditModal>
        <formEditModal
                ref="resetSuperAdmin"
                title="重置超级管理员"
                width="30%"
                :form_des="super_admin_reset_from_des"
                @sure="sureResetSuperAdmin"
        ></formEditModal>
        <fromEditDrawer
                ref="addShop"
                title="新增商户"
                width="50%"
                :form_des="shop_from_des"
                @sure="sureAddShop"
        ></fromEditDrawer>
        <formEditModal
                ref="editShop"
                title="编辑商户"
                width="50%"
                :form_des="shop_edit_from_des"
                @sure="sureEditShop"

        ></formEditModal>
        <showDetailModal ref="showDetailModal" title="详情"></showDetailModal>
    </div>


</template>

<script>
    import headMenus from '../../_components/headMenus'
    import hTable from '../../_components/hTable'
    import formEditModal from '../../_components/formEditModal'
    import fromEditDrawer from '../../_components/fromEditDrawer'
    import showDetailModal from '../../_components/showDetailModal'
    import areaChoose from '../../_components/areaChoose'
    import coneHttpShop from '../../_commom/connectorHttp/coneHttpShop'

    import tableFromMix from '../../_commom/mixins/tableFromMix'
    import utils from '../../_commom/lib/utils'
    import pageEvent from "../../_commom/lib/pageEvent";

    console.log('tableFromMix',tableFromMix)
    export default {
        name: "shopList",
        components: {headMenus, hTable, formEditModal, fromEditDrawer, showDetailModal},
        mixins: [tableFromMix],
        data() {

            let self = this;



            return {
                //获取数据列表参数
                params: {

                    name: '', //商户名称
                    state: '',//商户状态 0.待审,1.审核通过,-1.作废
                    prov_uuid: '',//省
                    city_uuid: '',//市
                    area_uuid: '',//区
                    addr: '',//详细地址
                    local: '',//当前经纬度
                    range: '',//距离范围
                    is_pause: '',//是否暂停营业 0:营业,1.暂停营业
                    is_work_day: '',//当天开业的商铺 1.当天营业的商铺,0.当天不营业的商铺
                    is_now_work: '',//当前还营业时间范围的商铺 0.非营业范围,1.营业范围

                    order_field: null,
                    order_sort: '',
                    page_num: 1,
                    page_len: 20,

                },
                table_des: [
                    {sortable: false, label: '#', prop: '_list_index', width: '50'}
                    // , {sortable: false, label: 'lat', prop: 'lat', width: ''}
                    // , {sortable: false, label: 'lng', prop: 'lng', width: ''}

                    , {sortable: false, label: '商户名称', prop: 'name', width: ''}
                    // , {sortable: false, label: '商户简介', prop: 'remark', width: ''}
                    // , {sortable: false, label: '商户描叙', prop: 'detail', width: ''}
                    , {sortable: false, label: '商户地址', prop: 'addr', width: ''}
                    , {sortable: false, label: '联系人', prop: 'contact_name', width: ''}
                    , {sortable: false, label: '联系电话', prop: 'contact_tel', width: ''}
                    // , {sortable: false, label: '工作日开始时间', prop: 'day_start_time', width: ''}
                    // , {sortable: false, label: '工作日结束时间', prop: 'day_end_time', width: ''}
                    // , {sortable: false, label: '周工作天数', prop: 'week_work_day', width: ''}
                    // ,{sortable:false,  label:'商户状态'  ,   prop:'state'      ,     width:'',
                    //     filters:[
                    //         { text: '待审', value: '0' },
                    //         { text: '审核通过', value: '1' },
                    //         { text: '作废', value: '-1' },
                    //     ]
                    // }
                    , {
                        sortable: false, label: '商户状态', prop: 'state', width: '',



                        // },
                        render: (h, {column, index, row}) => {
                            // console.log(row.state)
                            // this.$forceUpdate()

                            let obj = utils.getFieldDesConf('shop', 'state');
                            // console.log(obj)
                            // return obj[row.operate_state + ''];
                            return h('div', obj[row.state + ''].name)
                    /*        return h(
                                // {String | Object | Function}
                                // 一个 HTML 标签名、组件选项对象，或者
                                // resolve 了上述任何一种的一个 async 函数。必填项。
                                'el-switch',

                                // {Object}
                                // 一个与模板中 attribute 对应的数据对象。可选。
                                {
                                    style: {},
                                    attrs: {
                                        // 'name':false,
                                        // 'nameA':row.state,
                                        // 'nameb':row.state === 1,
                                        'active-value': '1',
                                        'inactive-value': '-1',
                                        'active-text': 'ON',
                                        'inactive-text': 'OFF',
                                        'value': row.state + ''
                                    },
                                    on: {
                                        change(val) {
                                            self.switchChange(val, row)
                                        }
                                    },
                                },

                                // {String | Array}
                                // 子级虚拟节点 (VNodes)，由 `createElement()` 构建而成，
                                // 也可以使用字符串来生成“文本虚拟节点”。可选。
                                // [
                                //     '先写一些文字',
                                //     h('h1',{
                                //         style: {
                                //             color: 'blue',
                                //
                                //         },
                                //     }, '一则头条'),
                                //
                                // ],

                            )*/
                        }
                    },
                    {
                        sortable: false, label: '运营状态', prop: 'operate_state', width: '',
                        render(h, {column, index, row}) {

                            let obj = utils.getFieldDesConf('shop', 'operate_state');
                            // console.log(obj)
                            // return obj[row.operate_state + ''];
                            return h('div', obj[row.operate_state + ''].name)

                        }

                    }
                    // , {sortable: 'custom', label: '商户名称', prop: 'name', width: ''}
                    , {sortable: 'custom', label: '添加时间', prop: 'add_time', width: 'auto'}

                ],

                /*搜索字段*/
                form_des: [
                    {
                        field_type: 'composite',
                        type: 'address',
                        label: '省市区',
                        field: 'lng',
                        field_arr: [
                            {field: 'prov_uuid', value: ''},
                            {field: 'city_uuid', value: ''},
                            {field: 'area_uuid', value: ''},
                        ]
                    },
                    {field: 'addr', type: 'text', label: '地址信息', value: ''},
                    {field: 'name', type: 'text', label: '商户名称', value: ''},
                    {
                        field: 'state', type: 'radio', label: '商户状态', value: '', options:
                            [
                                {name: '全部', value: ''},
                                {name: '待审', value: '0'},
                                {name: '审核通过', value: '1'},
                                {name: '作废', value: '-1'}
                            ]
                    },
                    /*{field: 'range', type: 'number', label: '距离范围', value: ''},
                    {
                        field: 'is_pause', type: 'radio', label: '是否暂停营业', value: '', options:
                            [{name: '全部', value: ''}, {name: '营业', value: '0'}, {name: '暂停营业', value: '1'}]
                    },
                    {
                        field: 'is_work_day', type: 'radio', label: '当天开业的商铺 ', value: '', options:
                            [
                                {name: '全部', value: ''},
                                {name: '当天不营业', value: '0'},
                                {name: '当天营业', value: '1'},
                            ]
                    },
                    {
                        field: 'is_now_work', type: 'radio', label: '当前营业的商铺', value: '', options:
                            [
                                {name: '全部', value: ''},
                                {name: '非营业范围', value: '0'},
                                {name: '营业范围', value: '1'},
                            ]
                    },*/
                ],

                /*添加超管字段*/
                super_admin_from_des: [
                    // {field: 'shop_uuid', type: 'hidden', label: '商户唯一值',
                    //     value:  this,
                    //     value1:  this.super_admin_from_des,
                    // },
                    {
                        field: 'name', type: 'text', label: '超管名称', value: '',
                        rules: [{required: true, message: '超管名称不可为空', trigger: 'blur'},]
                    },
                    {
                        field: 'mobile', type: 'tel', label: '超管手机号', value: '',
                        rules: [
                            {trigger: 'blur', required: true, validator: utils.telCheck}
                        ]
                    },
                    {
                        field: 'pwd', type: 'password', label: '超管密码', value: '',
                        rules: [{
                            required: true, trigger: 'blur', validator: (rule, value, callback) => {

                                if (!value) {
                                    callback(new Error('密码不能为空'));
                                } else {
                                    let re = utils.checkParams(value, 'pwd')
                                    if (!re.is_check) {
                                        callback(new Error(re.msg));
                                    } else {
                                        callback();
                                    }
                                }
                            }

                        }
                        ]
                    }
                ],
                /*重置超管字段*/
                super_admin_reset_from_des: [


                    {
                        field: 'pwd', type: 'password', label: '超管密码', value: '',
                        rules: [{
                            required: true, trigger: 'blur', validator: (rule, value, callback) => {

                                if (!value) {
                                    callback(new Error('密码不能为空'));
                                } else {
                                    let re = utils.checkParams(value, 'pwd')
                                    if (!re.is_check) {
                                        callback(new Error(re.msg));
                                    } else {
                                        callback();
                                    }
                                }
                            }

                        }
                        ]
                    }
                ],
                /*      /!*添加商户字段*!/
                      add_shop_params: {//添加商户参数
                          name: '',
                          face: '',
                          remark: '',
                          addr: '',
                          prov_uuid: '',
                          city_uuid: '',
                          area_uuid: '',
                          lng: '',
                          lat: '',
                          parent_uuid: '',//required
                          contact_name: '',//required
                          contact_tel: '',//required

                      },*/
                /*添加商户字段配置*/
                shop_from_des: [
                    {
                        field: 'name', type: 'text', label: '商户名称', value: '',
                        rules: [{required: true, message: '商户名称不可为空', trigger: 'blur'},]
                    },
                    {
                        field: 'contact_name', type: 'text', label: '商户联系人', value: '',
                        rules: [{required: true, message: '商户联系人不可为空', trigger: 'blur'},]
                    },
                    {
                        field: 'contact_tel', type: 'tel', label: '商户联系手机', value: '',
                        rules: [
                            {trigger: 'blur', required: true, validator: utils.telCheck}
                        ]
                    },
                    {field: 'parent_uuid', type: 'searchSelect', label: '商户所属父商户', value: '', options: []},
                    {field: 'face', type: 'upload', label: '商户封面', value: ''},
                    {
                        field_type: 'composite',
                        type: 'address',
                        label: '省市区',
                        field: 'lng',
                        field_arr: [
                            {field: 'prov_uuid', value: ''},
                            {field: 'city_uuid', value: ''},
                            {field: 'area_uuid', value: ''},
                        ]
                    },
                    {field: 'addr', type: 'textarea', label: '详细地址', value: ''},
                    {field: 'remark', type: 'textarea', label: '备注', value: ''},

                    // {field: 'prov_uuid', type: 'text', label: '省', value: ''},
                    // {field: 'city_uuid', type: 'text', label: '市', value: ''},
                    // {field: 'area_uuid', type: 'text', label: '区', value: ''},

                    // {
                    //     field_type: 'composite',
                    //     type: 'map',
                    //     label: '商户所在位置',
                    //     field: 'lng',
                    //     field_arr: [
                    //         {field: 'lng', value: '',},
                    //         {field: 'lat', value: '',}
                    //     ]
                    // }
                ],
                // 编辑商户字段
                /*   shop_edit_params: {
                       uuid: '',
                       name: '',
                       contact_name: '',
                       contact_tel: '',

                       remark: '',
                       addr: '',
                       prov_uuid: '',
                       city_uuid: '',
                       area_uuid: '',
                       'lng': '',
                       'lat': '',
                   },*/
                /*编辑商户字段表单描述*/
                shop_edit_from_des: [
                    // {field: 'uuid', type: 'text', label: 'uuid', value: ''},
                    // {field: 'uuid', type: 'searchSelect', label: '商户所属父商户', value: '',options:[]},
                    {
                        field: 'name', type: 'text', label: '商户名称', value: '',
                        rules: [{required: true, message: '商户名称不可为空', trigger: 'blur'},]
                    },
                    {
                        field: 'contact_name', type: 'text', label: '商户联系人', value: '',
                        rules: [{required: true, message: '商户联系人不可为空', trigger: 'blur'},]
                    },
                    {
                        field: 'contact_tel', type: 'tel', label: '商户联系手机', value: '',
                        rules: [
                            {trigger: 'blur', required: true, validator: utils.telCheck}
                        ]
                    },
                    {field: 'face', type: 'upload', label: '商户封面', value: ''},
                    {
                        field_type: 'composite',
                        type: 'address',
                        label: '省市区',
                        field: 'lng',
                        field_arr: [
                            {field: 'prov_uuid', value: ''},
                            {field: 'city_uuid', value: ''},
                            {field: 'area_uuid', value: ''},
                        ]
                    },


                    {field: 'addr', type: 'text', label: '详细地址', value: ''},
                    {field: 'remark', type: 'textarea', label: '备注', value: ''},
                    // {field: 'prov_uuid', type: 'text', label: '省', value: ''},
                    // {field: 'city_uuid', type: 'text', label: '市', value: ''},
                    // {field: 'area_uuid', type: 'text', label: '区', value: ''},
                    // {
                    //     field_type: 'composite',
                    //     type: 'map',
                    //     label: '商户所在位置',
                    //     field_arr: [
                    //         {field: 'lng', value: '',},
                    //         {field: 'lat', value: '',}
                    //     ]
                    // }


                ],
            }

        },
        methods: {
            /*点击菜单添加*/
            handleAdd() {
                this.$refs.addShop.show()

            },
            /*点击表单确认添加*/
            sureAddShop(val) {

                coneHttpShop.ManageAddShop(val.form_data)
                    .then(res => {
                        this.$message({
                            message: '添加成功',
                            type: 'success'
                        });
                        this.getList().then(list=>{
                            this.list=list;
                        })
                    })
                    .catch(err => {
                        // this.$message.error(err.msg);
                    })
            },
            /*点击编辑菜单*/
            handleEdit() {
                //删除数据之前要选择数据
                let curr_list_one = this.curr_list_one || {};//选中的数据
                let index = curr_list_one._index;
                let re = this.checkChooseListOne();
                if (!re) {
                    return false;
                }
                this.addDataFrom( //初始化表单描叙默认值
                    this.shop_edit_from_des,
                    this.curr_list_one
                );


                console.log('this.shop_edit_from_des', this.shop_edit_from_des);

                this.$refs.editShop.show();


            },
            /*编辑弹出层确认*/
            sureEditShop(val) {
                console.log(val)
                // return false;
                let curr_list_one = this.curr_list_one;
                let uuid = curr_list_one.uuid;

                coneHttpShop.ManageShopEdit({...val.form_data, uuid})
                    .then(res => {
                        this.$message({
                            message: '修改成功',
                            type: 'success'
                        });
                        this.onRefreshIndex(curr_list_one._index-1)

                    })
                    .catch(err => {
                        // this.$message.error(err.msg);
                    })
            },

            /*删除*/
            handleDel() {
                //删除数据之前要选择数据
                let curr_list_one = this.curr_list_one || {};//选中的数据
                let re = this.checkChooseListOne();
                if (!re) {
                    return false;
                }

                this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    coneHttpShop.ManageShopDel({uuid: curr_list_one.uuid})
                        .then(res => {
                            this.list.splice(curr_list_one._index - 1, 1)
                            this.$message({
                                type: 'success',
                                message: '删除成功!'
                            });
                        })

                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });

            }
            ,
            handleDetail() {
                let curr_list_one = this.curr_list_one || {};//选中的数据
                let index = curr_list_one._index;
                let re = this.checkChooseListOne();
                if (!re) {
                    return false;
                }
                coneHttpShop.ManageShopDetail({uuid: this.curr_list_one.uuid})
                    .then(res => {
                        let data = res.data;
                        this.$refs.showDetailModal.show([
                            {name: '商户名称', value: data.name},
                            {name: '封面', value: data.face, type: 'image'},
                            {name: '商户简介备注', value: data.remark},
                            {name: '商户详细', value: data.detail},
                            {name: '商户地址', value: data.prov_name + data.city_name + data.area_name + data.addr},
                            {name: '联系人名称', value: data.contact_name},
                            {name: '联系人名称', value: data.contact_name},
                            {name: '联系电话', value: data.contact_tel},
                            {name: '商户状态', value: utils.getFieldDesConf('shop', 'state')[data.state].name},
                            {
                                name: '运营状态',
                                value: utils.getFieldDesConf('shop', 'operate_state')[data.operate_state].name
                            },
                            {name: '每日运营开始时间', value: data.day_start_time},
                            {name: '每日运营结束时间', value: data.day_end_time},
                            {name: '每周工作天数', value: data.week_work_day},
                            {name: '是否暂停服务', value: utils.getFieldDesConf('shop', 'is_pause')[data.is_pause].name},
                            {name: '暂停开始日期', value: data.pause_start_date},
                            {name: '暂停结束日期', value: data.pause_end_date},
                            {name: '商户添加时间', value: data.add_time},
                            {name: '添加ip', value: data.add_ip},
                            {name: '最近一次修改时间', value: data.last_time},
                            {name: '结算手机号码', value: data.acc_mobile},

                        ],'drawer')

                    })


            },
            /*获取数据列表*/
            getList(params1) {
                this.loading = true;

                let params =params1|| this.params;
                return new Promise((resolve, reject) => {
                    coneHttpShop.getMemberShopList(params)
                        .then(res => {
                            let data = res.data;


                            this.total = data.total / 1;
                            let rows = data.rows;

                            let page_len = params.page_len,
                                page_num = params.page_num;
                            let list = this._disposeList(rows, page_num, page_len);
                            this.loading = false;


                            // console.log('列表数据', this.list)
                            resolve(list)
                        })

                })


            },


            /*点击添加超级管理员 给商户添加*/
            handleAddSup() {

                let curr_list_one = this.curr_list_one || {};//选中的数据

                let re = this.checkChooseListOne();
                if (!re) {
                    return false;
                }
                this.$refs.addSuperAdmin.show()

            },
            /*确认添加超级管理员*/
            sureAddSuperAdmin(val) {

                let params = val.form_data;

                //删除数据之前要选择数据
                let curr_list_one = this.curr_list_one;//选中的数据


                coneHttpShop.ManageCreateShopAdmin({...params, shop_uuid: curr_list_one.uuid})
                    .then(res => {

                        this.$message({
                            type: 'success',
                            message: '添加!'
                        });
                    })
                    .catch(err => {
                        // this.$message.error(err.msg);
                    })
            }
            /*重置*/,
            handleReset() {


                let re = this.checkChooseListOne();
                if (!re) {
                    return false;
                }
                this.$refs.resetSuperAdmin.show()
            }
            // 确认重置
            , sureResetSuperAdmin(val) {
                console.log(val)
                let data = val.form_data;
                let curr_list_one = this.curr_list_one;//选中的数据

                coneHttpShop.ManageShopAdminResetPwd({new_pwd: data.pwd, uuid: curr_list_one.uuid})
                    .then(res => {
                        this.$message({
                            type: 'success',
                            message: '重置成功'
                        });
                    })
            }
            /*审核菜单*/
            , handleExamine(row) {

                let curr_list_one = this.curr_list_one;
                let curr_one ={ };

                if(Object.keys(row).length){
                    curr_one= row
                }else {
                    if(this.checkChooseListOne()){
                        curr_one = curr_list_one ;
                    }else {
                        return false;
                    }
                }


                let _index = curr_one._index;
                let uuid = curr_one.uuid;


                this.$confirm('确认修改该商户状态么, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    coneHttpShop.ManageShopCheck({uuid: uuid})
                        .then(res => {

                            this.onRefreshIndex( _index -1)


                        })
                })
                .catch(()=>{

                })

            }

            /*切换商户状态*/
            , switchChange(val, row) {

                this.handleExamine(row)


            }
            /*获取商户列表*/
            , getShopList(val) {
                console.log('获取商户列表', val)
                coneHttpShop.getMemberShopList({
                    name: val.value,
                    page_num: 1,
                    page_len: utils.getConfig('select_page_len')
                })
                    .then(res => {

                        let list = res.data.rows;
                        let options = list.map(v => {
                            return {name: v.name, value: v.uuid}
                        })
                        utils.editObjField(val.from_des, {field: 'parent_uuid'}, {options});
                    });


            }
            ,
            /*文件上传成功*/
            uploadFaceSuccess(val) {
                console.log(val)
                let value = val.value;
                let from_des = val.from_des;
                utils.editObjField(from_des, {field: 'face'}, {value});

            }
        },
        mounted() {
            this.getList()
                .then(list => {
                    this.list = list
                })
            // this.$refs.formEditModal.show()
            // console.log(utils.checkParams('17333043302','tel'))
            pageEvent.$off('getShopList', null);
            pageEvent.$on('getShopList', this.getShopList);
            pageEvent.$off('uploadFaceSuccess', null);
            pageEvent.$on('uploadFaceSuccess', this.uploadFaceSuccess);
        }
    }
</script>

