
/**
 *
 *@description 全局事件分发 使用方法和 uni-app 的全局通信 同
 * @author hxq
 * @date 2020.9.21
 *  */
export default {

    //事件中心
    store:{
        name:'store',
    },

    $emit(event, args) {
        let store = this.store;
        let event_key  = store[event];
        if(this.store[event]){ //函数组成的数组


            this.store[event].forEach((v,k)=>{
                v(args);
                if(v._once){
                    this.store[event][k]=null;
                }
                // console.log('this.store[event]',this.store[event])
            })


        }else {
            console.error('执行事件之前需要绑定')
        }

    },
    /*绑定事件*/
    $on(event, callback) {
        let store = this.store;
        let event_key  = store[event];

        if(event_key){
            event_key.push(callback)
        }else {
            this.store[event]=[];

            console.log(this.store)
            this.store[event].push(callback)
        }



    },
    $off(event, callback) {
        this.store[event] = []
        typeof callback ==='function' ? callback() : null ;

    },
    $once(event, callback) {
        callback._once=true;
        this.$on(event, callback)

    }
}