<template>

    <div>
        <template v-if="type==='tel'">
            <el-input v-model="value1"  @input="telInputChange" :placeholder="placeholder"></el-input>
        </template>
        <template v-if="type==='number'">
            <el-input v-model="value1"    @input="numberInputChange" :placeholder="placeholder"></el-input>
        </template>
        <template v-if="type==='number_positive'">
            <el-input v-model="value1"    @input="numberPositiveInputChange" :placeholder="placeholder"></el-input>
        </template>
    </div>



</template>

<script>
    import utils from "../../_commom/lib/utils";

    export default {
        name: "hElInput",
        data() {
            return {
                value1:''
            }

        },
        props: {
            value:{},
            type: {
                default: 'text',
                type: String,

            },
            placeholder:{
                default: '',
                type: String,
            }

        },
        methods: {


            /*电话输入值改变*/
            telInputChange(val){

                let value1 = utils.val2Number(val)+'';

                if(value1>11 ){
                    value1 = value1.slice(0,11)
                }
                this.value1 = value1;
                this.$emit('inputChange',value1)
            },
            /*数字输入*/
            numberPositiveInputChange(val){
                let value1 = utils.val2Number(val) ;

                if(value1<0){
                    value1 = 0;
                }
                if (value1>100000000000000){
                    value1 = 100000000000000;
                }
                if (val / 1 === 0) {


                    value1 = ''

                }

                this.value1 = value1;
                this.$emit('inputChange',value1)

            },
            /*输入事件*/
            inputChange(val){
                /*根据不同类型做输入限制*/



                let type = this.type;
                console.log(val)
                console.log(type)
                if(type === 'tel' || type==='number_positive' ||type==='number'){
                    let value1= parseInt(val)
                    if (isNaN(value1)){
                        this.value1=''
                    }else {
                        this.value1=value1;
                    }


                    if(type === 'tel' && val.length>11 ){
                        this.value1=val.slice(0,11)
                    }
                    if(type === 'number_positive' && val<0 ){
                        this.value1=0
                    }
                    if(type ==='number_positive' ||  type ==='number'){
                        if((Math.abs(val)>100000000000000)){
                            this.value1=  100000000000000
                        }
                    }

                    this.$emit('inputChange',this.value)
                }

            }

        },
        mounted() {
          this.value1 = this.value;
        },
        watch:{
            value(newVla,oldVla){
                if(newVla){
                  this.value1 = this.value;
                }

            },
            value1(newVla,oldVla){
                // this.$emit('inputChange',newVla)

            }
        }

    }
</script>

