<template>
    <div class="hUpload">


        <label for="upload">

<!--            {{value}}-->
            <template v-if="value || img_base64">
                <el-image
                        style="width: 100px; height: 100px"
                        :src="value || img_base64"
                        fit="cover"></el-image>
            </template>

            <template v-else>
                    <span class="wrap posi-relative">
                <el-button type="primary ">文件上传</el-button>
                <span class="innner posi-absolute"></span>

            </span>
            </template>


            <input accept="image/*" id="upload" type="file" name="file" class="input" @change="change">
        </label>
        <!--        <label for="upload">  z</label>-->


    </div>


</template>

<script>

    import lime from "@/lime";

    import imgUtils from "../_commom/lib/imgUtils";

    import FileHttp from '../_commom/http/FileHttp'
    import pageEvent from "../_commom/lib/pageEvent";


    export default {

        name: "hUpload",
        data() {
            return {


                img_base64: '',
                file:null
            }

        },
        props:['value'],
        methods: {

            /*上传文件*/
            upload(_module='OssManageUpload'){
                let file = this.file;
                let formData = new FormData(); //创建form对象
                let timestamp = lime.get_timestamp();
                let up_type = 'file';
                let login_token = lime.cookie_get('login_token')

                formData.append('file', file);//通过append向form对象添加数据
                formData.append('up_type', 'file');//通过append向form对象添加数据
                formData.append('timestamp', timestamp);//通过append向form对象添加数据
                formData.append('login_token', login_token);//通过append向form对象添加数据
                formData.append('sign', lime.md5_str(lime.set_sign({timestamp, up_type, login_token})));//通过append向form对象添加数据

                console.log(formData)
                FileHttp.uploadFile(_module, formData)
                    .then(res => {

                        let url = res.data.url;

                        this.$emit('uploadSuccess',url)
                    })

            },
            /*选取文件*/
            change(e) {

                let file = e.target.files[0];
                console.log(e)
                this.file = file;
                imgUtils.img2base64(file)
                    .then(src => {
                        this.img_base64 = src;
                        this.upload()
                        })





            }
        },
        mounted() {

        },
        updated() {
           this.img_base64=''
        }
    }
</script>

<style scoped lang="less">
    @import "../css/common";

    .input {
        display: none;
    }
</style>