<template>
    <div class="shopAccount">
        <headMenus
                @onSearch="onSearch"
                @onRefresh="onRefresh"
                @handleDetail="handleDetail"
        ></headMenus>
        <showDetailModal title="详情" ref="showDetailModal"></showDetailModal>
        <hTable
                :list="list"
                :total="total"
                :table_des="table_des"
                :page_len="params.page_len"
                :currentPage="params.page_num"
                @currentChange="currentChange"
                @tableChoose="tableChoose"
                @sortChange="sortChange"
                :loading="loading"
        ></hTable>
        <formEditModal
                ref="formEditModal"
                title="搜索"
                :form_des="form_des"
                @sure="sureSearch"
        ></formEditModal>
    </div>


</template>

<script>
    import headMenus from '../../_components/headMenus'
    import hTable from '../../_components/hTable'
    import formEditModal from '../../_components/formEditModal'
    import showDetailModal from '../../_components/showDetailModal'

    import tableFromMix from '../../_commom/mixins/tableFromMix'


    import coneHttpShop from '../../_commom/connectorHttp/coneHttpShop'
    import utils from "../../_commom/lib/utils";

    export default {
        name: "shopAccount",
        mixins: [tableFromMix],

        components: {headMenus, hTable, formEditModal, showDetailModal},
        data() {
            return {
                params: {

                    member_name: '',
                    max_enable_money: '',//最大可用金额
                    min_enable_money: '',//最小可用金额
                    max_freeze_money: '',//最大冻结金额
                    min_freeze_money: '',//最小冻结金额
                    order_field: null,
                    order_sort: '',
                    page_num: 1,
                    page_len: 20,

                },
                table_des: [
                    {sortable: false, label: '#', prop: '_list_index', width: '50'},
                    {sortable: false, label: '公司名称', prop: 'shop_name', width: ''}
                    , {sortable: 'custom', label: '可用数', prop: 'enable_coins', width: ''}
                    , {sortable: 'custom', label: '冻结数', prop: 'freeze_coins', width: ''}

                    , {sortable: 'custom', label: '添加时间', prop: 'add_time', width: 'auto'}
                    , {sortable: 'custom', label: '更新时间', prop: 'last_time', width: 'auto'}

                ],
                /*搜索字段*/
                form_des: [
                    {field: 'max_enable_money', type: 'number_positive', label: '最大可用金额', value: ''},
                    {field: 'min_enable_money', type: 'number_positive', label: '最小可用金额', value: ''},
                    {field: 'max_freeze_money', type: 'number_positive', label: '最大冻结金额', value: ''},
                    {field: 'min_freeze_money', type: 'number_positive', label: '最小冻结金额', value: ''},

                ],
                list: [],
                total: 0,
                loading: true
            }

        },
        methods: {
            /*获取数据列表*/
            getList(params1) {


                this.loading = true;

                let params =params1|| this.params;
                return new Promise((resolve, reject) => {
                    coneHttpShop.getShopAccountList(params)
                        .then(res => {
                            let data = res.data;


                            this.total = data.total / 1;
                            let rows = data.rows;

                            let page_len = params.page_len,
                                page_num = params.page_num;
                            let list = this._disposeList(rows, page_num, page_len);
                            this.loading = false;


                            // console.log('列表数据', this.list)
                            resolve(list)
                        })

                })
            },

            /*获取详情*/
            handleDetail() {
                // let re = this.checkChooseListOne();
                // if (!re) {
                //     return false;
                // }
                // let item = this.curr_list_one;
                //
                // coneHttpShop.ShopAccountDetails()
                //     .then(res => {
                //         console.log(res)
                //     })
                // this.$refs.showDetailModal.show([
                //     {name: '商户名称', value: item.shop_name},
                //     {name: '可用金额', value: item.enable_money},
                //     {name: '冻结金额', value: item.freeze_money},
                // ])
            }


        },
        mounted() {
            this.getList()
                .then(list => {
                    this.list = list
                })
            // this.$refs.formEditModal.show()
        }
    }
</script>

<style lang="less" scoped>
    @import "../../css/common";
</style>