/**
 * @description 所有交易相关的接口
 * @author hxq
 * */
import Http from '../http/Http'

export default {
    /**
     * @description 运营查询会员与平台交易记录
     * @return Promise
     * */
    ManageMemberTradeList(params) {
        return new Promise((resolve, reject) => {
            Http.httpPost('ManageMemberTradeList', params)
                .then(res => {
                    resolve(res)

                })
                .catch(err => {
                    reject(err)
                })
        })

    },
    /**
     * @description 运营与商户交易列表
     * @return Promise
     * */
    ManageShopTradeList(params) {
        return new Promise((resolve, reject) => {
            Http.httpPost('ManageShopTradeList', params)
                .then(res => {
                    resolve(res)

                })
                .catch(err => {
                    reject(err)
                })
        })

    },

}