<template>
    <!--标签分组-->
    <div>
        <headMenus
                @onSearch="onSearch"
                @onRefresh="onRefresh"
                @handleAdd="handleAdd"
                @handleEdit="handleEdit"
                @handleDel="handleDel"
        ></headMenus>
        <hTable
                :list="list"
                :total="total"
                :table_des="table_des"

                :page_len="params.page_len"
                :currentPage="params.page_num"
                @currentChange="currentChange"
                @tableChoose="tableChoose"
                @sortChange="sortChange"
                :loading="loading"
        ></hTable>
        <formEditModal
                ref="formEditModal"
                title="搜索"
                :form_des="form_des"
                @sure="sureSearch"
        ></formEditModal>
        <formEditModal
                ref="addTags"
                title="添加"
                :form_des="add_tags_from_des"
                @sure="sureAddTags"
        ></formEditModal>
        <formEditModal
                ref="editTags"
                title="编辑"
                :form_des="edit_tags_from_des"
                @sure="sureEditTags"
        ></formEditModal>
    </div>


</template>

<script>
    import coneHttpTags from '../../_commom/connectorHttp/coneHttpTags'
    import headMenus from '../../_components/headMenus'
    import hTable from '../../_components/hTable'
    import formEditModal from '../../_components/formEditModal'
    import tableFromMix from '../../_commom/mixins/tableFromMix'

    export default {
        name: "tagGroup",
        mixins: [tableFromMix],
        components: {headMenus, hTable, formEditModal},
        data() {
            return {
                params: {


                    title: '',
                    order_field: null,
                    order_sort: '',
                    page_num: 1,
                    page_len: 20,

                },
                table_des: [
                    {sortable: false, label: '#', prop: '_list_index', width: '50'}
                    // {sortable:false,  label:'用户id'  ,   prop:'uuid'      ,     width:''}
                    , {sortable: false, label: '标签名', prop: 'title', width: ''}
                    , {sortable: false, label: '备注', prop: 'remark', width: ''}


                ],
                form_des: [
                    {field: 'title', type: 'text', label: '标签名', value: ''},


                ],
                /*添加表单描叙*/
                add_tags_from_des: [
                    {
                        field: 'title', type: 'text', label: '标签名', value: '',
                        rules: [
                            {trigger: 'blur', required: true, message: '标签名不可为空'}
                        ]
                    },
                    {field: 'remark', type: 'textarea', label: '备注', value: '',}
                ],
                edit_tags_from_des: [
                    {
                        field: 'title', type: 'text', label: '标签名', value: '',
                        rules: [
                            {trigger: 'blur', required: true, message: '标签名不可为空'}
                        ]
                    },
                    {field: 'remark', type: 'textarea', label: '备注', value: '',}
                ]
            }

        },
        methods: {
            /*获取数据列表*/
            getList(params1) {


                this.loading = true;

                let params =params1|| this.params;
                return new Promise((resolve, reject) => {
                    coneHttpTags.ManageQuesTagList(params)
                        .then(res => {
                            let data = res.data;


                            this.total = data.total / 1;
                            let rows = data.rows;

                            let page_len = params.page_len,
                                page_num = params.page_num;
                            let list = this._disposeList(rows, page_num, page_len);
                            this.loading = false;


                            // console.log('列表数据', this.list)
                            resolve(list)
                        })

                })
            },


            handleAdd() {
                this.$refs.addTags.show()

            },
            /*确认添加*/
            sureAddTags(val) {

                let form_data = val.form_data;
                coneHttpTags.ManageQuesTagAdd(form_data)
                    .then(res => {
                        this.$message({
                            message: '添加成功',
                            type: 'success'
                        });
                        this.getList()
                            .then(list => {
                                this.list = list
                            })
                    })

            },
            handleEdit() {
                let curr_list_one = this.curr_list_one || {};//选中的数据

                let re = this.checkChooseListOne();
                if (!re) {
                    return false;
                }
                this.addDataFrom( //初始化表单描叙默认值
                    this.edit_tags_from_des,
                    this.curr_list_one
                );
                this.$refs.editTags.show()
            },
            /*编辑确认*/
            sureEditTags(val){
                let curr_list_one = this.curr_list_one;
                let  uuid = curr_list_one.uuid;
                let form_data = val.form_data;
                coneHttpTags.ManageQuesTagEdit({...form_data,uuid})
                    .then(res => {
                        this.$message({
                            message: '修改成功',
                            type: 'success'
                        });

                         this.onRefreshIndex(curr_list_one._index-1)
                    })
            },
            /*删除*/
            handleDel() {
                //删除数据之前要选择数据
                let curr_list_one = this.curr_list_one || {};//选中的数据
                let re = this.checkChooseListOne();
                if (!re) {
                    return false;
                }

                this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    coneHttpTags.ManageQuesTagDel({uuid: curr_list_one.uuid})
                        .then(res => {
                            this.list.splice(curr_list_one._index - 1, 1)
                            this.$message({
                                type: 'success',
                                message: '删除成功!'
                            });
                        })

                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });

            }


        },
        mounted() {
            this.getList()
                .then(list => {
                    this.list = list
                })
            // this.$refs.formEditModal.show()
        }
    }
</script>

<style scoped lang="less">

    @import "../../css/common";

</style>