import  Http from '../http/Http'

export default {
    /**
     * @description 获取城市信息

     * */
    getProv(params={}){
        return new Promise((resolve, reject) => {
            Http.httpPost('GetArea',params)
                .then(res => {
                    resolve(res)

                })
                .catch(err=>{
                    reject(err)
                })
        })

    }  ,
    getCity(params={}){
        return new Promise((resolve, reject) => {
            Http.httpPost('GetCityUuid',params)
                .then(res => {
                    resolve(res)

                })
                .catch(err=>{
                    reject(err)
                })
        })

    }  ,

}