/*
学习计划
*/
<template>
    <div class="staff">
        <!-- 菜单操作 -->
        <div style="height: 46px; line-height: 46px; overflow: hidden;">
            <el-row>
                <el-col :span="6">
                    <div style="padding-left:16px;">
                        <i class="el-icon-s-unfold"></i>
                        <span style="padding-left:9px;">
                            {{$store.state.AdminData.active_title}}
                        </span>
                    </div>
                </el-col>

                <el-col :span="18">
                    <div style="text-align: right; ">
                        <el-link @click="onSubMenu('onRefresh',true)" class="menu">刷新</el-link>
                        <el-link @click="onSubMenu('onSearch',true)" class="menu">搜索</el-link>
                        <el-link @click="onSubMenu('onDetail',true)" class="menu">详情</el-link>
                        <!-- <el-link
                            class="menu" 
                            @click="onSubMenu(item)"
                            v-for="(item,index) in $store.state.AdminData.right_menus" 
                            :key="index">
                            {{item.name}}
                        </el-link> -->
                    </div>
                </el-col>
            </el-row>
        </div>
        <!-- 学习计划列表 -->
        <div style="border-top: solid 1px #f2f1f4;">
            <el-table 
                :data="rows"
                :height="height - 60 - 46 - 48"
                v-loading="loading"
                element-loading-text="拼命加载中"
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                @sort-change="onSortChange"
                :highlight-current-row="true"
                @current-change="onSelectRow"
                style="width: 100%" 
                size="mini">
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="shop_name" label="公司名称" align="center"></el-table-column>
                <el-table-column prop="title" label="计划名称"></el-table-column>
                <el-table-column prop="start_time" label="开始时间" sortable width="300"></el-table-column>
                <el-table-column prop="end_time" label="结束时间" sortable width="300"></el-table-column>
            </el-table>
            
            <div class="page" :style="{width:width - 250 + 'px'}">
                <el-pagination
                    :current-page.sync="SearchFormData.page_num"
                    @current-change="onPageChange"
                    layout="prev, pager, next, jumper"
                    :total="total">
                </el-pagination>
            </div>
        </div>

        <!-- 菜单操作弹框 -->
        <!-- 搜索 -->
        <el-dialog
            title="提示"
            :visible.sync="search_show"
            width=400px>
            <el-form :model="SearchFormData" label-width="80px" size="small">
                <el-form-item label="公司名称:">
                    <el-input v-model="SearchFormData.real_name"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="search_show = false" size="small">取 消</el-button>
                <el-button type="primary" @click="onSearchSubmit" size="small">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 添加模板 -->
        <el-dialog  
            title="详情"
            :visible.sync="detail_show"
            width="750px">
            <!-- <CellGroup>
                <Cell title="计划名称" :extra="create_detail.cname"></Cell>
                <Cell title="计划开始时间" :extra="create_detail.start_time"></Cell>
                <Cell title="计划结束时间" :extra="create_detail.end_time"></Cell>
                <Cell title="实际计划开始时间"><pre slot="label">{{create_detail.real_start}}</pre></Cell>
                <Cell title="实际计划完成时间" :extra="create_detail.real_end"></Cell>
                <Cell title="资源列表" :extra="create_detail.resoure_list"></Cell>
                <Cell title="问题规则" :extra="create_detail.question_rule"></Cell>
                <Cell title="人员组成" :extra="create_detail.study_user_type"></Cell>
                <Cell title="员工范围" :extra="create_detail.staff_list"></Cell>
                <Cell title="学员范围" :extra="create_detail.member_list"></Cell>
                <Cell title="用户范围" :extra="create_detail.user_list"></Cell>
                <Cell title="描述" :extra="create_detail.remark"></Cell>
                <Cell title="考试总分" :extra="create_detail.total_score"></Cell>
                <Cell title="考试合格分数" :extra="create_detail.pass_score"></Cell>
                <Cell title="是否抽验" :extra="create_detail.rand_check_num"></Cell>
                <Cell title="状态" :extra="create_detail.state"></Cell>
                <Cell title="添加时间" :extra="create_detail.add_time"></Cell>
                <Cell title="更新时间" :extra="create_detail.last_time"></Cell>
                <Cell title="公司名称" :extra="create_detail.shop_name"></Cell>
            </CellGroup> -->
            <ul>
                <li>
                    <p><span>计划名称：</span><span>{{create_detail.name}}</span></p>
                    <p><span>计划开始时间：</span><span>{{create_detail.start_time}}</span></p>
                </li>
                <li>
                    <p><span>计划结束时间：</span><span>{{create_detail.end_time}}</span></p>
                    <p><span>实际计划开始时间：</span><span>{{create_detail.real_start}}</span></p>
                </li>
                <li>
                    <p><span>实际计划完成时间：</span><span>{{create_detail.real_end}}</span></p>
                    <p><span>资源列表：</span><span>{{create_detail.resoure_list}}</span></p>
                </li>
                <!-- <li>
                    <p>
                        <span>问题规则：</span>
                        <span>
                            <el-select v-model="questionRule" placeholder="请选择">
                                <el-option
                                v-for="item in create_detail.question_rule"
                                :key="item.num"
                                :label="item.num"
                                :value="item.num">
                                </el-option>
                            </el-select>
                        </span>
                    </p>
                    <p><span>人员组成：</span><span>{{create_detail.study_user_type}}</span></p>
                </li> -->
                <!-- <li>
                    <p><span>员工范围：</span><span>{{create_detail.staff_list}}</span></p>
                    <p><span>学员范围：</span><span>{{create_detail.member_list}}</span></p>
                </li> -->
                <li>
                    <p><span>用户范围：</span><span>{{create_detail.user_list}}</span></p>
                    <p><span>描述：</span><span>{{create_detail.remark}}</span></p>
                </li>
                <li>
                    <p><span>考试总分：</span><span>{{create_detail.total_score}}</span></p>
                    <p><span>考试合格分数：</span><span>{{create_detail.pass_score}}</span></p>
                </li>
                <li>
                    <p><span>是否抽验：</span><span>{{create_detail.rand_check_num}}</span></p>
                    <p><span>状态：</span><span>{{create_detail.state}}</span></p>
                </li>
                <li>
                    <p><span>添加时间：</span><span>{{create_detail.add_time}}</span></p>
                    <p><span>更新时间：</span><span>{{create_detail.last_time}}</span></p>
                </li>
                <li>
                    <p><span>公司名称：</span><span>{{create_detail.shop_name}}</span></p>
                </li>
            </ul>
            <span slot="footer">
                <el-button @click="detail_show = false" size="small">取消</el-button>
                <!-- <el-button @click="onAddSubmit" type="primary" size="small">确定</el-button> -->
            </span>
        </el-dialog>

        <!-- 编辑模板 -->
        <el-dialog  
            title="编辑"
            :visible.sync="edit_show"
            width="500px">
            <el-form :model="EditFormData" label-width="80px" size="small">
                <el-form-item label="员工姓名:">
                    <el-input v-model="EditFormData.real_name" />
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="edit_show = false" size="small">取消</el-button>
                <el-button @click="onEditSubmit" type="primary" size="small">确定</el-button>
            </span>
        </el-dialog>

        <!-- 设置角色 -->
         <el-dialog 
            title="设置角色"
            width="450px"
            :visible.sync="role_show">
            <el-form :model="RoleFormData" label-width="80px" size="small">
                <el-form-item label="拥有角色:">
                    <el-cascader 
                        clearable 
                        :options="role_rows"
                        :props="{checkStrictly:true,expandTrigger: 'hover',value:'uuid', label:'name',emitPath:false}"
                        placeholder="不选择,无权限"
                        v-model="RoleFormData.role_uuid">
                    </el-cascader>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="role_show = false" size="small">取消</el-button>
                <el-button type="primary" @click="onRoleSubmit" size="small">确认</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { cookie_get } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import { ManagePlanList } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import { ManagePlanDetail } from "../../_commom/connectorHttp/trainingResourcesHttp.js";

import store from "@/store";
import util from "@/util.js";
export default {
    data () {
        return {
            /*员工列表*/ 
            rows:[],
            total:0,
            loading:false,

            curr_row:null,

            SearchFormData:{
                shop_name:'',//公司名称
                title: '',//计划名称
                start_time: '',//开始时间
                end_time: '',//结束时间
                page_num: 1,//当前页码
                page_len: '10',//每页条数
                order_field:'add_time',//培训字段
                order_sort:'desc'//排序类型
            },

            /*菜单操作*/ 
            // 搜素
            search_show:false,
            // 详情
            detail_show: false,
            questionRule: '',
            questionRuleData: null,
            create_detail: {},

            // 添加
            add_show:false,
            AddFormData:{
                real_name:'',
                mobile:'',
                pwd:'',
            },

            // 编辑
            edit_show:false,
            EditFormData:{
                real_name:''
            },

            //设置角色
            role_show:false,
            role_rows:[],
            RoleFormData:{
                manage_uuid:'',
                role_uuid:'',
            },
        }
    },
    mounted () {
        this.init();
    },
    computed:{
        width:() => {
            return store.state.AppData.width;
        },
        height:() => {
            return store.state.AppData.height;
        },
        upload_url:() => {
            return store.state.AppData.upload_url;
        },
        img_host:() => {
            return store.state.AppData.img_host;
        }
    },
    methods: {
        // 按钮点击 menu:参数数据 local是否本地程序
        onSubMenu(menu, local = false) {
            util.submenu(menu,this,cookie_get (), local);
        },
        /*员工列表方法*/ 
        //员工列表初始化
        init () {
            this.loading = true;
            var dataList = {
                login_token:cookie_get(),
                shop_name:this.SearchFormData.shop_name,
                title:this.SearchFormData.title,
                start_time:this.SearchFormData.start_time,
                end_time:this.SearchFormData.end_time,
                page_num:this.SearchFormData.page_num + '',
                page_len: this.SearchFormData.page_len,
                order_field: this.SearchFormData.order_field,
                order_sort: this.SearchFormData.order_sort
            }
            ManagePlanList (dataList).then(res => {
                this.loading = false;
                this.rows = res.data.rows;
                this.total = res.data.total;
            });
            // 超时关闭遮罩层
            setTimeout(() => {
                this.loading = false;
            }, 10000);
        },
        //排序处理
        onSortChange (column, prop, order) {
            this.SearchFormData.order_field = column.prop;
            if (column.order == 'ascending') {
                this.SearchFormData.order_sort  = 'asc';
            } else {
                this.SearchFormData.order_sort  = 'desc';
            }
            this.init();
        },
        //选择表格行
        onSelectRow (row) {
            this.curr_row = row;
        },
        //分页处理
        onPageChange (page) {
            this.SearchFormData.page_num = page;
            this.init();
        },

        /*菜单操作*/
        //刷新
        onRefresh() {
            this.init();
        },
        //搜索显示弹框
        onSearch () {
            this.search_show = true;
        },
        //搜索弹框提交
        onSearchSubmit () {
            this.SearchFormData.page_num = 1;
            this.init();
            this.search_show = false;
            this.SearchFormData.real_name = '';
        },
        //详情
        onDetail () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            console.log(this.curr_row.uuid)
            var detailParams = {
                login_token:cookie_get(),
                uuid: this.curr_row.uuid
            }
            ManagePlanDetail (detailParams).then(res => {
                this.create_detail = res.data;
                this.questionRuleData = JSON.parse(res.data.question_rule);
                console.log(JSON.parse(res.data.question_rule))
                // this.rows = res.data.rows;
                // this.total = res.data.total;
            });
            this.detail_show = true;
        },






        //添加显示
        handleAdd() {
            this.add_show = true;
        },
        //添加模板提交
        onAddSubmit () {
            this.AddFormData.login_token = cookie_get();
            ManageAdd (this.AddFormData).then(res => {
                this.SearchFormData.page_num = 1;
                this.init();
                this.add_show = false;
                this.AddFormData.real_name = '';
            }).catch(err => {
                this.$message.error(err.msg);
            })
        },
        //编辑显示
        handleEdit () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            this.EditFormData.real_name = this.curr_row.real_name;
            this.edit_show = true;
        },
        //编辑模板提交
        onEditSubmit () {
            this.EditFormData.login_token = cookie_get();
            this.EditFormData.uuid = this.curr_row.uuid;
            ManageEdit (this.EditFormData).then(res => {
                this.init();
                this.edit_show = false;
                this.EditFormData.real_name = '';
            }).catch(err => {
                this.$message.error(err.msg);
            });
        },
        //设置角色
        handleSetRole () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            
            // 获取当前登录账号拥有的角色
            ManageSetRole ({
                module:'ManageSetRole',
                ver:'1.0.0',
                relation_module:'ManageRoleList',
                relation_ver:'1.0.0',
                login_token:cookie_get(),
            }).then(res => {
                this.role_rows = util.toTree(res.data);
                this.role_show = true;
            })
        },
        //角色模板提交
        onRoleSubmit () {
            ManageSetRole ({
                login_token:cookie_get(),
                manage_uuid:this.curr_row.uuid,
                role_uuid:this.RoleFormData.role_uuid
            }).then(res =>{
                // this.init,
                this.role_show=false
            }).catch(err => {
                this.$message.error(err.msg)
            })
        },
        //删除提交
        handleDel(menu) {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }

            this.$confirm('确认删除?', '提示').then(() => {
                ManageDel({
                    login_token:cookie_get(),
                    uuid:this.curr_row.uuid
                }).then(res => {
                    this.init();
                    this.$message.success('操作成功');
                }).catch(err => {
                    this.$message.error(err.msg);
                })
            })
        },
    }
}
</script>

<style lang="less" scoped>
    .staff{
        .menu{
            display: inline-block;
            padding:0 16px;
            text-align: center;
        }
        .page {
            height: 40px; 
            line-height: 40px; 
            text-align: right;
            position: fixed;
            bottom: 0;
            right:0;
            overflow: hidden;
        }
        ul{
            list-style: none;
            li{
                line-height: 40px;
                overflow: hidden;
                p:nth-of-type(1){
                    float: left;
                    margin-left: 0px;
                }
                p:nth-of-type(2){
                    float: right;
                    margin-right: 0px;
                }
                p{
                    span:nth-of-type(1){
                        width: 140px;
                        text-align: right;
                        display: inline-block;
                        float: left;
                    }
                    span:nth-of-type(2){
                        width: 210px;
                        display: inline-block;
                        float: left;
                    }
                }
            }
        }
    }
</style>