/*
学习记录
*/
<template>
    <div class="resource">
        <!-- 菜单操作 -->
        <div style="height: 46px; line-height: 46px; overflow: hidden;">
            <el-row>
                <el-col :span="6">
                    <div style="padding-left:16px;">
                        <i class="el-icon-s-unfold"></i>
                        <span style="padding-left:9px;">
                            {{$store.state.AdminData.active_title}}
                        </span>
                    </div>
                </el-col>

                <el-col :span="18">
                    <div style="text-align: right; ">
                        <el-link @click="onSubMenu('onRefresh',true)" class="menu">刷新</el-link>
                        <el-link @click="onSubMenu('onSearch',true)" class="menu">搜索</el-link>

                        <el-link
                            class="menu" 
                            @click="onSubMenu(item)"
                            v-for="(item,index) in $store.state.AdminData.right_menus" 
                            :key="index">
                            {{item.name}}
                        </el-link>
                    </div>
                </el-col>
            </el-row>
        </div>
        <!-- 学习记录列表 -->
        <div style="border-top: solid 1px #f2f1f4;">
            <el-table 
                :data="rows"
                :height="height - 60 - 46 - 48"
                v-loading="loading"
                element-loading-text="拼命加载中"
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                @sort-change="onSortChange"
                :highlight-current-row="true"
                @current-change="onSelectRow"
                style="width: 100%" 
                size="mini">
                
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="state" label="状态"></el-table-column>
                <el-table-column prop="certificate_img" label="证书图片"></el-table-column>
                <el-table-column prop="is_online" label="类型"></el-table-column>
                <el-table-column prop="in_face" label="第一次拍照"></el-table-column>
                <el-table-column prop="out_face" label="最后一次拍照"></el-table-column>
                <el-table-column prop="add_time" label="添加时间" :formatter="common.formatDate" sortable></el-table-column>
                <el-table-column prop="last_time" label="结束时间" :formatter="common.formatDate" sortable></el-table-column>
            </el-table>
            
            <div class="page" :style="{width:width - 250 + 'px'}">
                <el-pagination
                    :current-page.sync="SearchFormData.page_num"
                    @current-change="onPageChange"
                    layout="prev, pager, next, jumper"
                    :total="total">
                </el-pagination>
            </div>
        </div>
        <!-- 菜单操作弹框 -->
        <!-- 搜索弹框 -->
        <el-dialog
            title="搜索"
            :visible.sync="search_show"
            width=400px>
            <el-form :model="SearchFormData" label-width="80px" size="small">
                <el-form-item label="状态">
                    <el-select v-model="SearchFormData.state" style="width: 280px;">
                        <el-option
                            v-for="item in options_state"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="类型">
                    <el-select v-model="SearchFormData.is_online" style="width: 280px;">
                        <el-option
                            v-for="item in options_isOnline"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="search_show = false" size="small">取 消</el-button>
                <el-button type="primary" @click="onSearchSubmit" size="small">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 编辑弹框 -->
        <el-dialog
            title="编辑"
            :visible.sync="edit_show"
            width=400px>
            <el-form :model="EditFormData" label-width="80px" size="small">
                <el-form-item label="标签名称" prop="tag_name">
                    <el-input v-model="EditFormData.tag_name"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="edit_show = false" size="small">取 消</el-button>
                <el-button type="primary" @click="onEditSubmit" size="small">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 添加弹框 -->
        <el-dialog
            title="添加"
            :visible.sync="add_show"
            width=400px>
            <el-form :model="AddFormData" :rules="AddFormRule" label-width="80px" size="small">
                <el-form-item label="标签名称" prop="tag_name">
                    <el-input v-model="AddFormData.tag_name"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="add_show = false" size="small">取 消</el-button>
                <el-button type="primary" @click="onAddSubmit" size="small">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 详情弹框 -->
        <el-dialog
            title="详情"
            :visible.sync="detail_show"
            width=550px>
            <ul>
                <li>
                    <div>状态：</div>{{DetailFormData.state}}
                </li>
                <li>
                    <div>证书图片：</div>{{DetailFormData.certificate_img}}
                </li>
                <li>
                    <div>类型：</div>{{DetailFormData.is_online}}
                </li>
                <li>
                    <div>第一次拍照：</div>{{DetailFormData.in_face}}
                </li>
                <li>
                    <div>最后一次拍照：</div>{{DetailFormData.out_face}}
                </li>
                <li>
                    <div>添加时间:</div>{{DetailFormData.add_time}}
                </li>
                <li>
                    <div>结束时间:</div>{{DetailFormData.last_time}}
                </li>
            </ul>

            <span slot="footer">
                <!-- <el-button @click="DetailFormData = false" size="small">取 消</el-button> -->
                <el-button type="primary" @click="detail_show = false" size="small">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { cookie_get } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import { ManageStudyRecodeList } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import { ManageStudyRecodeDetail } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import store from "@/store";
import util from "@/util.js";
export default {
    data () {
        return {
            // 学习记录数据初始化
            rows:[],
            total:0,
            loading:false,
            curr_row:null,
            // 菜单操作
            // 搜索
            search_show: false,
            SearchFormData: {
                start_date: '',
                end_date: '',
                page_no: 1,//当前页 默认第一页
                page_len: 10,//每页数据条数
                order_field:'add_time',//时间排序
                order_sort:'desc'//排序规则
            },
            options_state: [
                {
                    value: 1,
                    label: '完成'
                },
                {
                    value: 0,
                    label: '未完成'
                },
            ],
            options_isOnline: [
                {
                    value: 1,
                    label: '现场'
                },
                {
                    value: 2,
                    label: '线下'
                },
            ],
            // 编辑
            edit_show: false,
            EditFormData: {
                tag_name: '',
            },
            // 添加
            add_show: false,
            AddFormData: {
                tag_name: ''
            },
            AddFormRule: {
                tag_name: [
                    { required: true, message: '请输入标签名称', trigger: 'blur' },
                ]
            },
            // 详情
            detail_show: false,
            DetailFormData: {}
        }
    },
    created () {
        this.init();
    },
    computed:{
        width:() => {
            return store.state.AppData.width;
        },
        height:() => {
            return store.state.AppData.height;
        },
        upload_url:() => {
            return store.state.AppData.upload_url;
        },
        img_host:() => {
            return store.state.AppData.img_host;
        }
    },
    methods: {
        // 按钮点击 menu:参数数据 local是否本地程序
        onSubMenu(menu, local = false) {
            util.submenu(menu,this,cookie_get (), local);
        },

        // 资源标签列表方法
        // 获取资源标签列表数据方法 init
        init () {
            this.loading = true;
            var dataList = {
                start_date: this.SearchFormData.start_date,
                end_date: this.SearchFormData.end_date,
                page_no: this.SearchFormData.page_no,//当前页 默认第一页
                page_len: this.SearchFormData.page_len,//每页数据条数
                order_field: this.SearchFormData.order_field,//时间排序
                order_sort: this.SearchFormData.order_sort,//排序规则
                login_token: cookie_get()
            }
            ManageStudyRecodeList (dataList).then(res => {
                if (res.code == 0) {
                    this.rows = res.data.rows;
                    for (var i = 0;i<res.data.rows.length;i++) {
                        if (res.data.rows[i].state == 0) {
                            this.rows[i].state = '未完成';
                        } else {
                            this.rows[i].state = '完成';
                        }
                        if (res.data.rows[i].is_online == 1) {
                            this.rows[i].is_online = '现场';
                        } else {
                            this.rows[i].is_online = '线上';
                        }
                    }
                    this.total = res.data.total;
                    
                } else {
                    this.$message.error(res.msg);
                }
                this.loading = false;
            })
            // 超时关闭遮罩层
            setTimeout(() => {
                this.loading = false;
            }, 10000);
        },
        // 排序
        onSortChange (column) {
            this.SearchFormData.order_field = column.prop;
            if (column.order == 'ascending') {
                this.SearchFormData.order_sort  = 'asc';
            } else {
                this.SearchFormData.order_sort  = 'desc';
            }
            this.init();
        },
        // 选择行
        onSelectRow (row) {
            this.curr_row = row;
        },
        // 分页
        onPageChange (page) {
            this.SearchFormData.page_no = page;
            this.init();
        },

        // 菜单操作方法
        // 刷新
        onRefresh () {
            this.init();
        },
        // 搜索弹框显示
        onSearch () {
            this.search_show = true;
        },
        // 搜索弹框提交
        onSearchSubmit () {
            this.SearchFormData.page_num = 1;
            this.init();
            this.search_show = false;
            this.SearchFormData.tag_name = '';
        },
        // 编辑弹框显示
        handleEdit () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            this.EditFormData.tag_name = this.curr_row.tag_name;
            this.edit_show = true;
        },
        // 编辑弹框提交
        onEditSubmit () {
            var EditData = {
                tag_name: this.EditFormData.tag_name,
                uuid: this.curr_row.uuid,
                login_token: cookie_get()
            }
            ManageResourceTagEdit (EditData).then(res => {
                if (res.code == 0) {
                    this.init();
                } else {
                    this.$message.error(res.msg);
                }
                this.edit_show = false;
            })
        },
        // 添加弹框显示
        handleAdd () {
            this.add_show = true;
        },
        // 添加弹框提交
        onAddSubmit () {
            var AddData = {
                tag_name: this.AddFormData.tag_name,
                login_token: cookie_get()
            }
            ManageResourceTagAdd (AddData).then(res => {
                if (res.code == 0) {
                    this.$message.success('编辑成功');
                    this.init();
                } else {
                    this.$message.error(res.msg);
                }
                this.add_show = false;
            })
        },
        // 删除
        handleDel () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            this.$confirm('确定删除？','提示').then(() => {
                var DelData = {
                    uuid: this.curr_row.uuid,
                    login_token: cookie_get()
                }
                ManageResourceTagDel (DelData).then(res => {
                    if (res.code == 0) {
                        this.$message.success('删除成功');
                        this.init();
                    } else {
                        this.$message.error(res.msg);
                    }
                })
            })
        },
        // 详情弹框显示
        handleDetail () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            var DetailData = {
                uuid: this.curr_row.uuid,
                login_token: cookie_get()
            }
            ManageStudyRecodeDetail (DetailData).then(res => {
                this.DetailFormData = res.data;
                this.detail_show = true;
            })
        }
        
    }

}
</script>

<style lang="less" scoped>
    .resource{
        .menu{
            display: inline-block;
            padding:0 16px;
            text-align: center;
        }
        .page {
            height: 40px; 
            line-height: 40px; 
            text-align: right;
            position: fixed;
            bottom: 0;
            right:0;
            overflow: hidden;
        }
        ul{
            list-style: none;
            li{
                line-height: 40px;
                div{
                    width: 150px;
                    text-align: right;
                    display: inline-block;
                }
            }
        }
    }
</style>