<!--提交弹窗弹出层模块-->

<template>
    <div class="showDetailModal">
        <el-drawer
                :title="title"
                v-if="type==='drawer'"
                :visible.sync="is_show"
                :size="width"
                >
            <div class="con1">

                <template v-for="(item,index)  in detail_des">
                    <div class="one flew flew-between" :key="index">
                        <div class="name  ">{{item.name}} :</div>

                        <div class="value " v-if="item.type==='image'">

                            <el-image
                                    v-if="item.value"
                                    style="width: 100px; height: 100px"
                                    :src="item.value"
                                    fit="cover"></el-image>
                            <div class="value align-left" v-else>{{ '--'}}</div>
                        </div>
                        <div class="value align-left" v-else>{{item.value||'--'}}</div>
                    </div>

                </template>



            </div>
        </el-drawer>
        <el-dialog
                :title="title"
                v-else
                :visible.sync="is_show"
                :width="width"
        >


            <div class="con1">

                <template v-for="(item,index)  in detail_des">
                    <div class="one flew flew-between" :key="index">
                        <div class="name  ">{{item.name}} :</div>

                        <div class="value " v-if="item.type==='image'">

                            <el-image
                                    v-if="item.value"
                                    style="width: 100px; height: 100px"
                                    :src="item.value"
                                    fit="cover"></el-image>
                            <div class="value align-left" v-else>{{ '--'}}</div>
                        </div>
                        <div class="value align-left" v-else>{{item.value||'--'}}</div>
                    </div>

                </template>



            </div>


        </el-dialog>


    </div>


</template>

<script>



    export default {
        name: "showDetailModal",



        data() {
            return {
                is_show: false,
                type:'',
                detail_des: [
                //     {name: '员工', value: 'hxq'},
                //     {name: '员工', value: 'hxq'},
                //     {name: '员工', value: 'hxq'},
                //     {name: '员工', value: 'hxq'},
                //     {name: '员工', value: 'hxq'},
                ]


            }

        },
        props: {
            title: {
                type: String,
                default: '标题'
            },
            width: {
                default: '30%'
            }
        },
        methods: {

            /*展现*/
            show(val,type) {

                this.type =type;
                this.detail_des = val
                this.is_show = true;
            },
            /*隐藏*/
            hide() {
                this.is_show = false;
            },
            /*取消*/
            cancel() {
                this.hide()

            },
            /*确认*/
            sure(val) {

                this.hide();

                this.$emit('sure', val)

            }

        }
    }
</script>

<style scoped lang="less">

    @import "../css/common";

    .showDetailModal {

    }

    .con1 {
        padding: 0 30px;

        .name {
            width: 50%;
        }

        .value {
            width: 50%;
        }
    }


    .one {
        padding: 5px 0;
    }
</style>