/**
 * @description 工具函数
 * */
 import  fieldDesConf from './fieldDesConf'
 import config from "../../config";

 import elementUtils from "./elementUtils";

export default {
    ...elementUtils,

     /*获取配置文件*/
     getConfig(key){
         if(key){
             return config[key]||''
         }else {
             return  config;
         }


     },
    /*获取字段描叙 关联 fieldDesConf.js  用于table渲染
    *
    * 返回值状态
    * { //营业状态
        //     '0':'营业',
        //     '1':'暂停'
        // }
    * */
    getFieldDesConf(model,key){
        let data = fieldDesConf[model]||[];
        data=data[key];

        let re = {};//
        for (let i in data){
            let item  =data[i];
            re[item.value+''] = item;

        }
        return re;




    },
    /*获取字段描叙 关联 fieldDesConf.js  用户from表单*/
    getFromFieldDesConf(){

    },
    /*校验格式*/
    checkParams(val='_213123',type='pwd'){

        let re = {
            is_check:false,//是否通过校验
            msg:'',//提示

        };
        let r='';

        switch(type) {
            case 'tel'://手机号类型检测
                if (typeof val !== 'string'){
                    re.msg='数据类型错误';
                }
                if(val.length!==11){
                    re.msg='手机号长度错误';
                }else {


                    if(val[0] ==='1'){
                        re.is_check=true;
                        re.msg='';
                    }
                }



                break;

            case 'pwd':
                    r = /^(?![0-9]+$)(?![a-z]+$)(?![A-Z]+$)(?!([^(0-9a-zA-Z)])+$).{6,20}$/.test(val)
                    if(r){
                        re.is_check=true;
                        re.msg='';
                    }else {
                        re.is_check=false;
                        re.msg='密码必须是6-20位字符,必须英文,数字,特殊字符两种及以上的混合';
                    }


                break;

        }


        return re;




    }


    /**
     * @description 检测对象是否有包含关系
     * @return boolean
     * */
    ,objInner(big_obj,inner_obj){


        for (let i in inner_obj){

            if( inner_obj[i] !== big_obj[i]){

                return  false;
            }

        }
        return true;


    }


    /*给数组对象的某个子对象 的某个属性赋值*/
    ,editObjField(arr_obj,obj,new_obj){


        for (let i =0 , len = arr_obj.length; i < len;i++){


            let item = arr_obj[i];

            // 对象检测是否存在要修改的列

           let is_inner =   this.objInner(arr_obj[i],obj);

           if(is_inner){

               Object.assign(item,new_obj)

           }


        }
        // console.log(arr_obj)



    },
    /*值转化为数字*/
    val2Number(val){
        let num = '';
        num = parseInt(val)

        if (isNaN(num)){
            num=''
        }
        return num
    },

}