/**
 * @description 关于element 的方法
 * */

import {Message, Loading, Progress} from 'element-ui';
import utils from "./utils";

export default {
    Message,
    Loading(ele, text = '拼命加载中...') {
        return Loading.service({
            lock: true,
            text: text,//可以自定义文字
            spinner: 'el-icon-loading',//自定义加载图标类名
            // background: 'rgba(0, 0, 0, 0.7)',//遮罩层背景色
            target: ele
        })
    },
    /*手机号格式校验*/
    telCheck(rule, value, callback) {

        if (!value) {
            callback(new Error('手机号不能为空'));
        } else {
            let re = utils.checkParams(value, 'tel')
            if (!re.is_check) {
                callback(new Error('手机号格式错误'));
            } else {
                callback();
            }
        }
    }
}