<template>
    <!--区域选择Cascader 级联选择器-->
    <div>
        <div class="block">


            <div class="one">

                <el-select v-model="value[0]" placeholder="请选择" @change="provChange">
                    <el-option
                            v-for="item in prov_list"
                            :key="item.value"
                            :label="item.label"

                            :value="item.value">
                    </el-option>
                </el-select>
            </div>

            <div class="one" v-if="value[0]">
                <el-select v-model="value[1]" placeholder="请选择" @change="cityChange">
                    <el-option
                            v-for="item in city_list"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </div>
            <div class="one" v-if="value[1]">
                <el-select v-model="value[2]" placeholder="请选择" @change="areaChange">
                    <el-option
                            v-for="item in area_list"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </div>


        </div>
    </div>


</template>

<script>
    import coneHttpOther from '../_commom/connectorHttp/coneHttpOther'

    import utils from "../_commom/lib/utils";

    export default {
        name: "areaChoose",

        data() {
            return {

                grade: 0,


                prov_list: [],
                city_list: [],
                area_list: [],
                // value: [ ]
            }

        },
        props: {
            value:{
                default(){
                    return []


                }
            }
        },
        methods: {

            getProps1() {

                return {
                    multiple: false,
                    emitPath: true


                }
            },


            /*获取省份*/
            getProv() {
                let grade = 0;
                return new Promise((resolve, reject) => {
                    coneHttpOther.getProv({grade})
                        .then(res => {

                            let data = res.data;

                            this.prov_list = data.map(v => {
                                return {value: v.uuid, label: v.name, ...v}
                            });
                            resolve(this.prov_list);

                        })
                })
            },
            /*获取city*/
            getCity(id) {
                let grade = 1;
                return new Promise((resolve, reject) => {
                    coneHttpOther.getProv({parent_uuid: id, grade})
                        .then(res => {

                            let data = res.data;

                            this.city_list = data.map(v => {
                                return {value: v.uuid, label: v.name, ...v}
                            });
                            // this.city_list.unshift({value:"",label:'全部'})
                            resolve(this.city_list);

                        })
                })
            },
            /*getArea*/
            getArea(id) {
                let grade = 2;
                return new Promise((resolve, reject) => {
                    coneHttpOther.getProv({parent_uuid: id, grade})
                        .then(res => {

                            let data = res.data;

                            this.area_list = data.map(v => {
                                return {value: v.uuid, label: v.name, ...v}
                            });
                            // this.area_list.unshift({value:"",label:'全部'})
                            resolve(this.area_list);

                        })
                })
            },
            /*选择省份*/
            provChange(val) {
                console.log('provChange',val)


                let self = this;
                return new Promise((resolve, reject) => {
                    this.getCity(val)
                        .then(data => {
                            console.log(data)
                            let city_id = data[0].uuid
                            self.value[1] = city_id;


                            this.getArea(city_id)
                                .then(data => {
                                    let area_id = data[0].uuid;
                                    self.value[2] = area_id;
                                    this.$forceUpdate();

                                    self.$emit('areaChange',self.value)
                                })

                        })

                })


            },
            cityChange(val) {

              let self = this ;
                let city_id =val
                self.value[1] = city_id;


                this.getArea(city_id)
                    .then(data => {
                        let area_id = data[0].uuid;
                        self.value[2] = area_id;
                        this.$forceUpdate();
                        self.$emit('areaChange',self.value)
                    })


            },
            areaChange() {
                this.$emit('areaChange',this.value)

            },
            init(){

                let value = this.value;
                // console.log('this.value',this.value)
                // if(!this.value[0]){
                //
                //     return false;
                // }

                this.getProv()
                    .then(data => {
                        let prov_id = value[0] || data[0].value;
                        this.getCity(prov_id)
                            .then(data => {
                                let city_id = value[1] || data[0].value;
                                this.getArea(city_id)
                            })

                    })
            }
        },
        created() {
            console.log('created')
        },
        mounted() {
            console.log('mounted')

            this.init()

        },
        updated() {
            console.log('updated')
            // this.init()

        },
        beforeDestroy(){
            console.log('beforeDestroy')
        },
        watch:{
            value(val, oldVal){//普通的watch监听
                // console.log("value: "+val, oldVal);
                // this.init()
            },

        }
    }
</script>

<style scoped lang="less">

    .block {
        display: flex;
        justify-content: space-between;
    }

    .one {


    }
</style>