/*
试题管理
*/
<template>
    <div class="test-quest">
        <!-- 菜单操作 -->
        <div style="height: 46px; line-height: 46px; overflow: hidden;">
            <el-row>
                <el-col :span="6">
                    <div style="padding-left:16px;">
                        <i class="el-icon-s-unfold"></i>
                        <span style="padding-left:9px;">
                            {{$store.state.AdminData.active_title}}
                        </span>
                    </div>
                </el-col>

                <el-col :span="18">
                    <div style="text-align: right; ">
                        <el-link @click="onSubMenu('onRefresh',true)" class="menu">刷新</el-link>
                        <el-link @click="onSubMenu('onSearch',true)" class="menu">搜索</el-link>

                        <el-link
                            class="menu" 
                            @click="onSubMenu(item)"
                            v-for="(item,index) in $store.state.AdminData.right_menus" 
                            :key="index">
                            {{item.name}}
                        </el-link>
                    </div>
                </el-col>
            </el-row>
        </div>
        <!-- 试题管理列表 -->
        <div style="border-top: solid 1px #f2f1f4;">
            <el-table 
                :data="rows"
                :height="height - 60 - 46 - 48"
                v-loading="loading"
                element-loading-text="拼命加载中"
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                @sort-change="onSortChange"
                :highlight-current-row="true"
                @current-change="onSelectRow"
                style="width: 100%" 
                size="mini">
                
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="title" label="题目"></el-table-column>
                <el-table-column prop="ques_type" label="题型" align="center" width="120"></el-table-column>
                <el-table-column prop="content" label="选项" align="center" width="100">
                    <template slot-scope="scope">
                        <!-- <div v-for="(item,index) in scope.row.content" :key="index">{{item.opt + '. ' + item.content}}&nbsp;<span @click="editOption(item)" style="color: red;cursor: pointer;">编辑</span><span @click="delOption(item)" style="color: red;cursor: pointer;">删除</span></div> -->
                        <el-button
                        size="mini"
                        @click="handleSee(scope.$index, scope.row)">查看</el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="is_pay" label="是否付费" align="center"></el-table-column>
                <el-table-column prop="is_show" label="是否展示" align="center"></el-table-column>
                <el-table-column prop="tag" label="标签" align="center">
                    <template slot-scope="scope">
                        <el-tag v-for="(item,index) in scope.row.tag" :key="index">{{item.title}}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="answer" label="答案" align="center"></el-table-column>
                <el-table-column prop="check_state" label="审核状态" align="center"></el-table-column>
                <el-table-column prop="add_time" label="添加时间" :formatter="common.formatDate" align="center" sortable></el-table-column>
            </el-table>
            
            <div class="page" :style="{width:width - 250 + 'px'}">
                <el-pagination
                    :current-page.sync="SearchFormData.page_num"
                    @current-change="onPageChange"
                    layout="prev, pager, next, jumper"
                    :total="total">
                </el-pagination>
            </div>
        </div>
        <!-- 菜单操作弹框 -->
        <!-- 搜索弹框 -->
        <el-dialog
            title="搜索"
            :visible.sync="search_show"
            width=420px>
            <el-form :model="SearchFormData" label-width="100px" size="small">
                <el-form-item label="题目内容：">
                    <el-input v-model="SearchFormData.title"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="search_show = false" size="small">取 消</el-button>
                <el-button type="primary" @click="onSearchSubmit" size="small">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 编辑抽屉 -->
        <el-drawer
            title="编辑"
            :visible.sync="edit_show"
            size="40%">
            <div style="padding-right: 10px;position: relative;">
                <el-form :model="EditFormData" :rules="EditFormRule" label-width="150px" size="small">
                    <el-form-item label="题目内容：" prop="title">
                        <el-input v-model="EditFormData.title"/>
                    </el-form-item>
                    <el-form-item label="题型：" prop="ques_type">
                        <el-radio-group v-model="EditFormData.ques_type" @change="onEditQuesType">
                            <el-radio :label="1">判断题</el-radio>
                            <el-radio :label="2">单选题</el-radio>
                            <el-radio :label="3">多选题</el-radio>
                            <el-radio :label="4">简答</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item v-if="EditFormData.ques_type == 4" label="答案：" prop="answer">
                        <el-input v-model="EditFormData.answer" type="textarea" :rows="5"/>
                    </el-form-item>
                    <el-form-item v-if="EditFormData.ques_type == 1" label="判断题题选项：" prop="optionArr">
                        <p>
                            A&nbsp;&nbsp;对
                        </p>
                        <p>
                            B&nbsp;&nbsp;错
                        </p>
                    </el-form-item>
                    <div v-else-if="EditFormData.ques_type == 2">
                        <el-form-item label="单选题选项：" prop="optionArr">
                            <p>
                                A
                                <el-input v-model="optionContent1" placeholder=""/>
                            </p>
                            <p>
                                B
                                <el-input v-model="optionContent2" placeholder=""/>
                            </p>
                            <p>
                                C
                                <el-input v-model="optionContent3" placeholder=""/>
                            </p>
                            <p>
                                D
                                <el-input v-model="optionContent4" placeholder=""/>
                            </p>
                        </el-form-item>
                    </div>
                    <div v-else-if="EditFormData.ques_type == 3">
                        <el-form-item label="多选题选项：" prop="optionArr">
                            <p>
                                A
                                <el-input v-model="optionContent1" placeholder=""/>
                            </p>
                            <p>
                                B
                                <el-input v-model="optionContent2" placeholder=""/>
                            </p>
                            <p>
                                C
                                <el-input v-model="optionContent3" placeholder=""/>
                            </p>
                            <p>
                                D
                                <el-input v-model="optionContent4" placeholder=""/>
                            </p>
                        </el-form-item>
                    </div>
                    
                    <el-form-item v-if="EditFormData.ques_type == 1 || EditFormData.ques_type == 2" label="请选择正确选项：" prop="true_option">
                        <el-radio-group v-if="EditFormData.ques_type == 1" v-model="EditFormData.true_option">
                            <el-radio label="A">A</el-radio>
                            <el-radio label="B">B</el-radio>
                        </el-radio-group>
                        <el-radio-group v-else-if="EditFormData.ques_type == 2" v-model="EditFormData.true_option">
                            <el-radio label="A">A</el-radio>
                            <el-radio label="B">B</el-radio>
                            <el-radio label="C">C</el-radio>
                            <el-radio label="D">D</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item v-if="EditFormData.ques_type == 3" label="请选择正确选项：" prop="true_option_arr1">
                        <el-checkbox-group v-model="EditFormData.true_option_arr" @change="restCheckbox">
                            <el-checkbox label="A" name="true_option_arr" style="width: 60px;"></el-checkbox>
                            <el-checkbox label="B" name="true_option_arr" style="width: 60px;"></el-checkbox>
                            <el-checkbox label="C" name="true_option_arr" style="width: 60px;"></el-checkbox>
                            <el-checkbox label="D" name="true_option_arr" style="width: 60px;"></el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>
                    <el-form-item label="分析：" prop="analysis">
                        <el-input v-model="EditFormData.analysis" type="textarea" :rows="5"/>
                    </el-form-item>
                    <el-form-item label="标签：" prop="tag_uuids">
                        <!-- <el-button @click="addTags" type="primary">添加标签</el-button>
                        <el-input v-model="addTagsInputList" placeholder="展示添加的标签"/> -->

                        <!-- <el-autocomplete
                            v-model="state"
                            :fetch-suggestions="querySearchAsync"
                            placeholder="请输入内容"
                            @select="handleSelect"
                        ></el-autocomplete> -->
                        <el-select
                            v-model="value"
                            multiple
                            filterable
                            allow-create
                            default-first-option
                            placeholder="请选择标签">
                            <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>




                    </el-form-item>
                    <el-form-item label="试题图片地址：" prop="pic_urls">
                        <el-upload
                            class="upload-demo"
                            action="string"
                            :http-request="UploadImage"  
                            list-type="picture"
                        >
                            <el-button size="small" type="primary">点击上传图片</el-button>
                            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="是否需要支付：" prop="is_pay">
                        <el-radio-group v-model="EditFormData.is_pay">
                            <el-radio :label="0">免费</el-radio>
                            <el-radio :label="1">支付</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item v-if="EditFormData.is_pay == 1" label="学币：" prop="min_num">
                        <el-input type="number" v-model="EditFormData.min_num" style="width: 100px;"/>
                    </el-form-item>
                    <el-form-item label="是否展示：" prop="is_show">
                        <el-switch v-model="EditFormData.is_show">
                        </el-switch>
                    </el-form-item>
                </el-form>

                <span slot="footer" style="display: inline-block;position: fixed;bottom: 20px;right: 16%;">
                    <el-button @click="edit_show = false" size="small">取 消</el-button>
                    <el-button type="primary" @click="onEditSubmit('EditFormData')" size="small">确 定</el-button>
                </span>
            </div>
        </el-drawer>
        <!-- 添加弹框 -->
        <el-drawer
            title="添加"
            :visible.sync="add_show"
            size="40%">
            <div style="padding-right: 10px;position: relative;overflow-y: auto;height: calc(100vh - 135px);">
                <el-form ref="AddFormData" :model="AddFormData" :rules="AddFormRule" label-width="150px" size="small">
                    <el-form-item label="题目内容：" prop="title">
                        <el-input v-model="AddFormData.title"/>
                    </el-form-item>
                    <el-form-item label="题型：" prop="ques_type">
                        <el-radio-group v-model="AddFormData.ques_type" @change="onQuesType">
                            <el-radio :label="1">判断题</el-radio>
                            <el-radio :label="2">单选题</el-radio>
                            <el-radio :label="3">多选题</el-radio>
                            <el-radio :label="4">简答</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item v-if="AddFormData.ques_type == 4" label="答案：" prop="answer">
                        <el-input v-model="AddFormData.answer" type="textarea" :rows="5"/>
                    </el-form-item>
                    <el-form-item v-if="AddFormData.ques_type == 1" label="判断题题选项：" prop="optionArr">
                        <p>
                            A&nbsp;&nbsp;对
                        </p>
                        <p>
                            B&nbsp;&nbsp;错
                        </p>
                    </el-form-item>
                    <div v-else-if="AddFormData.ques_type == 2">
                        <el-form-item label="单选题选项：" prop="optionArr">
                            <p>
                                A
                                <el-input v-model="optionContent1" placeholder=""/>
                            </p>
                            <p>
                                B
                                <el-input v-model="optionContent2" placeholder=""/>
                            </p>
                            <p>
                                C
                                <el-input v-model="optionContent3" placeholder=""/>
                            </p>
                            <p>
                                D
                                <el-input v-model="optionContent4" placeholder=""/>
                            </p>
                        </el-form-item>
                    </div>
                    <div v-else-if="AddFormData.ques_type == 3">
                        <el-form-item label="多选题选项：" prop="optionArr">
                            <p>
                                A
                                <el-input v-model="optionContent1" placeholder=""/>
                            </p>
                            <p>
                                B
                                <el-input v-model="optionContent2" placeholder=""/>
                            </p>
                            <p>
                                C
                                <el-input v-model="optionContent3" placeholder=""/>
                            </p>
                            <p>
                                D
                                <el-input v-model="optionContent4" placeholder=""/>
                            </p>
                        </el-form-item>
                    </div>
                    
                    <el-form-item v-if="AddFormData.ques_type == 1 || AddFormData.ques_type == 2" label="请选择正确选项：" prop="true_option">
                        <el-radio-group v-if="AddFormData.ques_type == 1" v-model="AddFormData.true_option">
                            <el-radio label="A">A</el-radio>
                            <el-radio label="B">B</el-radio>
                        </el-radio-group>
                        <el-radio-group v-else-if="AddFormData.ques_type == 2" v-model="AddFormData.true_option">
                            <el-radio label="A">A</el-radio>
                            <el-radio label="B">B</el-radio>
                            <el-radio label="C">C</el-radio>
                            <el-radio label="D">D</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item v-else-if="AddFormData.ques_type == 3" label="请选择正确选项：" prop="true_option_arr">
                        <el-checkbox-group v-model="AddFormData.true_option_arr" @change="restCheckbox">
                            <el-checkbox label="A" name="true_option_arr" style="width: 60px;"></el-checkbox>
                            <el-checkbox label="B" name="true_option_arr" style="width: 60px;"></el-checkbox>
                            <el-checkbox label="C" name="true_option_arr" style="width: 60px;"></el-checkbox>
                            <el-checkbox label="D" name="true_option_arr" style="width: 60px;"></el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>
                    <el-form-item label="分析：" prop="analysis">
                        <el-input v-model="AddFormData.analysis" type="textarea" :rows="5"/>
                    </el-form-item>
                    <el-form-item label="标签：" prop="tag_uuids">
                        <!-- <el-button @click="addTags" type="primary">添加标签</el-button>
                        <el-input v-model="addTagsInputList" placeholder="展示添加的标签"/> -->
                        <el-select
                            v-model="value"
                            multiple
                            filterable
                            allow-create
                            default-first-option
                            placeholder="请选择标签">
                            <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="试题图片地址：" prop="pic_urls">
                        <el-upload
                            class="upload-demo"
                            action="string"
                            :http-request="UploadImage"  
                            list-type="picture"
                        >
                            <el-button size="small" type="primary">点击上传图片</el-button>
                            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="是否需要支付：" prop="is_pay">
                        <el-radio-group v-model="AddFormData.is_pay">
                            <el-radio :label="0">免费</el-radio>
                            <el-radio :label="1">支付</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item v-if="AddFormData.is_pay == 1" label="学币：" prop="min_num">
                        <el-input type="number" v-model="AddFormData.min_num" style="width: 100px;"/>
                    </el-form-item>
                    <el-form-item label="是否展示：" prop="is_show">
                        <el-switch v-model="AddFormData.is_show">
                        </el-switch>
                    </el-form-item>
                </el-form>

                <span slot="footer" style="display: inline-block;position: fixed;bottom: 20px;right: 16%;">
                    <el-button @click="onAddCancel('AddFormData')" size="small">取 消</el-button>
                    <el-button type="primary" @click="onAddSubmit('AddFormData')" size="small">确 定</el-button>
                </span>
            </div>
        </el-drawer>
        <!-- 标签列表弹框 -->
        <el-dialog
            title="标签列表"
            :visible.sync="tags_show"
            width=500px>
            <el-form :model="TagsFormData" label-width="80px" size="small">
                <el-checkbox-group v-model="selectTitle">
                    <el-checkbox v-for="(item,index) in tagsList" :key="index" :label="item" @change="selectTitleFn">{{item.title}}</el-checkbox>
                </el-checkbox-group>
            </el-form>

            <span slot="footer">
                <el-button @click="tags_show = false" size="small">取 消</el-button>
                <el-button type="primary" @click="onTagsAddSubmit" size="small">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 详情弹框 -->
        <el-drawer
            title="详情"
            :visible.sync="detail_show"
            size=40%>
            <ul class="test-quest-ul">
                <li>
                    <div>题目内容：</div>{{DetailFormData.title}}
                </li>
                <li>
                    <div>是否付费：</div>{{DetailFormData.is_pay}}
                </li>
                <li>
                    <div>是否展示：</div>{{DetailFormData.is_show}}
                </li>
                <li>
                    <div>答案：</div><a v-for="(item,index) in DetailFormData.answer" :key="index">{{item}}</a>
                </li>
                <li>
                    <div>分析：</div>{{DetailFormData.analysis}}
                </li>
                <li>
                    <div>标签：</div>
                    <el-tag v-for="(item,index) in DetailFormData.tag" :key="index">{{item.title}}</el-tag>
                </li>
                <li>
                    <div>题型：</div>{{DetailFormData.ques_type}}
                </li>
                <li style="overflow: hidden;">
                    <div style="float: left;">选项：</div>
                    <div style="float: left;">
                        <div v-for="(item,index) in DetailFormData.content" :key="index" style="text-align: left;">
                            {{item.opt}}.{{item.content}}
                        </div>
                    </div>
                </li>
                <li style="overflow: hidden;">
                    <div style="float: left;">图片：</div><img :src="DetailFormData.pic_urls" style="width: 300px;float: left;">
                </li>
                <li>
                    <div>审核状态：</div>{{DetailFormData.check_state == 0 ? '草稿' : (DetailFormData.check_state == 1 ? '已审核' : (DetailFormData.check_state == 2 ? '待审核' : '作废'))}}
                </li>
                <li>
                    <div>添加时间：</div>{{DetailFormData.add_time}}
                </li>
            </ul>

            <el-button type="primary" @click="detail_show = false" size="small" style="position: fixed;bottom: 10px;right: 16%;">取 消</el-button>
        </el-drawer>

        <!-- 添加选项 -->
        <el-dialog
            title="添加选项"
            :visible.sync="add_options_show"
            width="540px">
            <div v-if="questionType == '单选题' || questionType == '判断题' || questionType == '多选题'">
                <el-form :model="AddOptionFormData" :rules="AddOptionFormRule" label-width="100px" size="small">
                    <el-form-item label="选项内容：" prop="option_content">
                        <el-input v-model="AddOptionFormData.option_content" type="textarea" rows="3" placeholder="请输入选项内容" style="width: 380px;display: inline-block;"/>
                    </el-form-item>
                    <el-form-item label="答案：" prop="is_answer">
                        <el-radio-group v-model="AddOptionFormData.is_answer">
                            <el-radio :label="1">正确答案</el-radio>
                            <el-radio :label="0">错误答案</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-form>
            </div>
            <span slot="footer">
                <el-button @click="add_options_show = false" size="small">取 消</el-button>
                <el-button type="primary" @click="AddOptionsSubmit" size="small">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 编辑选项 -->
        <!-- <el-dialog
            title="编辑选项"
            :visible.sync="edit_options_show"
            width="500px">
            <el-form :model="EditOptionFormData" label-width="100px" size="small">
                <el-form-item label="选项内容：" prop="option_content">
                    <el-input v-model="EditOptionFormData.option_content" type="textarea" :rows="3" placeholder="请输入选项内容"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="edit_options_show = false" size="small">取 消</el-button>
                <el-button type="primary" @click="EditOptionsSubmit" size="small">确 定</el-button>
            </span>
        </el-dialog> -->
        <!-- 选项查看 -->
        <el-dialog
            title="查看选项"
            :visible.sync="see_options_show"
            width="700px">
            <!-- <el-form :model="SeeOptionFormData" label-width="100px" size="small">
                <el-form-item v-for="(item,index) in SeeOptionContent" :key="index" label="选项内容：" prop="option_content">
                    <span>{{item.opt}}</span>&nbsp;
                    
                    <span style="width: 350px;display: inline-block;">{{item.content}}</span>
                    &nbsp;
                    <el-button :ref="`icon${index}`" @click="EditOption('input'+index,index)" type="primary">编辑</el-button>
                    <el-button @click="DelOption" type="danger" icon="el-icon-delete"></el-button>
                </el-form-item>
            </el-form> -->
            <el-table 
                :data="SeeOptionContent"
                v-loading="loading"
                element-loading-text="拼命加载中"
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                @sort-change="onSortChange"
                :highlight-current-row="true"
                @current-change="onSelectRow"
                style="width: 100%" 
                size="mini">
                <el-table-column prop="opt" label="选项" width="80"></el-table-column>
                <el-table-column prop="content" label="内容" width="400">
                    <template slot-scope="scope">
                        <el-input v-model="scope.row.content" @input="onInput"></el-input>
                    </template>
                </el-table-column>
                <el-table-column prop="content" label="操作" align="center">
                    <template slot-scope="scope">
                        <el-button
                        size="mini"
                        @click="handleOptionEdit1(scope.$index, scope.row)">编辑</el-button>
                        <el-button
                        size="mini"
