                    option_D = 0;
                } else if (this.AddFormData.true_option == 'B') {
                    option_A = 0;
                    option_B = 1;
                    option_C = 0;
                    option_D = 0;
                } else if (this.AddFormData.true_option == 'C') {
                    option_A = 0;
                    option_B = 0;
                    option_C = 1;
                    option_D = 0;
                } else {
                    option_A = 0;
                    option_B = 0;
                    option_C = 0;
