                console.log(this.AddFormData.true_option_arr)
                if (this.AddFormData.true_option_arr.length == 1) {
                    if (this.AddFormData.true_option_arr[0] == 'A') {
                        option_A = 1;option_B = 0;option_C = 0;option_D = 0;
                    } else if (this.AddFormData.true_option_arr[0] == 'B') {
                        option_A = 0;option_B = 1;option_C = 0;option_D = 0;
                    } else if (this.AddFormData.true_option_arr[0] == 'C') {
                        option_A = 0;option_B = 0;option_C = 1;option_D = 0;
                    } else {
                        option_A = 0;option_B = 0;option_C = 0;option_D = 1;
                    }
                }
                if (this.AddFormData.true_option_arr.length == 2) {
                    if (this.AddFormData.true_option_arr[0] == 'A' && this.AddFormData.true_option_arr[1] == 'B') {
                        option_A = 1;option_B = 1;option_C = 0;option_D = 0;
                    } else if (this.AddFormData.true_option_arr[0] == 'A' && this.AddFormData.true_option_arr[1] == 'C') {
                        option_A = 1;option_B = 0;option_C = 1;option_D = 0;
                    } else if (this.AddFormData.true_option_arr[0] == 'A' && this.AddFormData.true_option_arr[1] == 'D') {
