                        option_A = 0;option_B = 1;option_C = 0;option_D = 1;
                    } else if (this.AddFormData.true_option_arr[0] == 'C' && this.AddFormData.true_option_arr[1] == 'D') {
                        option_A = 0;option_B = 0;option_C = 1;option_D = 1;
                    }
                }
                if (this.AddFormData.true_option_arr.length == 3) {
                    if (this.AddFormData.true_option_arr[0] == 'A' && this.AddFormData.true_option_arr[1] == 'B' && this.AddFormData.true_option_arr[1] == 'C') {
                        option_A = 1;option_B = 1;option_C = 1;option_D = 0;
                    } else if (this.AddFormData.true_option_arr[0] == 'A' && this.AddFormData.true_option_arr[1] == 'B' && this.AddFormData.true_option_arr[1] == 'D') {
                        option_A = 1;option_B = 1;option_C = 0;option_D = 1;
                    } else if (this.AddFormData.true_option_arr[0] == 'A' && this.AddFormData.true_option_arr[1] == 'C' && this.AddFormData.true_option_arr[1] == 'D') {
                        option_A = 1;option_B = 0;option_C = 1;option_D = 1;
                    } else if (this.AddFormData.true_option_arr[0] == 'B' && this.AddFormData.true_option_arr[1] == 'C' && this.AddFormData.true_option_arr[1] == 'D') {
                        option_A = 0;option_B = 1;option_C = 1;option_D = 1;
                    }
                }
                if (this.AddFormData.true_option_arr.length == 4) {
                    if (this.AddFormData.true_option_arr[0] == 'A' && this.AddFormData.true_option_arr[1] == 'B' && this.AddFormData.true_option_arr[1] == 'C' && this.AddFormData.true_option_arr[1] == 'D') {
                        option_A = 1;option_B = 1;option_C = 1;option_D = 1;
                    }
                }
            }
            
            this.AddFormData.optionArr = [{content: this.optionContent1, is_answer: option_A},{content: this.optionContent2, is_answer: option_B},{content: this.optionContent3, is_answer: option_C},{content: this.optionContent4, is_answer: option_D}]
            console.log(this.AddFormData.optionArr)
            console.log(this.AddFormData.is_pay)
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    var AddData;
                    if (this.AddFormData.ques_type == 4) {
                        AddData = {
                            title: this.AddFormData.title,
                            tag_uuids: this.tagsUuidList,
                            is_pay: this.AddFormData.is_pay,
                            ques_type: this.AddFormData.ques_type,
                            is_show: this.AddFormData.is_show == false ? 0 : 1,
                            analysis: this.AddFormData.analysis,
                            pic_urls: this.AddFormData.pic_urls,
                            answer: this.AddFormData.answer,
                            optionArr: this.AddFormData.optionArr,
                            min_num: parseFloat(this.AddFormData.min_num),
                            login_token: cookie_get()
                        }
                    } else {
                        AddData = {
                            title: this.AddFormData.title,
                            tag_uuids: this.tagsUuidList,
                            is_pay: this.AddFormData.is_pay,
                            ques_type: this.AddFormData.ques_type,
                            is_show: this.AddFormData.is_show == false ? 0 : 1,
                            analysis: this.AddFormData.analysis,
                            pic_urls: this.AddFormData.pic_urls,
                            optionArr: this.AddFormData.optionArr,
                            min_num: parseFloat(this.AddFormData.min_num),
                            login_token: cookie_get()
                        }
                    }
                    
                    console.log(AddData)
                    ManageQuesQuestionAdd (AddData).then(res => {
                        if (res.code == 0) {
                            this.$message.success('添加成功');
                            this.$refs[formName].resetFields();
                            this.init();
                        } else {
                            this.$message.error(res.msg);
                        }
                        this.add_show = false;
                    })
                } else {
                    return false;
                }
            });
        },
        // 添加弹框取消
        onAddCancel (formName) {
            this.$refs[formName].resetFields();
            this.add_show = false;
        },
        // 删除
        handleDel () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            this.$confirm('确定删除？','提示').then(() => {
                var DelData = {
                    uuid: this.curr_row.uuid,
                    login_token: cookie_get()
                }
                ManageQuesQuestionDel (DelData).then(res => {
                    if (res.code == 0) {
                        this.$message.success('删除成功');
                        this.init();
                    } else {
                        this.$message.error(res.msg);
                    }
                })
            })
        },
        // 详情弹框显示
        handleDetail () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            var DetailData = {
                uuid: this.curr_row.uuid,
                login_token: cookie_get()
            }
            ManageQuesQuestionDetail (DetailData).then(res => {
                this.DetailFormData = res.data;
                this.DetailFormData.is_pay = res.data.is_pay == 0 ? '免费' : '收费';
                this.DetailFormData.is_show = res.data.is_show == 0 ? '未展示' : '展示';
                //this.DetailFormData.ques_type = res.data.ques_type == 1 ? '判断题' 
                if (res.data.ques_type == 1) {
                    this.DetailFormData.ques_type = '判断题';
                } else if (res.data.ques_type == 2) {
                    this.DetailFormData.ques_type = '单选题';
                } else if (res.data.ques_type == 3) {
                    this.DetailFormData.ques_type = '多选题';
                } else {
                    this.DetailFormData.ques_type = '简答题';
                }
                this.detail_show = true;
            })
        },

        // 添加选项
        handleAddOptions () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            this.questionType = this.curr_row.ques_type;
            console.log(this.questionType)
            this.add_options_show = true;
        },
        // 添加选项提交
        AddOptionsSubmit () {
            var optionContent = {
                content: this.AddOptionFormData.option_content,
                question_uuid: this.curr_row.uuid,
                is_answer: this.AddOptionFormData.is_answer,
                login_token: cookie_get()
            }
            ManageQuesOptionAdd (optionContent).then(res => {
                if (res.code == 0) {
                    this.init();
                    this.add_options_show = false;
                } else {
                    this.$message.error(res.msg);
                }
            })
        },
        // 选项查看
        handleSee (index,row) {
            console.log(row)
            this.SeeOptionContent = row.content;
            this.rightKey = row.answer;
            this.see_options_show = true;
        },
        // 编辑选项
        onInput (e) {
            console.log(e)
            this.EditOptionFormData.option_content = e;
        },


        EditOption (a,index) {
            this.$nextTick(() => {
                this.$refs[`list${index}`][0].focus()
            })
        },
        // onInput (index,e) {
        //     this.$nextTick(() => {
        //         //this.iconEdit = 'el-icon-check'
        //         //console.log(this.iconEdit)
        //         this.$refs[`icon${index}`][0].icon = 'el-icon-check';
        //     })
        // },
        // 编辑选项----
        editOption (con) {
            console.log(con)
            this.edit_options_show = true;
            this.EditOptionFormData.option_content = con.content;
            this.EditOptionFormData.is_answer = con.content;
        },
        // 编辑选项
        handleEditOptions () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            this.edit_options_show = true;
            
        },
        SeeOptionsSubmit () {

        },
        // 选项查看编辑
        handleOptionEdit1 (index,row) {
            var optionContent = {
                content: this.EditOptionFormData.option_content,
                uuid: row.uuid,
                login_token: cookie_get()
            }
            ManageQuesOptionEdit (optionContent).then(res => {
                if (res.code == 0) {
                    this.$message.success('编辑成功');
                } else {
                    this.$message.error(res.msg);
                }
            })
        },
        // 选项查看删除
        handleOptionDel1 (index,row) {
            this.$confirm('确认删除?', '提示').then(() => {
                ManageQuesOptionDel({
                    login_token:cookie_get(),
                    uuid:row.uuid
                }).then(res => {
                    if (res.code == 0) {
                        this.$message.success('删除成功');
                        this.init();
                        this.handleSee();
                    } else {
                        this.$message.error(res.msg);
                    }
                }).catch(err => {
                    //this.$message.error(err.msg);
                })
            })
        },
        // 
        DelOption () {

        },
        // 删除选项
        delOption () {
            var optionContent = {
                uuid: this.curr_row.uuid,
                login_token: cookie_get()
            }
            ManageQuesOptionDel (optionContent).then(res => {

            })
        },

        // 审核
        handleCheck () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            this.check_show = true;
        },
        // 审核提交
        CheckSubmit () {
            var CheckList = {
                uuid: this.curr_row.uuid,
                check_state: this.CheckFormData.check_state,
                remark: this.CheckFormData.remark,
                login_token: cookie_get()
            }
            ManageQuesQuestionCheck (CheckList).then(res => {
                if (res.code == 0) {
                    this.init();
                    this.$message.success('审核成功');
                } else {
                    this.$message.error(res.msg);
                }
                this.check_show = false;
            })
        },
    }

}
</script>

