/**
 * @description 运营平台的路由
 * */
import StaffMange from './pages/manage/StaffManage.vue'
import RoleManage from './pages/manage/RoleManage.vue'
/*会员*/
import memberList from './pages/member/memberList'
import memberAccount from './pages/member/memberAccount'
/*商户*/
import shopList from './pages/shop/shopList'
import shopAccount from './pages/shop/shopAccount'
import shopCommunicate from './pages/shop/shopCommunicate'
// 交易
import mmTransaction from './pages/deal/mmTransaction'
import msTransaction from './pages/deal/msTransaction'

//培训资源
import tagGroup from './pages/trainingResources/tagGroup'
import testTags from './pages/trainingResources/testTags'

import WorkRecord from './pages/manage/WorkRecord'

import LearningPlan from './pages/trainingResources/LearningPlan'
import LearningResource from './pages/trainingResources/LearningResource'
import PlanningTemplate from './pages/trainingResources/PlanningTemplate'
import ResourceTag from './pages/trainingResources/ResourceTag'
import LearningRecord from './pages/trainingResources/LearningRecord'
import TestQuestionManage from './pages/trainingResources/TestQuestionManage'
import TestQuestionTag from './pages/trainingResources/TestQuestionTag'
import VehicleManage from './pages/vehicleEquipment/VehicleManage'
import DeviceManage from './pages/vehicleEquipment/DeviceManage'
import SimManage from './pages/vehicleEquipment/SimManage'
import SystemConfig from './pages/systemConfig/SystemConfig'

export default {
    Admin:[
        {
            path: 'manage/:menu_uuid',
            component: StaffMange,
            meta: {
                rbac: 'logins',
                title: '员工管理'
            }
        },
        {
            path: 'manage_role/:menu_uuid',
            component: RoleManage,
            meta: {
                rbac: 'logins',
                title: '角色管理'
            }
        },
        // 会员业务
        {
            path: 'work/:menu_uuid',
            component: WorkRecord,
            meta: {
                rbac: 'logins',
                title: '工作记录'
            }
        },
        {
            path: 'member_list/:menu_uuid',
            component: memberList,
            meta: {
                rbac: 'logins',
                title: '会员管理'
            }
        },
        {
            path: 'member_account/:menu_uuid',
            component: memberAccount,
            meta: {
                rbac: 'logins',
                title: '会员资金账户'
            }
        },
        {
            path: 'plan/:menu_uuid',
            component: LearningPlan,
            meta: {
                rbac: 'logins',
                title: '学习计划'
            }
        },
        {
            path: 'resource/:menu_uuid',
            component: LearningResource,
            meta: {
                rbac: 'logins',
                title: '学习资源'
            }
        },
        {
            path: 'plan_template/:menu_uuid',
            component: PlanningTemplate,
            meta: {
                rbac: 'logins',
                title: '计划模板'
            }
        },
        {
            path: 'resource_tags/:menu_uuid',
            component: ResourceTag,
            meta: {
                rbac: 'logins',
                title: '资源标签'
            }
        },
        {
            path: 'learn_record/:menu_uuid',
            component: LearningRecord,
            meta: {
                rbac: 'logins',
                title: '学习记录'
            }
        },
        {
            path: 'question/:menu_uuid',
            component: TestQuestionManage,
            meta: {
                rbac: 'logins',
                title: '试题管理'
            }
        },

        {
            path: 'vehicle/:menu_uuid',
            component: VehicleManage,
            meta: {
                rbac: 'logins',
                title: '车辆管理'
            }
        },
        {
            path: 'device/:menu_uuid',
            component: DeviceManage,
            meta: {
                rbac: 'logins',
                title: '设备管理'
            }
        },
        {
            path: 'sim/:menu_uuid',
            component: SimManage,
            meta: {
                rbac: 'logins',
                title: 'sim卡管理'
            }
        },
        {
            path: 'setting/:menu_uuid',
            component: SystemConfig,
            meta: {
                rbac: 'logins',
                title: '学习配置'
            }

        },
        // 商户业务
        {
            path: 'shop_list/:menu_uuid',
            component: shopList,
            meta: {
                rbac: 'logins',
                title: '商户管理'
            }
        },
        {
            path: 'shop_account/:menu_uuid',
            component: shopAccount,
            meta: {
                rbac: 'logins',
                title: '商户资金账户'
            }
        },
        {
            path: 'Communicate/:menu_uuid',
            component: shopCommunicate,
            meta: {
                rbac: 'logins',
                title: '与商户沟通记录'
            }
        },
        // 交易记录
        {
            path: 'mm_transaction/:menu_uuid',
            component: mmTransaction,
            meta: {
                rbac: 'logins',
                title: '会员与平台交易'
            }
        },
        {
            path: 'ms_transaction/:menu_uuid',
            component: msTransaction,
            meta: {
                rbac: 'logins',
                title: '平台与商户交易'
            }
        },
        //培训资源
        {
            path: 'quest_group/:menu_uuid',
            component: tagGroup,
            meta: {
                rbac: 'logins',
                title: '标签分组'
            }
        },
        {
            path: 'question_tags/:menu_uuid',
            component: testTags,
            meta: {
                rbac: 'logins',
                title: '试题标签'
            }
        },
    ]
}