/*
系统配置
*/
<template>
    <div class="system">
        <!-- 菜单操作 -->
        <div style="height: 46px; line-height: 46px; overflow: hidden;">
            <el-row>
                <el-col :span="6">
                    <div style="padding-left:16px;">
                        <i class="el-icon-s-unfold"></i>
                        <span style="padding-left:9px;">
                            {{$store.state.AdminData.active_title}}
                        </span>
                    </div>
                </el-col>

                <el-col :span="18">
                    <div style="text-align: right; ">
                        <el-link @click="onSubMenu('onRefresh',true)" class="menu">刷新</el-link>
                        <el-link @click="onSubMenu('onSearch',true)" class="menu">搜索</el-link>

                        <el-link
                            class="menu" 
                            @click="onSubMenu(item)"
                            v-for="(item,index) in $store.state.AdminData.right_menus" 
                            :key="index">
                            {{item.name}}
                        </el-link>
                    </div>
                </el-col>
            </el-row>
        </div>
        <!-- 系统配置列表 -->
        <div style="border-top: solid 1px #f2f1f4;">
            <el-table 
                :data="rows"
                :height="height - 60 - 46 - 48"
                v-loading="loading"
                element-loading-text="拼命加载中"
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                @sort-change="onSortChange"
                :highlight-current-row="true"
                @current-change="onSelectRow"
                style="width: 100%" 
                size="mini">
                
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="real_name" label="姓名"></el-table-column>
                <el-table-column prop="mobile" label="登录手机"></el-table-column>
                <el-table-column prop="role_name" label="角色">
                    <template slot-scope="scope">
                        {{scope.row.role_name == '' ? '未设置' : scope.row.role_name}}
                    </template>
                </el-table-column>
                <el-table-column prop="add_time" label="添加时间" :sortable=true></el-table-column>
                <el-table-column 
                    prop="last_time" 
                    label="最近登录时间" 
                    align="center"
                    :sortable=true>
                    <template slot-scope="scope">
                        {{scope.row.last_time == 0 ? '还未登陆' : scope.row.last_time}}
                    </template>
                </el-table-column>
                <el-table-column prop="last_ip" label="最近登录IP" align="center">
                    <template slot-scope="scope">
                        {{scope.row.last_ip == 0 ? '-' : scope.row.last_ip}}
                    </template>
                </el-table-column>
            </el-table>
            
            <div class="page" :style="{width:width - 250 + 'px'}">
                <el-pagination
                    :current-page.sync="SearchFormData.page_num"
                    @current-change="onPageChange"
                    layout="prev, pager, next, jumper"
                    :total="total">
                </el-pagination>
            </div>
        </div>

        <!-- 菜单操作弹框 -->
        <!-- 搜索 -->
        <el-dialog
            title="提示"
            :visible.sync="search_show"
            width=400px>
            <el-form :model="SearchFormData" label-width="80px" size="small">
                <el-form-item label="配置名称:">
                    <el-input v-model="SearchFormData.name"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="search_show = false" size="small">取 消</el-button>
                <el-button type="primary" @click="onSearchSubmit" size="small">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 添加模板 -->
        <el-dialog
            title="添加"
            :visible.sync="add_show"
            width="500px">
            <el-form :model="AddFormData" :rules="AddRules" label-width="100px" size="small">
                <el-form-item label="配置名称：" prop="name">
                    <el-input v-model="AddFormData.name" />
                </el-form-item>

                <el-form-item label="配置标识：" prop="ename">
                    <el-input v-model="AddFormData.ename" />
                </el-form-item>

                <el-form-item label="配置数据：" prop="set_config">
                    <el-input v-model="AddFormData.set_config" />
                </el-form-item>

                <el-form-item label="配置描述：" prop="remark">
                    <el-input v-model="AddFormData.remark" />
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="add_show = false" size="small">取消</el-button>
                <el-button @click="onAddSubmit" type="primary" size="small">确定</el-button>
            </span>
        </el-dialog>

        <!-- 编辑模板 -->
        <el-dialog  
            title="编辑"
            :visible.sync="edit_show"
            width="500px">
            <el-form :model="EditFormData" label-width="80px" size="small">
                <el-form-item label="配置名称:">
                    <el-input v-model="EditFormData.name" />
                </el-form-item>

                <el-form-item label="配置清单:">
                    <el-input v-model="EditFormData.set_config" />
                </el-form-item>

                <el-form-item label="配置清单说明:">
                    <el-input v-model="EditFormData.remark" />
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="edit_show = false" size="small">取消</el-button>
                <el-button @click="onEditSubmit" type="primary" size="small">确定</el-button>
            </span>
        </el-dialog>

        <!-- 设置角色 -->
         <el-dialog 
            title="设置角色"
            width="450px"
            :visible.sync="role_show">
            <el-form :model="RoleFormData" label-width="80px" size="small">
                <el-form-item label="拥有角色:">
                    <el-cascader 
                        clearable 
                        :options="role_rows"
                        :props="{checkStrictly:true,expandTrigger: 'hover',value:'uuid', label:'name',emitPath:false}"
                        placeholder="不选择,无权限"
                        v-model="RoleFormData.role_uuid">
                    </el-cascader>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="role_show = false" size="small">取消</el-button>
                <el-button type="primary" @click="onRoleSubmit" size="small">确认</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { cookie_get } from "../../_commom/connectorHttp/systemConfigHttp.js";
import { ManageSetList } from "../../_commom/connectorHttp/systemConfigHttp.js";
import { ManageSetAdd } from "../../_commom/connectorHttp/systemConfigHttp.js";
import { ManageSetDel } from "../../_commom/connectorHttp/systemConfigHttp.js";

import store from "@/store";
import util from "@/util.js";
export default {
    data () {
        return {
            /*系统配置列表*/ 
            rows:[],
            total:0,
            loading:false,
            curr_row:null,
            SearchFormData:{
                name:'',//名称
                ename: '',//标识
                page_num:1,//当前页码
                page_len:10,//每页条数
                order_field:'add_time',//排序
                order_sort:'desc'//排序类型
            },

            /*菜单操作*/ 
            // 搜素
            search_show:false,

            // 添加
            add_show:false,
            AddFormData:{
                name: '',
                ename: '',
                set_config: '',
                remark: '',
            },
            AddRules: {
                name: [
                    { required: true, message: '请输入配置名称', trigger: 'blur' },
                ],
                ename: [
                    { required: true, message: '请输入配置标识', trigger: 'blur' },
                ],
                set_config: [
                    { required: true, message: '请输入配置数据', trigger: 'blur' },
                ],
            },

            // 编辑
            edit_show:false,
            EditFormData:{
                uuid: '',//唯一值
                name:'',//配置名称
                set_config: '',//配置清单
                remark: ''//配置清单说明
            },

            //设置角色
            role_show:false,
            role_rows:[],
            RoleFormData:{
                manage_uuid:'',
                role_uuid:'',
            },
        }
    },
    mounted () {
        this.init();
    },
    computed:{
        width:() => {
            return store.state.AppData.width;
        },
        height:() => {
            return store.state.AppData.height;
        },
        upload_url:() => {
            return store.state.AppData.upload_url;
        },
        img_host:() => {
            return store.state.AppData.img_host;
        }
    },
    methods: {
        // 按钮点击 menu:参数数据 local是否本地程序
        onSubMenu(menu, local = false) {
            util.submenu(menu,this,cookie_get (), local);
        },
        /*系统配置列表方法*/ 
        //系统配置初始化
        init () {
            this.loading = true;
            var dataList = {
                login_token: cookie_get(),
                name: this.SearchFormData.name,
                ename: this.SearchFormData.ename,
                page_num: this.SearchFormData.page_num,
                page_len: this.SearchFormData.page_len,
                order_field: this.SearchFormData.order_field,
                order_sort: this.SearchFormData.order_sort
            }
            ManageSetList (dataList).then(res => {
                if (res.code == 0) {
                    this.loading = false;
                    this.rows = res.data.rows;
                    this.total = res.data.total;
                } else {
                    this.$message.error(res.msg);
                }
            });
            // 超时关闭遮罩层
            setTimeout(() => {
                this.loading = false;
            }, 10000);
        },
        //排序处理
        onSortChange () {
            this.SearchFormData.order_field = sort.prop;
            if (sort.order == 'ascending') {
                this.SearchFormData.order_sort  = 'asc';
            } else {
                this.SearchFormData.order_sort  = 'desc';
            }
            this.init();
        },
        //选择表格行
        onSelectRow (row) {
            this.curr_row = row;
        },
        //分页处理
        onPageChange (page) {
            this.SearchFormData.page_num = page;
            this.init();
        },

        /*菜单操作*/
        //刷新
        onRefresh() {
            this.init();
        },
        //搜索显示弹框
        onSearch () {
            this.search_show = true;
        },
        //搜索弹框提交
        onSearchSubmit () {
            this.SearchFormData.page_num = 1;
            this.init();
            this.search_show = false;
            this.SearchFormData.name = '';
        },
        //添加显示
        handleAdd() {
            this.add_show = true;
        },
        //添加模板提交
        onAddSubmit () {
            this.AddFormData.login_token = cookie_get();
            console.log(this.AddFormData)
            ManageSetAdd (this.AddFormData).then(res => {
                if (res.code == 0) {
                    this.$message.success('添加成功');
                } else {
                    this.$message.error(res.msg);
                }
                this.SearchFormData.page_num = 1;
                this.init();
                this.add_show = false;
                this.AddFormData.name = '';
                this.AddFormData.ename = '';
                this.AddFormData.set_config = '';
                this.AddFormData.remark = '';
            }).catch(err => {
                this.$message.error(err.msg);
            })
        },
        //编辑显示
        handleEdit () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            this.EditFormData.name = this.curr_row.name;
            this.EditFormData.set_config = this.curr_row.set_config;
            this.EditFormData.remark = this.curr_row.remark;
            this.edit_show = true;
        },
        //编辑模板提交
        onEditSubmit () {
            this.EditFormData.login_token = cookie_get();
            this.EditFormData.uuid = this.curr_row.uuid;
            console.log(this.EditFormData)
            ManageEdit (this.EditFormData).then(res => {
                this.init();
                this.edit_show = false;
                this.EditFormData.name = '';
                this.EditFormData.set_config = '';
                this.EditFormData.remark = '';
            }).catch(err => {
                this.$message.error(err.msg);
            });
        },
        //设置角色
        handleSetRole () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            
            // 获取当前登录账号拥有的角色
            ManageSetRole ({
                module:'ManageSetRole',
                ver:'1.0.0',
                relation_module:'ManageRoleList',
                relation_ver:'1.0.0',
                login_token:cookie_get(),
            }).then(res => {
                this.role_rows = util.toTree(res.data);
                this.role_show = true;
            })
        },
        //角色模板提交
        onRoleSubmit () {
            ManageSetRole ({
                login_token:cookie_get(),
                manage_uuid:this.curr_row.uuid,
                role_uuid:this.RoleFormData.role_uuid
            }).then(res =>{
                // this.init,
                this.role_show=false
            }).catch(err => {
                this.$message.error(err.msg)
            })
        },
        //删除提交
        handleDel(menu) {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }

            this.$confirm('确认删除?', '提示').then(() => {
                ManageSetDel({
                    login_token:cookie_get(),
                    uuid:this.curr_row.uuid
                }).then(res => {
                    if (res.code == 0) {
                        this.$message.success('操作成功');
                    } else {
                        this.$message.error(res.msg);
                    }
                    this.init();
                }).catch(err => {
                    this.$message.error(err.msg);
                })
            })
        },
    }
}
</script>

<style lang="less" scoped>
    .system{
        .menu{
            display: inline-block;
            padding:0 16px;
            text-align: center;
        }
        .page {
            height: 40px; 
            line-height: 40px; 
            text-align: right;
            position: fixed;
            bottom: 0;
            right:0;
            overflow: hidden;
        }
    }
</style>