/*
车辆管理
*/
<template>
    <div class="vehicle">
        <!-- 菜单操作 -->
        <div style="height: 46px; line-height: 46px; overflow: hidden;">
            <el-row>
                <el-col :span="6">
                    <div style="padding-left:16px;">
                        <i class="el-icon-s-unfold"></i>
                        <span style="padding-left:9px;">
                            {{$store.state.AdminData.active_title}}
                        </span>
                    </div>
                </el-col>

                <el-col :span="18">
                    <div style="text-align: right; ">
                        <el-link @click="onSubMenu('onRefresh',true)" class="menu">刷新</el-link>
                        <el-link @click="onSubMenu('onSearch',true)" class="menu">搜索</el-link>

                        <el-link
                            class="menu" 
                            @click="onSubMenu(item)"
                            v-for="(item,index) in $store.state.AdminData.right_menus" 
                            :key="index">
                            {{item.name}}
                        </el-link>
                    </div>
                </el-col>
            </el-row>
        </div>
        <!-- 车辆管理列表 -->
        <div style="border-top: solid 1px #f2f1f4;">
            <el-table 
                :data="rows"
                :height="height - 60 - 46 - 48"
                v-loading="loading"
                element-loading-text="拼命加载中"
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                @sort-change="onSortChange"
                :highlight-current-row="true"
                @current-change="onSelectRow"
                style="width: 100%" 
                size="mini">
                
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="shop_name.cname" label="商户名称"></el-table-column>
                <el-table-column prop="vehicle_firm" label="车辆厂商"></el-table-column>
                <el-table-column prop="vehicle_vin" label="车架号"></el-table-column>
                <el-table-column prop="vehicle_owner" label="车主"></el-table-column>
                <el-table-column prop="owner_tel" label="车主手机"></el-table-column>
                <el-table-column prop="market" label="责任人"></el-table-column>
                <el-table-column prop="vehicle_type" label="车辆类型"></el-table-column>
                <el-table-column prop="vehicle_weight" label="总质量(kg)"></el-table-column>
                <el-table-column prop="transfer_weight" label="准牵引总质量(kg)"></el-table-column>
                <el-table-column prop="outline_long" label="外廓尺寸（mm）长"></el-table-column>
                <el-table-column prop="outline_wide" label="外廓尺寸（mm）宽"></el-table-column>
                <el-table-column prop="outline_high" label="外轮廓高"></el-table-column>
                <el-table-column prop="inline_long" label="货厢内尺寸（mm）长"></el-table-column>
                <el-table-column prop="inline_wide" label="货厢内尺寸（mm）宽"></el-table-column>
                <el-table-column prop="inline_high" label="货厢内尺寸（mm）高"></el-table-column>
                <el-table-column prop="axle_num" label="轴数"></el-table-column>
                <el-table-column prop="tyre_num" label="轮胎数"></el-table-column>
                <el-table-column prop="tyre_specs" label="轮胎规格"></el-table-column>
                <el-table-column prop="vehicle_delivery_time" label="车辆出厂时间"></el-table-column>
                <el-table-column prop="business_scope" label="经营范围"></el-table-column>
                <el-table-column prop="vehicle_color" label="车身颜色"></el-table-column>
                <!-- <el-table-column prop="pay_type" label="车身颜色"></el-table-column> -->
                <el-table-column prop="pay_type" label="购置方式"></el-table-column>
                <!-- <el-table-column prop="insurance_buy_time" label="车身颜色"></el-table-column> -->
                <el-table-column prop="insurance_buy_time" label="车辆保险购买日期"></el-table-column>
                <el-table-column prop="insurance_expire_time" label="车辆保险到期日期"></el-table-column>
                <el-table-column prop="vehicle_checkout_time" label="车辆年检日期"></el-table-column>
                <el-table-column prop="payment_time" label="服务缴费日期"></el-table-column>
                <el-table-column prop="payment_expire_time" label="服务费期限"></el-table-column>
                <el-table-column prop="road_business_license" label="道路运输经营许可证"></el-table-column>
                <el-table-column prop="business_type" label="车辆年检日期"></el-table-column>
                <el-table-column prop="inline_type" label="运输类别"></el-table-column>
                <el-table-column prop="vehicle_checkout_time" label="入网类型"></el-table-column>
                <el-table-column prop="add_time" label="添加时间"></el-table-column>
            </el-table>
            
            <div class="page" :style="{width:width - 250 + 'px'}">
                <el-pagination
                    :current-page.sync="SearchFormData.page_num"
                    @current-change="onPageChange"
                    layout="prev, pager, next, jumper"
                    :total="total">
                </el-pagination>
            </div>
        </div>
        <!-- 菜单操作弹框 -->
        <!-- 搜索弹框 -->
        <el-dialog
            title="搜索"
            :visible.sync="search_show"
            width=400px>
            <el-form :model="SearchFormData" label-width="80px" size="small">
                <el-form-item label="商户名称">
                    <el-input v-model="SearchFormData.shop_name"/>
                </el-form-item>
                <el-form-item label="车架号">
                    <el-input v-model="SearchFormData.vehicle_vin"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="search_show = false" size="small">取 消</el-button>
                <el-button type="primary" @click="onSearchSubmit" size="small">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 编辑弹框 -->
        <el-dialog
            title="编辑"
            :visible.sync="edit_show"
            width=400px>
            <el-form :model="EditFormData" label-width="80px" size="small">
                <el-form-item label="标签名称" prop="tag_name">
                    <el-input v-model="EditFormData.tag_name"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="edit_show = false" size="small">取 消</el-button>
                <el-button type="primary" @click="onEditSubmit" size="small">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 添加弹框 -->
        <el-dialog
            title="添加"
            :visible.sync="add_show"
            width=400px>
            <el-form :model="AddFormData" :rules="AddFormRule" label-width="80px" size="small">
                <el-form-item label="标签名称" prop="tag_name">
                    <el-input v-model="AddFormData.tag_name"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="add_show = false" size="small">取 消</el-button>
                <el-button type="primary" @click="onAddSubmit" size="small">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 详情弹框 -->
        <!-- <el-dialog
            title="详情"
            :visible.sync="detail_show"
            width=750px>

            <span slot="footer">
                <el-button type="primary" @click="detail_show = false" size="small">确 定</el-button>
            </span>
        </el-dialog> -->
        <el-drawer
            :visible.sync="detail_show"
            size="40%"
            title="详情">
            <!-- <h3 class="drawer-title">详细</h3> -->
            <div class="drawer-body">
                <ul class="vehicle-ul">
                    <li>
                        <div><span>商户名称：</span><span>{{DetailFormData.shop_name != null || DetailFormData.shop_name != undefined ? DetailFormData.shop_name.cname : ''}}</span></div>
                        <div><span>车辆厂商：</span><span>{{DetailFormData.vehicle_firm}}</span></div>
                    </li>
                    <li>
                        <div><span>车架号：</span><span>{{DetailFormData.vehicle_vin}}</span></div>
                        <div><span>车主：</span><span>{{DetailFormData.vehicle_owner}}</span></div>
                    </li>
                    <li>
                        <div><span>车主手机：</span><span>{{DetailFormData.owner_tel}}</span></div>
                        <div><span>责任人：</span><span>{{DetailFormData.market}}</span></div>
                    </li>
                    <li>
                        <div><span>车辆类型：</span><span>{{DetailFormData.vehicle_type}}</span></div>
                        <div><span>总质量(kg)：</span><span>{{DetailFormData.vehicle_weight}}</span></div>
                    </li>
                    <li>
                        <div><span>准牵引总质量(kg)：</span><span>{{DetailFormData.transfer_weight}}</span></div>
                        <div><span>外廓尺寸(mm)长：</span><span>{{DetailFormData.outline_long}}</span></div>
                    </li>
                    <li>
                        <div><span>外廓尺寸(mm)宽：</span><span>{{DetailFormData.outline_wide}}</span></div>
                        <div><span>外轮廓高：</span><span>{{DetailFormData.outline_high}}</span></div>
                    </li>
                    <li>
                        <div><span>货厢内尺寸(mm)长：</span>{{DetailFormData.inline_long}}<span></span></div>
                        <div><span>货厢内尺寸(mm)宽：</span>{{DetailFormData.inline_wide}}<span></span></div>
                    </li>
                    <li>
                        <div><span>货厢内尺寸(mm)高：</span>{{DetailFormData.inline_high}}<span></span></div>
                        <div><span>轴数：</span><span>{{DetailFormData.axle_num}}</span></div>
                    </li>

                    <li>
                        <div><span>轮胎数：</span><span>{{DetailFormData.tyre_num}}</span></div>
                        <div><span>轮胎规格：</span><span>{{DetailFormData.tyre_specs}}</span></div>
                    </li>
                    <li>
                        <div><span>车辆出厂时间：</span><span>{{DetailFormData.vehicle_delivery_time}}</span></div>
                        <div><span>经营范围：</span><span>{{DetailFormData.business_scope}}</span></div>
                    </li>
                    <li>
                        <div><span>车身颜色：</span><span>{{DetailFormData.vehicle_color}}</span></div>
                        <div><span>购置方式：</span><span>{{DetailFormData.pay_type}}</span></div>
                    </li>
                    <li>
                        <div><span>车辆保险购买日期：</span><span>{{DetailFormData.insurance_buy_time}}</span></div>
                        <div><span>车辆保险到期日期：</span><span>{{DetailFormData.insurance_expire_time}}</span></div>
                    </li>
                    <li>
                        <div><span>车辆年检日期：</span><span>{{DetailFormData.vehicle_checkout_time}}</span></div>
                        <div><span>服务缴费日期：</span><span>{{DetailFormData.payment_time}}</span></div>
                    </li>
                    <li>
                        <div><span>服务费期限：</span><span>{{DetailFormData.payment_expire_time}}</span></div>
                        <div><span>道路运输经营许可证：</span><span>{{DetailFormData.road_business_license}}</span></div>
                    </li>

                    <li>
                        <div><span>车辆年检日期：</span><span>{{DetailFormData.business_type}}</span></div>
                        <div><span>运输类别：</span><span>{{DetailFormData.inline_type}}</span></div>
                    </li>
                    <li>
                        <div><span>入网类型：</span><span>{{DetailFormData.vehicle_checkout_time}}</span></div>
                        <div><span>添加时间：</span><span>{{DetailFormData.add_time}}</span></div>
                    </li>
                    
                </ul>
            </div>
            <div class="drawer-btn">
                <el-button type='danger' @click="detail_show = false" size="small" style="display: block;margin: 30px auto;">取消</el-button>
            </div>
        </el-drawer>
    </div>
</template>

<script>
import { cookie_get } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import { ManageVehExtendList } from "../../_commom/connectorHttp/vehicleEquipmentHttp.js";
import { ManageVehExtendDetail } from "../../_commom/connectorHttp/vehicleEquipmentHttp.js";

import store from "@/store";
import util from "@/util.js";
export default {
    data () {
        return {
            // 车辆管理数据初始化
            rows:[],
            total:0,
            loading:false,
            curr_row:null,
            // 菜单操作
            // 搜索
            search_show: false,
            SearchFormData: {
                shop_name: '',//商户名称
                vehicle_vin: '',//车架号
                page_num: 1,//当前页 默认第一页
                page_len: 10,//每页数据条数
                order_field:'add_time',//时间排序
                order_sort:'desc'//排序规则
            },
            // 编辑
            edit_show: false,
            EditFormData: {
                tag_name: '',
            },
            // 添加
            add_show: false,
            AddFormData: {
                tag_name: ''
            },
            AddFormRule: {
                tag_name: [
                    { required: true, message: '请输入标签名称', trigger: 'blur' },
                ]
            },
            // 详情
            detail_show: false,
            DetailFormData: {}
        }
    },
    created () {
        this.init();
    },
    computed:{
        width:() => {
            return store.state.AppData.width;
        },
        height:() => {
            return store.state.AppData.height;
        },
        upload_url:() => {
            return store.state.AppData.upload_url;
        },
        img_host:() => {
            return store.state.AppData.img_host;
        }
    },
    methods: {
        // 按钮点击 menu:参数数据 local是否本地程序
        onSubMenu(menu, local = false) {
            util.submenu(menu,this,cookie_get (), local);
        },

        // 车辆管理列表方法
        // 获取车辆管理列表数据方法 init
        init () {
            this.loading = true;
            var dataList = {
                shop_name: this.SearchFormData.shop_name,
                vehicle_vin: this.SearchFormData.vehicle_vin,
                page_num: this.SearchFormData.page_num,//当前页 默认第一页
                page_len: this.SearchFormData.page_len,//每页数据条数
                order_field: this.SearchFormData.order_field,//时间排序
                order_sort: this.SearchFormData.order_sort,//排序规则
                login_token: cookie_get()
            }
            ManageVehExtendList (dataList).then(res => {
                if (res.code == 0) {
                    this.rows = res.data.rows;
                    this.total = res.data.total;
                } else {
                    this.$message.error(res.msg);
                }
                this.loading = false;
            })
            // 超时关闭遮罩层
            setTimeout(() => {
                this.loading = false;
            }, 10000);
        },
        // 排序
        onSortChange (column) {
            this.SearchFormData.order_field = column.prop;
            if (column.order == 'ascending') {
                this.SearchFormData.order_sort  = 'asc';
            } else {
                this.SearchFormData.order_sort  = 'desc';
            }
            this.init();
        },
        // 选择行
        onSelectRow (row) {
            this.curr_row = row;
        },
        // 分页
        onPageChange (page) {
            this.SearchFormData.page_no = page;
            this.init();
        },

        // 菜单操作方法
        // 刷新
        onRefresh () {
            this.init();
        },
        // 搜索弹框显示
        onSearch () {
            this.search_show = true;
        },
        // 搜索弹框提交
        onSearchSubmit () {
            this.SearchFormData.page_num = 1;
            this.init();
            this.search_show = false;
            this.SearchFormData.shop_name = '';
            this.SearchFormData.vehicle_vin = '';
        },
        // 编辑弹框显示
        handleEdit () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            this.EditFormData.tag_name = this.curr_row.tag_name;
            this.edit_show = true;
        },
        // 编辑弹框提交
        onEditSubmit () {
            var EditData = {
                tag_name: this.EditFormData.tag_name,
                uuid: this.curr_row.uuid,
                login_token: cookie_get()
            }
            ManageResourceTagEdit (EditData).then(res => {
                if (res.code == 0) {
                    this.init();
                } else {
                    this.$message.error(res.msg);
                }
                this.edit_show = false;
            })
        },
        // 添加弹框显示
        handleAdd () {
            this.add_show = true;
        },
        // 添加弹框提交
        onAddSubmit () {
            var AddData = {
                tag_name: this.AddFormData.tag_name,
                login_token: cookie_get()
            }
            ManageResourceTagAdd (AddData).then(res => {
                if (res.code == 0) {
                    this.$message.success('编辑成功');
                    this.init();
                } else {
                    this.$message.error(res.msg);
                }
                this.add_show = false;
            })
        },
        // 删除
        handleDel () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            this.$confirm('确定删除？','提示').then(() => {
                var DelData = {
                    uuid: this.curr_row.uuid,
                    login_token: cookie_get()
                }
                ManageResourceTagDel (DelData).then(res => {
                    if (res.code == 0) {
                        this.$message.success('删除成功');
                        this.init();
                    } else {
                        this.$message.error(res.msg);
                    }
                })
            })
        },
        // 详情弹框显示
        handleDetail () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            var DetailData = {
                uuid: this.curr_row.uuid,
                login_token: cookie_get()
            }
            ManageVehExtendDetail (DetailData).then(res => {
                this.DetailFormData = res.data;
                this.detail_show = true;
            })
        }
        
    }

}
</script>

<style lang="less">
    .vehicle{
        .menu{
            display: inline-block;
            padding:0 16px;
            text-align: center;
        }
        .page {
            height: 40px; 
            line-height: 40px; 
            text-align: right;
            position: fixed;
            bottom: 0;
            right:0;
            overflow: hidden;
        }
        .drawer-body{
            ul{
                list-style: none;
                li{
                    overflow: hidden;
                    div:nth-of-type(1){
                        float: left;
                        margin-left: 30px;
                    }
                    div:nth-of-type(2){
                        float: right;
                        margin-right: 30px;
                    }
                    div{
                        span:nth-of-type(1){
                            width: 130px;
                            text-align: right;
                            display: inline-block;
                            font-size: 14px;
                        }
                        span:nth-of-type(2){
                            width: 200px;
                            text-align: left;
                            display: inline-block;
                            font-size: 14px;
                        }
                    }
                }
            }
        }
        .el-drawer__body{
            overflow: auto !important;
        }
    }
</style>