/*
学习资源
*/
<template>
    <div class="learn">
        <!-- 菜单操作 -->
        <div style="height: 46px; line-height: 46px; overflow: hidden;">
            <el-row>
                <el-col :span="6">
                    <div style="padding-left:16px;">
                        <i class="el-icon-s-unfold"></i>
                        <span style="padding-left:9px;">
                            {{$store.state.AdminData.active_title}}
                        </span>
                    </div>
                </el-col>

                <el-col :span="18">
                    <div style="text-align: right; ">
                        <el-link @click="onSubMenu('onRefresh',true)" class="menu">刷新</el-link>
                        <el-link @click="onSubMenu('onSearch',true)" class="menu">搜索</el-link>
                        <el-link
                            class="menu" 
                            @click="onSubMenu(item)"
                            v-for="(item,index) in $store.state.AdminData.right_menus" 
                            :key="index">
                            {{item.name}}
                        </el-link>
                    </div>
                </el-col>
            </el-row>
        </div>
        <!-- 学习资源列表 -->
        <div style="border-top: solid 1px #f2f1f4;">
            <el-table 
                :data="rows"
                :height="height - 60 - 46 - 48"
                v-loading="loading"
                element-loading-text="拼命加载中"
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                @sort-change="onSortChange"
                :highlight-current-row="true"
                @current-change="onSelectRow"
                style="width: 100%"
                size="mini">
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="title" label="标题" width="300"></el-table-column>
                <el-table-column prop="cont_type" label="类型" align="center"></el-table-column>
                <el-table-column prop="look_num" label="查看次数" align="center"></el-table-column>
                <el-table-column prop="from_type" label="类别" align="center"></el-table-column>
                <el-table-column prop="state" label="状态" align="center"></el-table-column>
                <el-table-column prop="is_pay" label="收费" align="center"></el-table-column>
                <el-table-column prop="min_num" label="学币最小数" align="center"></el-table-column>
                <el-table-column prop="add_time" label="添加时间" sortable align="center"></el-table-column>
                <el-table-column prop="last_time" label="最后更新时间" sortable align="center"></el-table-column>
            </el-table>
            
            <div class="page" :style="{width:width - 250 + 'px'}">
                <el-pagination
                    :current-page.sync="SearchFormData.page_num"
                    @current-change="onPageChange"
                    layout="prev, pager, next, jumper"
                    :total="total">
                </el-pagination>
            </div>
        </div>

        <!-- 菜单操作弹框 -->
        <!-- 搜索 -->
        <el-dialog
            title="搜索"
            :visible.sync="search_show"
            width="400px">
            <el-form :model="SearchFormData" label-width="70px" size="small">
                <el-form-item label="标题:">
                    <el-input v-model="SearchFormData.title" style="width: 280px;"/>
                </el-form-item>
                <el-form-item label="类型:">
                    <el-select v-model="SearchFormData.cont_type" style="width: 280px;">
                        <el-option
                            v-for="item in options_type"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <!-- <el-form-item label="类别:">
                    <el-select v-model="SearchFormData.from_type" style="width: 280px;">
                        <el-option
                            v-for="item in options_fromType"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">

                        </el-option>
                    </el-select>
                </el-form-item> -->
                <!-- <el-form-item label="状态:">
                    <el-select v-model="SearchFormData.state" style="width: 280px;">
                        <el-option
                            v-for="item in options_state"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">

                        </el-option>
                    </el-select>
                </el-form-item> -->
            </el-form>

            <span slot="footer">
                <el-button @click="search_show = false" size="small">取 消</el-button>
                <el-button type="primary" @click="onSearchSubmit" size="small">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 添加标签 -->
        <el-dialog
            title="标签列表"
            :visible.sync="add_tag_show"
            width="960px"
            class="tag-table"
        >
            <el-table
                :data="tag_rows"
                :height="height - 60 - 46 - 48"
                v-loading="loadingTag"
                element-loading-spinner="el-icon-loading"
                element-loading-text="拼命加载中"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                :highlight-current-row="true"
                @sort-change="onTagSortChange"
                @current-change="onTagSelectRow"
                style="width: 100%"
                size="mini">
                
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="from_type" label="部门类型"></el-table-column>
                <el-table-column prop="tag_name" label="标签名称"></el-table-column>
                <el-table-column prop="add_time" label="添加时间" :formatter="common.formatDate" sortable></el-table-column>
                <el-table-column prop="add_time" label="结束时间" :formatter="common.formatDate" sortable></el-table-column>
            </el-table>
            <div class="page-tag" :style="{width:width - 250 + 'px'}">
                <el-pagination
                    :current-page.sync="TagSearchFromData.page_no"
                    @current-change="onTagPageChange"
                    layout="prev, pager, next"
                    :total="tag_total">
                </el-pagination>
            </div>
            <span slot="footer">
                <el-button @click="add_tag_show = false" size="small">取消</el-button>
                <el-button @click="onTagAddSubmit" type="primary" size="small">确定</el-button>
            </span>
        </el-dialog>
        <!-- 编辑模板 -->
        <el-drawer  
            title="编辑"
            :visible.sync="edit_show"
            size="40%">
            <div style="padding-right: 10px;position: relative;">
                <el-form ref="EditFormData" :model="EditFormData" :rules="addRules" label-width="120px" size="small" style="height: calc(100vh - 130px);overflow-y: auto;">
                    <el-form-item label="标题：" prop="title">
                        <el-input v-model="EditFormData.title"/>
                    </el-form-item>
                    <el-form-item label="类型：" prop="cont_type">
                        <el-radio-group v-model="EditFormData.cont_type" @change="onContype">
                            <el-radio :label="1">视频</el-radio>
                            <el-radio :label="2">图文</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item v-if="EditFormData.cont_type == 1" label="是否加密：" prop="encrypt">
                        <div>
                            <el-radio-group v-model="EditFormData.encrypt">
                                <el-radio :label="0">不加密</el-radio>
                                <el-radio :label="1">加密</el-radio>
                            </el-radio-group>
                        </div>
                    </el-form-item>
                    <el-form-item label="上传文件：">
                        <div v-if="EditFormData.cont_type == 1">
                            <videoUpload @func="getVideoUrl" :isEncrypt="EditFormData.encrypt" @isShowVideo1="isShowVideoFn1" @isShowVideo2="isShowVideoFn2"></videoUpload>
                            <div v-if="videoPlayer3" class="prism-player" id="J_prismPlayer" style="width: 300px;height: 300px;"></div>
                            <video v-if="videoPlayer4" width="320" height="240" controls>
                                <source :src="videoUrl" type="video/mp4">
                                <source :src="videoUrl" type="video/ogg">
                                您的浏览器不支持Video标签。
                            </video>
                        </div>
                        <div v-else>
                            <el-upload
                                class="upload-demo"
                                action="string"
                                :http-request="UploadImage"
                                list-type="picture">
                                <el-button size="small" type="primary" :disabled="EditFormData.cont_type == 2 ? false : true">上传图片</el-button>
                                <!-- <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div> -->
                            </el-upload>
                        </div>
                    </el-form-item>
                    <el-form-item label="封面：">
                        <el-upload
                            class="upload-demo"
                            action="string"
                            :http-request="UploadImage"
                            list-type="picture">
                            <el-button size="small" type="primary">点击上传</el-button>
                            <!-- <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div> -->
                        </el-upload>
                    </el-form-item>
                    <!-- <el-form-item label="是否加密：" prop="encrypt">
                        <el-switch v-model="AddFormData.encrypt">
                        </el-switch>
                    </el-form-item> -->
                    <el-form-item label="是否展示：" prop="is_show">
                        <el-switch v-model="EditFormData.is_show">
                        </el-switch>
                    </el-form-item>
                    <el-form-item label="支付：" prop="is_pay" style="overflow: hidden;">
                        <el-radio-group v-model="EditFormData.is_pay" @change="onPayChange" style="float: left;margin-top: 10px;">
                            <el-radio :label="0">免费</el-radio>
                            <el-radio :label="1">收费</el-radio>
                        </el-radio-group>
                        <div style="float: left;margin-left: 20px;" v-if="studyCar">学币：<el-input type="number" v-model="EditFormData.min_num" style="width: 100px;"></el-input></div>
                    </el-form-item>
                    <el-form-item label="标签：" prop="tags">
                        <!-- <el-button @click="addTags" type="primary">添加标签</el-button>
                        <el-input v-model="addTagsInputList" placeholder="展示添加的标签"/> -->
                        <el-select
                            v-model="value11"
                            multiple
                            filterable
                            allow-create
                            default-first-option
                            placeholder="请选择文章标签">
                            <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="时长(秒)：" prop="content_time">
                        <el-input type="number" v-model="EditFormData.content_time"></el-input>
                    </el-form-item>
                    <el-form-item v-if="EditFormData.cont_type == 2" label="内容：" prop="content">
                        <el-input type="textarea" v-model="EditFormData.content"></el-input>
                    </el-form-item>
                    <el-form-item label="备注：" prop="remark">
                        <el-input type="textarea" v-model="EditFormData.remark"></el-input>
                    </el-form-item>
                </el-form>
                <span slot="footer" style="display: inline-block;position: fixed;bottom: 20px;right: 16%;">
                    <el-button @click="edit_show = false" size="small">取消</el-button>
                    <el-button @click="onEditSubmit('EditFormData')" type="primary" size="small">确定</el-button>
                </span>
            </div>
        </el-drawer>

        <!-- 添加模板 -->
        <el-drawer  
            title="添加"
            :visible.sync="add_show"
            size="40%">
            <div style="padding-right: 10px;position: relative;">
                <el-form ref="AddFormData" :model="AddFormData" :rules="addRules" label-width="120px" size="small" style="height: calc(100vh - 130px);overflow-y: auto;">
                    <el-form-item label="标题：" prop="title">
                        <el-input v-model="AddFormData.title"/>
                    </el-form-item>
                    <el-form-item label="类型：" prop="cont_type">
                        <el-radio-group v-model="AddFormData.cont_type" @change="onContype">
                            <el-radio :label="1">视频</el-radio>
                            <el-radio :label="2">图文</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item v-if="AddFormData.cont_type == 1" label="是否加密：" prop="encrypt">
                        <div>
                            <el-radio-group v-model="AddFormData.encrypt" @change="onEncrypt">
                                <el-radio :label="0">不加密</el-radio>
                                <el-radio :label="1">加密</el-radio>
                            </el-radio-group>
                        </div>
                    </el-form-item>
                    <el-form-item label="上传文件：">
                        <div v-if="AddFormData.cont_type == 1">
                            <videoUpload @func="getVideoUrl" :isEncrypt="AddFormData.encrypt"></videoUpload>
                        </div>
                        <div v-else>
                            <el-upload
                                class="upload-demo"
                                action="string"
                                :http-request="UploadImage"
                                list-type="picture">
                                <el-button size="small" type="primary" :disabled="AddFormData.cont_type == 2 ? false : true">上传图片</el-button>
                                <!-- <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div> -->
                            </el-upload>
                        </div>
                    </el-form-item>
                    <el-form-item label="封面：">
                        <el-upload
                            class="upload-demo"
                            action="string"
                            :http-request="UploadImage"
                            list-type="picture">
                            <el-button size="small" type="primary">点击上传</el-button>
                            <!-- <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div> -->
                        </el-upload>
                    </el-form-item>
                    <!-- <el-form-item label="是否加密：" prop="encrypt">
                        <el-switch v-model="AddFormData.encrypt">
                        </el-switch>
                    </el-form-item> -->
                    <el-form-item label="是否展示：" prop="is_show">
                        <el-switch v-model="AddFormData.is_show">
                        </el-switch>
                    </el-form-item>
                    <el-form-item label="支付：" prop="is_pay" style="overflow: hidden;">
                        <el-radio-group v-model="AddFormData.is_pay" @change="onPayChange" style="float: left;margin-top: 10px;">
                            <el-radio :label="0">免费</el-radio>
                            <el-radio :label="1">收费</el-radio>
                        </el-radio-group>
                        <div style="float: left;margin-left: 20px;" v-if="studyCar">学币：<el-input type="number" v-model="AddFormData.min_num" style="width: 100px;"></el-input></div>
                    </el-form-item>
                    <el-form-item label="标签：" prop="tags">
                        <!-- <el-button @click="addTags" type="primary">添加标签</el-button>
                        <el-input v-model="addTagsInputList" placeholder="展示添加的标签"/> -->
                        <el-select
                            v-model="value11"
                            multiple
                            filterable
                            allow-create
                            default-first-option
                            placeholder="请选择文章标签">
                            <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="时长(秒)：" prop="content_time">
                        <el-input type="number" v-model="AddFormData.content_time"></el-input>
                    </el-form-item>
                    <el-form-item v-if="AddFormData.cont_type == 2" label="内容：" prop="content">
                        <el-input type="textarea" v-model="AddFormData.content"></el-input>
                    </el-form-item>
                    <el-form-item label="备注：" prop="remark">
                        <el-input type="textarea" v-model="AddFormData.remark"></el-input>
                    </el-form-item>
                </el-form>
                <span slot="footer" style="display: inline-block;position: fixed;bottom: 20px;right: 16%;">
                    <el-button @click="add_show = false" size="small">取消</el-button>
                    <el-button @click="onAddSubmit('AddFormData')" type="primary" size="small">确定</el-button>
                </span>
            </div>
        </el-drawer>
        <!-- 审核模板 -->
        <el-dialog
            title="审核"
            :visible.sync="check_show"
            width="500px">
            <el-form :model="CheckFormData" :rules="CheckRules" label-width="100px" size="small">
                <el-form-item label="审核：" prop="cont_type">
                    <el-select v-model="CheckFormData.state" style="width: 320px;">
                        <el-option
                            v-for="item in options_state"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="check_show = false" size="small">取消</el-button>
                <el-button @click="onCheckSubmit" type="primary" size="small">确定</el-button>
            </span>
        </el-dialog>
        <!-- 详细 -->
        <el-drawer  
            title="详细"
            :visible.sync="detail_show"
            size="30%">
            <ul class="detail-ul">
                <li>
                    <p><span>类型：</span><span>{{create_detail.cont_type == 1 ? '视频' : '图文'}}</span></p>
                </li>
                <li>
                    <p><span>标题：</span><span>{{create_detail.title}}</span></p>
                </li>
                <li>
                    <p><span>视频时长：</span><span>{{create_detail.content_time}}秒</span></p>
                </li>
                <!-- <li>
                    <p><span>icon：</span><span>{{create_detail.icon}}</span></p>
                </li> -->
                <li>
                    <p><span>查看次数：</span><span>{{create_detail.look_num}}次</span></p>
                </li>
                <li>
                    <p><span>状态：</span><span>{{create_detail.state == 0 ? '草稿' : (create_detail.state == -1 ? '作废' : (create_detail.state == 1 ? '审核通过' : (create_detail.state == 2 ? '待审核' : '审核失败')))}}</span></p>
                </li>
                <li>
                    <p><span>封面图：</span><img :src="create_detail.thumb" style="width: 200px;"></p>
                </li>
                <li>
                    <p><span>支付方式：</span><span>{{create_detail.is_pay == 0 ? '免费' : '收费'}}</span></p>
                </li>
                <li>
                    <p><span>备注：</span><span>{{create_detail.remark}}</span></p>
                </li>
                <li>
                    <p><span>学币：</span><span>{{create_detail.min_num}}学币</span></p>
                </li>
                <li>
                    <p><span>创建时间：</span><span>{{create_detail.add_time}}</span></p>
                </li>
                <li>
                    <p><span>加密：</span><span>{{create_detail.encrypt == 0 ? '不加密' : '加密'}}</span></p>
                </li>
                <li v-if="create_detail.video_type == 1 || create_detail.video_type == 2 || create_detail.video_type == 3">
                    <p><span>视频类型：</span><span>{{create_detail.video_type == 1 ? videoType1 : (create_detail.video_type == 2 ? videoType2 : videoType3)}}</span></p>
                </li>
                <li>
                    <p>
                        <span>视频：</span>
                        <span style="display: line-block;">
                            <div v-if="videoPlayer1" class="prism-player" id="J_prismPlayer" style="width: 300px;height: 300px;border: 1px solid red;"></div>
                            <video v-if="videoPlayer2" width="320" height="240" :src="videoUrl" controls>
                                <!-- <source :src="videoUrl" type="video/mp4">
                                <source :src="videoUrl" type="video/ogg">
                                您的浏览器不支持Video标签。 -->
                            </video>
                        </span>
                        
                    </p>
                </li>
            </ul>
            <el-button type="primary" @click="detail_show = false" size="small" style="display: inline-block;position: fixed;bottom: 20px;right: 16%;">取消</el-button>
        </el-drawer>
    </div>
</template>

<script>
import { cookie_get } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import { ManageResourceList } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import { ManageResourceAdd } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import { OssManageUpload } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import { ManageResourceEdit } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import { ManageResourceDel } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import { ManageResourceSubmitAuth } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
import { ManageResourceDetail } from "../../_commom/connectorHttp/trainingResourcesHttp.js";
// 标签
// 列表
import { ManageResourceTagList } from "../../_commom/connectorHttp/trainingResourcesHttp.js";


import lime from "@/lime.js";
import store from "@/store";
import util from "@/util.js";
import videoUpload from '../../_components/videoUpload';
export default {
    components: {
        videoUpload
    },
    data () {
        return {
            options: [],
            value11: [],


            /*员工列表*/ 
            rows:[],
            total:0,
            loading:false,

            curr_row:null,

            SearchFormData:{
                title:'',//公司名称
                //is_pay: 0,//收费
                //state: 0,//状态
                //cont_type: 1,//类型
                //from_type: 0,//来源
                page_num: 1,//起始页
                page_len: 10,//每页数
                order_field:'add_time',//排序字段
                order_sort:'desc',//排序类型
                start_date: '',//开始时间
                end_date: '',//结束时间
            },

            /*菜单操作*/ 
            // 搜素
            search_show:false,
            // 搜索-类型
            options_type: [
                {
                    value: 1,
                    label: '视频'
                },
                {
                    value: 2,
                    label: '图文'
                },
            ],
            // 搜索-类别
            options_fromType: [
                {
                    value: 1,
                    label: '监管部门'
                },
                {
                    value: 2,
                    label: '代理商'
                },
                {
                    value: 3,
                    label: '培训机构'
                },
                {
                    value: 4,
                    label: '个体工商户'
                },
                {
                    value: 5,
                    label: '运输企业客户'
                },
            ],
            // 搜索-状态
            options_state: [
                {
                    value: -1,
                    label: '作废'
                },
                {
                    value: 0,
                    label: '草稿'
                },
                {
                    value: 1,
                    label: '审核通过'
                },
                {
                    value: 2,
                    label: '待审核'
                },
                {
                    value: 3,
                    label: '审核不通过'
                },
            ],
            // 支付
            options_isPay: [
                {
                    value: 0,
                    label: '免费'
                },
                {
                    value: 1,
                    label: '收费'
                },
            ],
            // 详情
            detail_show: false,
            create_detail: {},
            videoType1: '出版社1.0',
            videoType2: '出版社2.0',
            videoType3: '阿里云视频点播',
            videoPlayer1: false,
            videoPlayer2: false,
            // 添加标签
            add_tag_show: false,
            addTagsInputList: '',
            tag_rows:[],
            tag_total:0,
            tag_curr_row: null,
            TagSearchFromData: {
                tag_name: '',
                start_date: '',
                end_date: '',
                page_no: 1,//当前页 默认第一页
                page_len: 10,//每页数据条数
                order_field:'add_time',//时间排序
                order_sort:'desc'//排序规则
            },
            loadingTag: false,
            // 添加
            add_show:false,
            // 添加标签列表
            videoUrl: '',// 视频地址
            typeContent: '',
            studyCar: '',// 学币
            tagUuid: '',
            AddFormData:{
                title: '',
                cont_type: '',
                is_pay: '',
                is_show: 0,
                encrypt: -1,
                content: '',
                remark: '',
                thumb: '0',
            },
            addRules: {
                title: [
                    { required: true, message: '请输入标题', trigger: 'blur' },
                ],
                cont_type: [
                    { required: true, message: '请选择类型', trigger: 'change' },
                ],
                is_pay: [
                    { required: true, message: '请选择支付', trigger: 'change' },
                ],
                content: [
                    { required: true, message: '请输入内容', trigger: 'blur' },
                ],
                remark: [
                    { required: true, message: '请输入备注', trigger: 'blur' },
                ]
            },

            // 编辑
            edit_show:false,
            videoPlayer3: false,
            videoPlayer4: false,
            EditFormData:{
                uuid: '',
                title: '',
                cont_type: -2,
                is_pay: -2,
                is_show: 0,
                content: '',
                remark: ''
            },
            EditRules: {
                title: [
                    { required: true, message: '请输入标题', trigger: 'blur' },
                ],
                cont_type: [
                    { required: true, message: '请选择类型', trigger: 'blur' },
                ],
                is_pay: [
                    { required: true, message: '请选择支付', trigger: 'blur' },
                ],
                content: [
                    { required: true, message: '请输入内容', trigger: 'blur' },
                ],
            },
            // 审核
            check_show: false,
            CheckFormData: {
                state: ''
            },
            CheckRules: {
                state: [
                    { required: true, message: '请选择审核', trigger: 'blur' },
                ]
            },
            addTime: '',
            //设置角色
            role_show:false,
            role_rows:[],
            RoleFormData:{
                manage_uuid:'',
                role_uuid:'',
            },
        }
    },
    created () {
        this.init();
        this.tagInit();
    },
    computed:{
        width:() => {
            return store.state.AppData.width;
        },
        height:() => {
            return store.state.AppData.height;
        },
        upload_url:() => {
            return store.state.AppData.upload_url;
        },
        img_host:() => {
            return store.state.AppData.img_host;
        }
    },
    methods: {
        isShowVideoFn1 (data) {
            this.videoPlayer3 = data;
        },
        isShowVideoFn2 (data) {
            this.videoPlayer4 = data;
        },
        UploadImage(param){
            //alert(4354)
            console.log(param)
            let file = param.file;
            let formData = new FormData(); //创建form对象
            let timestamp = lime.get_timestamp();
            let up_type = 'file';
            let  login_token= lime.cookie_get('login_token')

            const isIMG = file.type === 'image/jpg' || file.type === 'image/jpeg' || file.type === 'image/png';
            if (!isIMG) {
                this.$message.error('上传图片只支持jpg、jpeg、png格式!')
            }

            formData.append('file', file);//通过append向form对象添加数据
            formData.append('up_type', 'file');//通过append向form对象添加数据
            formData.append('timestamp', timestamp);//通过append向form对象添加数据
            formData.append('login_token', login_token);//通过append向form对象添加数据
            formData.append('sign', lime.md5_str(lime.set_sign({timestamp, up_type, login_token})));//通过

            OssManageUpload(
                formData ,
                {'Content-Type':'multipart/form-data'}
            ).then(response => {
                this.AddFormData.thumb = response.data.url;
            }).catch(response => {
            })
            
        },
        // 按钮点击 menu:参数数据 local是否本地程序
        onSubMenu(menu, local = false) {
            util.submenu(menu,this,cookie_get (), local);
        },
        /*方法*/
        //学习资源初始化
        init () {
            this.loading = true;
            var dataList = {
                login_token:cookie_get(),
                title: this.SearchFormData.title,
                state: this.SearchFormData.state,
                cont_type: this.SearchFormData.cont_type,
                from_type: this.SearchFormData.from_type,
                page_no: this.SearchFormData.page_num,//起始页
                page_len: this.SearchFormData.page_len,//每页数
                order_field: this.SearchFormData.order_field,//排序字段
                order_sort: this.SearchFormData.order_sort,//排序类型
                start_date: this.SearchFormData.start_date,
                end_date: this.SearchFormData.end_date,
            }
            ManageResourceList (dataList).then(res => {
                if (res.data == null) {
                    //this.init();
                } else {
                    this.rows = res.data.rows;
                    for (var i = 0;i<res.data.rows.length;i++) {
                        if (res.data.rows[i].state == -1) {
                            this.rows[i].state = '作废';
                        }
                        if (res.data.rows[i].from_type == 0) {
                            this.rows[i].from_type = '运营';
                        }
                        if (res.data.rows[i].state == 0) {
                            this.rows[i].state = '草稿';
                        }
                        if (res.data.rows[i].is_pay == 0) {
                            this.rows[i].is_pay = '免费';
                        }
                        if (res.data.rows[i].cont_type == 1) {
                            this.rows[i].cont_type = '视频';
                        }
                        if (res.data.rows[i].state == 1) {
                            this.rows[i].state = '审核通过';
                        }
                        if (res.data.rows[i].from_type == 1) {
                            this.rows[i].from_type = '监管部门';
                        }
                        if (res.data.rows[i].is_pay == 1) {
                            this.rows[i].is_pay = '收费';
                        }
                        if (res.data.rows[i].cont_type == 2) {
                            this.rows[i].cont_type = '图文';
                            this.rows[i].state = '作废';
                            this.rows[i].from_type = '监管部门';
                        }
                        if (res.data.rows[i].state == 2) {
                            this.rows[i].state = '待审核';
                        }
                        if (res.data.rows[i].from_type == 2) {
                            this.rows[i].from_type = '代理商';
                        }
                        if (res.data.rows[i].from_type == 3) {
                            this.rows[i].from_type = '培训机构';
                        }
                        if (res.data.rows[i].state == 3) {
                            this.rows[i].state = '审核不通过';
                        }
                        if (res.data.rows[i].from_type == 4) {
                            this.rows[i].from_type = '个体工商户';
                        }
                        if (res.data.rows[i].from_type == 5) {
                            this.rows[i].from_type = '运输企业客户';
                        }
                    }
                    this.total = res.data.total;
                    this.loading = false;
                }
            });
            // 超时关闭遮罩层
            setTimeout(() => {
                this.loading = false;
            }, 10000);
        },
        //排序处理
        onSortChange (column, prop, order) {
            this.SearchFormData.order_field = column.prop;
            if (column.order == 'ascending') {
                this.SearchFormData.order_sort  = 'asc';
            } else {
                this.SearchFormData.order_sort  = 'desc';
            }
            this.init();
        },
        //选择表格行
        onSelectRow (row) {
            this.curr_row = row;
            // 详细接口
            //this.handleDetail();
        },
        //分页处理
        onPageChange (page) {
            this.SearchFormData.page_num = page;
            this.init();
        },

        // 标签列表
        tagInit () {
            this.loadingTag = true;
            var dataList = {
                tag_name: this.TagSearchFromData.tag_name,
                start_date: this.TagSearchFromData.start_date,
                end_date: this.TagSearchFromData.end_date,
                page_no: this.TagSearchFromData.page_no,//当前页 默认第一页
                page_len: this.TagSearchFromData.page_len,//每页数据条数
                order_field: this.TagSearchFromData.order_field,//时间排序
                order_sort: this.TagSearchFromData.order_sort,//排序规则
                login_token: cookie_get()
            }
            ManageResourceTagList (dataList).then(res => {
                if (res.code == 0) {
                    this.tag_rows = res.data.rows;
                    this.tag_total = res.data.total;
                } else {
                    this.$message.error(res.msg);
                }
                this.loadingTag = false;
            })
        },
        // 标签列表排序
        onTagSortChange (column, prop, order) {
            this.TagSearchFromData.order_field = column.prop;
            if (column.order == 'ascending') {
                this.TagSearchFromData.order_sort  = 'asc';
            } else {
                this.TagSearchFromData.order_sort  = 'desc';
            }
            this.tagInit();
        },
        // 标签列表选行
        onTagSelectRow (row) {
            console.log(row)
            this.tag_curr_row = row;
        },
        // 标签列表分页处理
        onTagPageChange (page) {
            this.TagSearchFromData.page_no = page;
            this.tagInit();
        },
        /*菜单操作*/
        //刷新
        onRefresh() {
            this.init();
        },
        //搜索显示弹框
        onSearch () {
            this.search_show = true;
        },
        //搜索弹框提交
        onSearchSubmit () {
            this.SearchFormData.page_num = 1;
            this.init();
            this.search_show = false;
            this.SearchFormData.title = '';
            this.SearchFormData.cont_type = '';
        },
        //添加显示
        handleAdd(param) {
            this.AddFormData.parent_uuid = param.parent_uuid;
            this.add_show = true;
        },
        // 标签添加
        addTags () {
            this.add_tag_show = true;
        },
        onTagAddSubmit () {
            console.log(this.tag_curr_row);
            this.addTagsInputList = this.tag_curr_row.tag_name;
            this.tagUuid = this.tag_curr_row.uuid;
            this.add_tag_show = false;
            console.log(this.tagUuid)
        },
        // 监听类型
        onContype (e) {
            console.log(e)
            this.AddFormData.encrypt = -1;
            //this.AddFormData.encrypt = -1;
            //this.AddFormData.content = '';
            this.typeContent = '';
        },
        // 监听加密
        onEncrypt (e) {
            console.log(e)
            this.typeContent = '';
        },
        // 视频url
        getVideoUrl (data) {
            console.log(data)
            this.typeContent = data;
        },
        onPayChange (e) {
            if (e == 0) {
                this.studyCar = false;
            } else {    
                this.studyCar = true;
            }
        },
        //添加模板提交
        onAddSubmit (formName) {
            if (this.AddFormData.cont_type == 2) {
                this.typeContent = this.AddFormData.content;
            }
            console.log(this.typeContent)
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    var dataList1 = {
                        cont_type:this.AddFormData.cont_type,
                        title:this.AddFormData.title,
                        encrypt: this.AddFormData.encrypt,
                        is_pay:this.AddFormData.is_pay == '免费' ? 0 : 1,
                        thumb:this.AddFormData.thumb,
                        is_show:this.AddFormData.is_show == false ? 0 : 1,
                        min_num: this.AddFormData.is_pay == '免费' ? 0 : parseInt(this.AddFormData.min_num),
                        tags: this.tagUuid,
                        content_time: parseInt(this.AddFormData.content_time),
                        content:this.typeContent,
                        remark: this.AddFormData.remark,
                        login_token:cookie_get(),
                    }
                    console.log(dataList1)
                    ManageResourceAdd (dataList1).then(res => {
                        if (res.code == 0) {
                            this.$message.success('添加成功');
                            this.SearchFormData.page_num = 1;
                            this.init();
                        } else {
                            this.$message.error(res.msg);
                        }
                        this.add_show = false;
                        this.$refs[formName].resetFields();
                    }).catch(err => {
                        this.$message.error(err.msg);
                    })
                } else {
                    return false;
                }
            });
        },
        //编辑显示
        handleEdit (editParam) {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            var detailParams = {
                login_token:cookie_get(),
                uuid: this.curr_row.uuid
            }
            ManageResourceDetail (detailParams).then(res => {
                if (res.code == 0) {
                    this.edit_show = true;
                    this.create_detail = res.data;
                    this.EditFormData.cont_type = this.create_detail.cont_type;
                    this.EditFormData.title = this.create_detail.title;
                    this.EditFormData.encrypt = this.create_detail.encrypt;
                    this.EditFormData.is_pay = this.create_detail.is_pay == '免费' ? 0 : 1;
                    this.EditFormData.thumb = this.create_detail.thumb;
                    this.EditFormData.is_show = this.create_detail.is_show == false ? 0 : 1;
                    this.EditFormData.min_num = parseInt(this.create_detail.min_num);
                    this.EditFormData.tags = this.tagUuid;
                    this.EditFormData.content_time = this.create_detail.content_time;
                    this.EditFormData.content = this.typeContent;
                    if (res.data.video_type == 3) {
                        var self = this;
                        if (res.data.encrypt == 0) {
                            this.videoPlayer3 = false;
                            this.videoPlayer4 = true;
                            var dTime1 = setInterval(function () {
                                lime.req('ManagerAliVideoPlayUrl', {
                                    login_token:lime.cookie_get('login_token'),
                                    videoId: res.data.content
                                }).then(res => {
                                    if (res.code == 0) {
                                        clearInterval(dTime1)
                                        self.videoUrl = res.data.playUrl;
                                        self.$emit('func',self.vid)

                                        //self.saveVideoId(self.videoId);
                                        //self.saveFileSize(self.fileSize);
                                    }
                                })
                            },3000)
                        } else {
                            this.videoPlayer4 = false;
                            this.videoPlayer3 = true;
                            var dTime2 = setInterval(function () {
                                lime.req('ManagerVideoAlPlayAuth', {
                                    login_token:lime.cookie_get('login_token'),
                                    videoId: res.data.content
                                }).then(res => {
                                    if (res.code == 0) {
                                        clearInterval(dTime2)
                                        self.playAuth1 = res.data;
                                        self.$emit('func',self.vid)
                                        //self.saveVideoId(self.videoId);
                                        var palyer = new Aliplayer({
                                            id: 'J_prismPlayer',
                                            vid: self.playAuth1.videoPlayId,//self.vid
                                            width: '100%',
                                            height: '100%',
                                            autoplay: false,
                                            // 播放方式二：点播用户推荐
                                            cover: 'http://liveroom-img.oss-cn-qingdao.aliyuncs.com/logo.png',
                                            playauth: self.playAuth1.playAuth,
                                            isLive: false,
                                            rePlay: false,
                                            playsinline: true,
                                            encryptType: 1,
                                        })
                                    }
                                });
                            },3000)
                        }
                    }
                } else {
                    this.$message.error(res.msg);
                }
            });
        },
        //编辑模板提交
        onEditSubmit () {
            var dataList1 = {
                uuid: this.EditFormData.uuid,
                title: this.EditFormData.title,
                //from_type: this.EditFormData.from_type,
                icon: this.EditFormData.icon,
                remark: this.EditFormData.remark,
                state: this.EditFormData.state,
                cont_type: this.EditFormData.cont_type == '视频' ? 1 : 2,
                is_pay: this.EditFormData.is_pay == '免费' ? 0 : 1,
                thumb: this.EditFormData.thumb,
                is_show: this.EditFormData.is_show == false ? 0 : 1,
                content: this.EditFormData.content,
                login_token:cookie_get(),
            }

            ManageResourceEdit (dataList1).then(res => {
                if (res.code == 0) {
                    this.$message.success('编辑成功');
                } else {
                    this.$message.error(res.msg);
                }
                this.init();
                this.edit_show = false;
            }).catch(err => {
                this.$message.error(err.msg);
            });
        },
        //删除提交
        handleDel(menu) {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            this.$confirm('确认删除?', '提示').then(() => {
                ManageResourceDel({
                    login_token:cookie_get(),
                    uuid:this.curr_row.uuid
                }).then(res => {
                    if (res.code == 0) {
                        this.$message.success('删除成功');
                    } else {
                        this.$message.error(res.msg);
                    }
                    this.init();
                }).catch(err => {
                    this.$message.error(err.msg);
                })
            })
        },
        //审核
        handleCheck () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            this.check_show = true;
        },
        //审核提交
        onCheckSubmit () {
            var check_params = {
                login_token:cookie_get(),
                uuid: this.curr_row.uuid
            }
            ManageResourceSubmitAuth (check_params).then(res => {
                if (res.code == 0) {
                    this.$message.success('审核成功');
                } else {
                    this.$message.error(res.msg);
                }
                this.init();
                this.check_show = false;
            })
        },
        //详情
        handleDetail () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            console.log(this.curr_row.uuid)
            var detailParams = {
                login_token:cookie_get(),
                uuid: this.curr_row.uuid
            }
            ManageResourceDetail (detailParams).then(res => {
                this.create_detail = res.data;
                if (res.data.video_type == 3) {
                    var self = this;
                    if (res.data.encrypt == 0) {
                        this.videoPlayer1 = false;
                        this.videoPlayer2 = true;
                        var dTime1 = setInterval(function () {
                            lime.req('ManagerAliVideoPlayUrl', {
                                login_token:lime.cookie_get('login_token'),
                                videoId: res.data.content
                            }).then(res => {
                                if (res.code == 0) {
                                    clearInterval(dTime1)
                                    self.videoUrl = res.data.playUrl;
                                    self.$emit('func',self.vid)

                                    //self.saveVideoId(self.videoId);
                                    //self.saveFileSize(self.fileSize);
                                }
                            })
                        },3000)
                    } else {
                        this.videoPlayer2 = false;
                        this.videoPlayer1 = true;
                        //var dTime2 = setInterval(function () {
                            lime.req('ManagerVideoAlPlayAuth', {
                                login_token:lime.cookie_get('login_token'),
                                videoId: res.data.content
                            }).then(res => {
                                if (res.code == 0) {
                                    //clearInterval(dTime2)
                                    self.playAuth1 = res.data;
                                    self.$emit('func',self.vid)
                                    //self.saveVideoId(self.videoId);
                                    var palyer = new Aliplayer({
                                        id: 'J_prismPlayer',
                                        vid: self.playAuth1.videoPlayId,//self.vid
                                        width: '100%',
                                        height: '100%',
                                        autoplay: false,
                                        // 播放方式二：点播用户推荐
                                        cover: 'http://liveroom-img.oss-cn-qingdao.aliyuncs.com/logo.png',
                                        playauth: self.playAuth1.playAuth,
                                        isLive: false,
                                        rePlay: false,
                                        playsinline: true,
                                        encryptType: 1,
                                    })
                                }
                            });
                        //},3000)
                    }
                }



                // let date = new Date(parseInt(this.create_detail.add_time) * 1000);
                // let Y = date.getFullYear() + '-';
                // let M = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) + '-' : date.getMonth() + 1 + '-';
                // let D = date.getDate() < 10 ? '0' + date.getDate() + ' ' : date.getDate() + ' ';
                // let h = date.getHours() < 10 ? '0' + date.getHours() + ':' : date.getHours() + ':';
                // let m = date.getMinutes()  < 10 ? '0' + date.getMinutes() + ':' : date.getMinutes() + ':';
                // let s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
                // this.addTime = Y + M + D ;
            });
            this.detail_show = true;
        },
    }
}
</script>

