                line-height: 30px;
                overflow: hidden;
                p:nth-of-type(1){
                    float: left;
                    margin-left: 0px;
                }
                p:nth-of-type(2){
                    float: right;
                    margin-right: 0px;
                }
                p{
                    span{
                        font-size: 14px;
                    }
                    span:nth-of-type(1){
                        width: 100px;
