/*
员工管理
*/
<template>
    <div class="staff">
        <!-- 菜单操作 -->
        <div style="height: 62px; line-height: 62px; overflow: hidden;border-bottom: 1px solid #F2F2F2;">
            <el-row>
                <el-col :span="6">
                    <div style="padding-left:16px;">
                        <!-- <i class="el-icon-s-unfold"></i> -->
                        <span style="padding-left:9px;">
                            {{$store.state.AdminData.active_title}}
                        </span>
                    </div>
                </el-col>

                <el-col :span="18">
                    <div style="text-align: right; ">
                        <el-link @click="onSubMenu('onRefresh',true)" class="menu">刷新</el-link>
                        <el-link @click="onSubMenu('onSearch',true)" class="menu">搜索</el-link>

                        <el-link
                            class="menu" 
                            @click="onSubMenu(item)"
                            v-for="(item,index) in $store.state.AdminData.right_menus" 
                            :key="index">
                            {{item.name}}
                        </el-link>
                    </div>
                </el-col>
            </el-row>
        </div>
        <div class="search-box">
            <el-form :model="SearchFormData" label-width="80px" size="small">
                <el-form-item label="员工姓名:">
                    <el-input v-model="SearchFormData.real_name" style="width: 240px;"/>
                </el-form-item>
                <el-button type="primary" @click="onSearchSubmit" size="small" style="margin-left: 24px;">搜 索</el-button>
            </el-form>
        </div>
        <!-- 员工列表 -->
        <div>
            <el-table 
                :data="rows"
                :height="height - 60 - 62 - 92 - 48"
                v-loading="loading"
                element-loading-text="拼命加载中"
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                @sort-change="onSortChange"
                :highlight-current-row="true"
                @current-change="onSelectRow"
                :row-class-name="tableRowClassName"
                :border="false"
                style="width: 100%;padding: 0 30px;" 
                size="mini">
                <el-table-column type="index" label="#" align="right"></el-table-column>
                <el-table-column prop="real_name" label="姓名" align="center"></el-table-column>
                <el-table-column prop="mobile" label="登录手机" align="center"></el-table-column>
                <el-table-column prop="role_name" label="角色" align="center">
                    <template slot-scope="scope">
                        {{scope.row.role_name == '' ? '未设置' : scope.row.role_name}}
                    </template>
                </el-table-column>
                <el-table-column prop="add_time" label="添加时间" align="center" :sortable=true></el-table-column>
                <el-table-column 
                    prop="last_time" 
                    label="最近登录时间"
                    align="center"
                    :sortable=true>
                    <template slot-scope="scope">
                        {{scope.row.last_time == 0 ? '还未登陆' : scope.row.last_time}}
                    </template>
                </el-table-column>
                <el-table-column prop="last_ip" label="最近登录IP" align="center">
                    <template slot-scope="scope">
                        {{scope.row.last_ip == 0 ? '-' : scope.row.last_ip}}
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="230px" align="center">
                <template slot-scope="scope">
                    <el-dropdown trigger="click">
                        <span class="el-dropdown-link">
                            更多<i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item @click="handleEdit(scope.$index, scope.row)">
                                <el-button
                                size="mini"
                                type="text"
                                @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                            </el-dropdown-item>
                            <el-dropdown-item>
                                <el-button
                                size="mini"
                                type="text"
                                @click="handleSetRole(scope.$index, scope.row)">设置角色</el-button>
                            </el-dropdown-item>
                            <el-dropdown-item>
                                <el-button
                                size="mini"
                                type="text"
                                @click="handleDetail(scope.$index, scope.row)">详情</el-button>
                            </el-dropdown-item>
                            <el-dropdown-item>
                                <el-button
                                size="mini"
                                type="text"
                                @click="handleDel(scope.$index, scope.row)">删除</el-button>
                            </el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </template>
    </el-table-column>
            </el-table>
            
            <div class="page" :style="{width:width - 250 + 'px'}">
                <el-pagination
                    :current-page.sync="SearchFormData.page_num"
                    @current-change="onPageChange"
                    layout="prev, pager, next, jumper"
                    :total="total">
                </el-pagination>
            </div>
        </div>

        <!-- 菜单操作弹框 -->
        <!-- 搜索 -->
        <el-dialog
            title="提示"
            :visible.sync="search_show"
            width=400px>
            <el-form :model="SearchFormData" label-width="80px" size="small">
                <el-form-item label="员工姓名:">
                    <el-input v-model="SearchFormData.real_name"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="search_show = false" size="small">取 消</el-button>
                <el-button type="primary" @click="onSearchSubmit" size="small">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 添加模板 -->
        <el-dialog  
            title="添加"
            :visible.sync="add_show"
            width="500px">
            <el-form :model="AddFormData" label-width="80px" size="small">
                <el-form-item label="登录手机:">
                    <el-input v-model="AddFormData.mobile" />
                </el-form-item>

                <el-form-item label="登录密码:">
                    <el-input show-password v-model="AddFormData.pwd" />
                </el-form-item>

                <el-form-item label="员工姓名:">
                    <el-input v-model="AddFormData.real_name" />
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="add_show = false" size="small">取消</el-button>
                <el-button @click="onAddSubmit" type="primary" size="small">确定</el-button>
            </span>
        </el-dialog>

        <!-- 编辑模板 -->
        <el-dialog  
            title="编辑"
            :visible.sync="edit_show"
            width="400px">
            <el-form :model="EditFormData" label-width="80px" size="small">
                <el-form-item label="员工姓名:">
                    <el-input v-model="EditFormData.real_name" />
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="edit_show = false" size="small">取消</el-button>
                <el-button @click="onEditSubmit" type="primary" size="small">确定</el-button>
            </span>
        </el-dialog>

        <!-- 设置角色 -->
         <el-dialog 
            title="设置角色"
            width="450px"
            :visible.sync="role_show">
            <el-form :model="RoleFormData" label-width="80px" size="small">
                <el-form-item label="拥有角色:">
                    <el-cascader 
                        clearable
                        :options="role_rows"
                        :props="{expandTrigger: 'hover',value:'uuid', label:'name',emitPath:false}"
                        placeholder="不选择,无权限"
                        v-model="RoleFormData.role_uuid" style="width: 330px;"
                        @change="handleChange">
                    </el-cascader>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="role_show = false" size="small">取消</el-button>
                <el-button type="primary" @click="onRoleSubmit" size="small">确认</el-button>
            </span>
        </el-dialog>

        <!-- 详情 -->
        <el-drawer
            title="详情"
            :visible.sync="detail_show"
            size="25%">
            <ul>
                <li>
                    <span>员工姓名：</span>
                    <span>{{DetailFormData.real_name}}</span>
                </li>
                <li>
                    <span>角色名称：</span>
                    <span>{{DetailFormData.role_name}}</span>
                </li>
                <li>
                    <span>员工登录手机：</span>
                    <span>{{DetailFormData.login_mobile}}</span>
                </li>
                <li>
                    <span>添加员工时间：</span>
                    <span>{{DetailFormData.add_time}}</span>
                </li>
                <li>
                    <span>最近一次登录ip：</span>
                    <span>{{DetailFormData.last_ip}}</span>
                </li>
                <li>
                    <span>最近一次登录时间：</span>
                    <span>{{DetailFormData.last_time}}</span>
                </li>
            </ul>

            <el-button type="primary" @click="detail_show = false" size="small" style="position: fixed;bottom: 10px;right: 10%;">取消</el-button>
        </el-drawer>
    </div>
</template>

<script>
import { ManageList } from "../../_commom/connectorHttp/manageHttp.js";
import { cookie_get } from "../../_commom/connectorHttp/manageHttp.js";
import { ManageSetRole } from "../../_commom/connectorHttp/manageHttp.js";
import { ManageAdd } from "../../_commom/connectorHttp/manageHttp.js";
import { ManageEdit } from "../../_commom/connectorHttp/manageHttp.js";
import { ManageDel } from "../../_commom/connectorHttp/manageHttp.js";
import { ManageDetail } from "../../_commom/connectorHttp/manageHttp.js";
import lime from "@/lime.js";

import store from "@/store";
import util from "@/util.js";
export default {
    data () {
        return {
            /*员工列表*/ 
            rows:[],
            total:0,
            loading:false,

            curr_row:null,

            SearchFormData:{
                real_name:'',
                page_num:1,
                page_len:10,
                order_field:'add_time',
                order_sort:'desc'
            },

            /*菜单操作*/ 
            // 搜素
            search_show:false,

            // 添加
            add_show:false,
            AddFormData:{
                real_name:'',
                mobile:'',
                pwd:'',
            },

            // 编辑
            edit_show:false,
            EditFormData:{
                real_name:''
            },

            // 设置角色
            role_show:false,
            role_rows:[],
            RoleFormData:{
                manage_uuid:'',
                role_uuid:'',
            },

            // 详情
            detail_show: false,
            DetailFormData: {
                real_name: ''
            }
        }
    },
    mounted () {
        this.init();
    },
    computed:{
        width:() => {
            return store.state.AppData.width;
        },
        height:() => {
            return store.state.AppData.height;
        },
        upload_url:() => {
            return store.state.AppData.upload_url;
        },
        img_host:() => {
            return store.state.AppData.img_host;
        }
    },
    methods: {
        // 按钮点击 menu:参数数据 local是否本地程序
        onSubMenu(menu, local = false) {
            util.submenu(menu,this,cookie_get (), local);
        },
        tableRowClassName({row, rowIndex}) {
            if (rowIndex%2=== 1) {
                return 'warning-row';
            } else {
                return 'success-row';
            }
            return '';
        },
        /*员工列表方法*/ 
        //员工列表初始化
        init () {
            this.loading = true;
            var dataList = {
                login_token:cookie_get(),
                real_name:this.SearchFormData.real_name,
                page_num:this.SearchFormData.page_num,
                page_len:this.SearchFormData.page_len,
                order_field:this.SearchFormData.order_field,
                order_sort:this.SearchFormData.order_sort
            }
            ManageList (dataList).then(res => {
                this.loading = false;
                this.rows = res.data.rows;
                this.total = res.data.total;
            });
            // 超时关闭遮罩层
            setTimeout(() => {
                this.loading = false;
            }, 10000);
        },
        //排序处理
        onSortChange (column) {
            this.SearchFormData.order_field = column.prop;
            if (column.order == 'ascending') {
                this.SearchFormData.order_sort  = 'asc';
            } else {
                this.SearchFormData.order_sort  = 'desc';
            }
            this.init();
        },
        //选择表格行
        onSelectRow (row) {
            this.curr_row = row;
        },
        //分页处理
        onPageChange (page) {
            this.SearchFormData.page_num = page;
            this.init();
        },

        /*菜单操作*/
        //刷新
        onRefresh() {
            this.init();
        },
        //搜索显示弹框
        onSearch () {
            this.search_show = true;
        },
        //搜索弹框提交
        onSearchSubmit () {
            this.SearchFormData.page_num = 1;
            this.init();
            this.search_show = false;
        },
        //添加显示
        handleAdd() {
            this.add_show = true;
        },
        //添加模板提交
        onAddSubmit () {
            this.AddFormData.login_token = cookie_get();
            console.log(this.AddFormData)
            ManageAdd (this.AddFormData).then(res => {
                this.SearchFormData.page_num = 1;
                this.init();
                this.add_show = false;
                this.AddFormData.real_name = '';
            }).catch(err => {
                this.$message.error(err.msg);
            })
        },
        //编辑显示
        handleEdit () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            this.EditFormData.real_name = this.curr_row.real_name;
            this.edit_show = true;
        },
        //编辑模板提交
        onEditSubmit () {
            this.EditFormData.login_token = cookie_get();
            this.EditFormData.uuid = this.curr_row.uuid;
            console.log(this.EditFormData)
            ManageEdit (this.EditFormData).then(res => {
                this.init();
                this.edit_show = false;
                this.EditFormData.real_name = '';
            }).catch(err => {
                this.$message.error(err.msg);
            });
        },
        //详情
        handleDetail () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            this.DetailFormData = null;
            var detailParam = {
                login_token:cookie_get(),
                uuid: this.curr_row.uuid
            }
            ManageDetail (detailParam).then(res => {
                this.DetailFormData = res.data;
            }).catch(err => {
                this.$message.error(err.msg);
            });
            this.detail_show = true;
        },
        //设置角色选中
        handleChange(value) {
            console.log(value);
        },
        //设置角色
        handleSetRole () {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }
            console.log(this.curr_row)
            // 获取当前登录账号拥有的角色
            lime.req({
                    module:'ManageSetRole',
                    ver:'1.0.0',
                    relation_module:'ManageRoleList',
                    relation_ver:'1.0.0'
                }, {    
                    login_token:cookie_get(),
                }).then(res => {
                    this.role_rows = util.toTree(res.data);
                    //const tempArray = res.data.name.split(',');
                    this.RoleFormData.role_uuid = this.curr_row.role_uuid;
                    this.role_show = true;
                })
        },
        //角色模板提交
        onRoleSubmit () {
            ManageSetRole ({
                login_token:cookie_get(),
                manage_uuid:this.curr_row.uuid,
                role_uuid:this.RoleFormData.role_uuid
            }).then(res =>{
                if (res.code == 0) {
                    this.$message.success('设置成功');
                } else {
                    this.$message.error(res.msg);
                }
                this.init();
                this.role_show=false;
            }).catch(err => {
                this.$message.error(err.msg)
            })
        },
        //删除提交
        handleDel(menu) {
            if (util.empty(this.curr_row)) {
                this.$message.error('请选择一条数据');
                return;
            }

            this.$confirm('确认删除?', '提示').then(() => {
                ManageDel({
                    login_token:cookie_get(),
                    uuid:this.curr_row.uuid
                }).then(res => {
                    this.init();
                    this.$message.success('操作成功');
                }).catch(err => {
                    this.$message.error(err.msg);
                })
            })
        },
    }
}
</script>

<style lang="less" scoped>
    .staff{
        .menu{
            display: inline-block;
            padding:0 16px;
            text-align: center;
        }
        .page {
            height: 40px; 
            line-height: 40px; 
            text-align: right;
            position: fixed;
            bottom: 0;
            right:0;
            overflow: hidden;
        }
        ul{
            list-style: none;
            padding-left: 20px;
            li{
                line-height: 50px;
                span{
                    font-size: 14px;
                }
                span:nth-of-type(1){
                    width: 150px;
                    text-align: right;
                    display: inline-block;
                }
            }
        }
    }
    
</style>