<template>
    <div class="shopCommunicate">
        <headMenus
                @onSearch="onSearch"
                @onRefresh="onRefresh"
                @handleAdd="handleAdd"
                @handleDel="handleDel"
                @handleEdit="handleEdit"
                @handleDetail="handleDetail"
        ></headMenus>

        <hTable
                :list="list"
                :total="total"
                :table_des="table_des"
                :page_len="params.page_len"
                :currentPage="params.page_num"
                @currentChange="currentChange"
                @tableChoose="tableChoose"
                @sortChange="sortChange"
                :loading="loading"
        ></hTable>
        <formEditModal
                ref="formEditModal"
                title="搜索"
                :form_des="form_des"
                @sure="sureSearch"
        ></formEditModal>
        <formEditModal
                ref="addTalkFrom"
                title="添加沟通记录"
                width="50%"
                :form_des="talk_form_des"
                @sure="sureAddTalk"
        ></formEditModal>
        <formEditModal
                ref="editTalkFrom"
                title="编辑沟通记录"
                width="50%"
                :form_des="talk_edit_form_des"
                @sure="sureEditTalk"
        ></formEditModal>

    </div>


</template>

<script>
    import headMenus from '../../_components/headMenus'
    import hTable from '../../_components/hTable'
    import formEditModal from '../../_components/formEditModal'
    import tableFromMix from '../../_commom/mixins/tableFromMix'


    import coneHttpShop from '../../_commom/connectorHttp/coneHttpShop'
    import coneHttpMember from '../../_commom/connectorHttp/coneHttpMember'

    import pageEvent from "../../_commom/lib/pageEvent";
    import utils from "../../_commom/lib/utils";
    export default {
        name: "shopCommunicate",
        mixins: [tableFromMix],

        components: {headMenus, hTable, formEditModal},
        data() {
            return {
                params: {

                    member_name: '',
                    max_enable_money: '',//最大可用金额
                    min_enable_money: '',//最小可用金额
                    max_freeze_money: '',//最大冻结金额
                    min_freeze_money: '',//最小冻结金额
                    order_field: null,
                    order_sort: '',
                    page_num: 1,
                    page_len: 20,

                },
                table_des: [
                    {sortable: false, label: '#', prop: '_index', width: '50'},
                    {sortable: false, label: '员工', prop: 'manage_name', width: ''},
                    {sortable: false, label: '商户', prop: 'shop_name', width: ''}
                    ,{sortable: false, label: '商户状态', prop: 'shop_state', width: '',
                        // render(scope) {
                        //     let row = scope.row;
                        //     let obj =  utils.getFieldDesConf('shop','shop_state');
                        //     return obj[row.shop_state + ''];
                        // }

                        render(h, {column, index, row}) {

                            let obj = utils.getFieldDesConf('shop', 'shop_state');

                            let item = obj[row.shop_state + ''];

                            return h('el-tag',
                                {
                                    attrs:{
                                        type:item.tag_type,
                                        size:'small'
                                    }
                                },
                                item.name
                            )

                        }
                    }
                    // , {sortable: false, label: '沟通内容', prop: 'content', width: ''}
                    , {sortable: false, label: '沟通效果评分', prop: 'effect_num', width: ''}
                    , {sortable: 'custom', label: '添加时间', prop: 'add_time', width: 'auto'}

                ],
                /*搜索字段*/
                form_des: [
                    {
                        field: 'member_uuid', type: 'searchSelect', label: '会员', value: "",
                        handler:'ManageMemberList', //远程事件
                        placeholder:'请输入搜索',
                        options: [
                            // {name:'苹果',value:'1'},
                            // {name:'香蕉',value:'2'}
                        ]

                    },

                ],
                /*添加的表单字段*/
                // add_from_params:{
                //     shop_uuid:'',
                //     content:'',
                //     effect_num:'',
                // },
                /*添加沟通记录表单*/
                talk_form_des: [
                    {
                        field: 'shop_uuid', type: 'searchSelect', label: '商户', value: "",
                        placeholder:'请输入搜索',
                        options: [
                                        // {name:'苹果',value:'1'},
                                        // {name:'香蕉',value:'2'}
                                    ],
                        rules:[ { required: true, message: '商户唯一值不可为空', trigger: 'blur' },]
                    },

                    {
                        field: 'effect_num', type: 'number_positive', label: '沟通效果', value: '',
                        rules:[ { required: true, message: '沟通效果不可为空', trigger: 'blur' },]
                    },
                    {
                        field: 'content', type: 'editor', label: '沟通内容', value: '',
                        rules:[ { required: true, message: '沟通内容不可为空', trigger: 'blur' },]
                    },

                ],
                /*编辑沟通记录表单*/
                talk_edit_form_des: [
                    // {
                    //     field: 'uuid', type: 'text', label: '商户唯一值', value: "",
                    //     rules:[ { required: true, message: '商户唯一值不可为空', trigger: 'blur' },]
                    // },

                    {
                        field: 'effect_num', type: 'number_positive', label: '沟通效果', value: '',
                        rules:[ { required: true, message: '沟通效果不可为空', trigger: 'blur' },]
                    },
                    {
                        field: 'content', type: 'editor', label: '沟通内容', value: '',
                        rules:[ { required: true, message: '沟通内容不可为空', trigger: 'blur' },]
                    },

                ],
            }

        },
        methods: {

            /*获取数据列表*/
            getList(params1) {


                this.loading = true;

                let params =params1|| this.params;
                return new Promise((resolve, reject) => {
                    coneHttpShop.getManageShopCommunicateList(params)
                        .then(res => {
                            let data = res.data;


                            this.total = data.total / 1;
                            let rows = data.rows;

                            let page_len = params.page_len,
                                page_num = params.page_num;
                            let list = this._disposeList(rows, page_num, page_len);
                            this.loading = false;


                            // console.log('列表数据', this.list)
                            resolve(list)
                        })

                })
            },

            /*查看*/

            handleDetail(){
            console.warn('没有此接口')
            },
            /*点击添加菜单*/
            handleAdd(){
                this.$refs.addTalkFrom.show()

            },
            /*确认添加内容*/
            sureAddTalk(val){

                coneHttpShop.ManageShopCommunicateAdd(val.form_data)
                .then(res=>{
                    this.$message({
                        message: '添加成功',
                        type: 'success'
                    });
                    this.getList()
                    .then(list=>{
                        this.list=list;
                    })


                })
                .catch(err=>{
                    this.$message.error(err.msg);
                })

            },

            // /*检测是否选中数据行*/
            // checkChooseListOne(){
            //     let curr_list_one = this.curr_list_one;//选中的数据
            //
            //
            //      return  curr_list_one &&  Object.keys(curr_list_one).length>0
            //
            //
            //
            // },


            /*点击编辑*/
            handleEdit(){
                //删除数据之前要选择数据
                let curr_list_one = this.curr_list_one;//选中的数据

                let index = curr_list_one._index;
                let re = this.checkChooseListOne();
                if(!re){
                    return false;
                }

                /*添加默认值*/
                this.addDataFrom(
                    this.talk_edit_form_des,
                    {
                        uuid:curr_list_one.uuid,
                        content:curr_list_one.content,
                        effect_num:curr_list_one.effect_num
                    }
                );
                this.$refs.editTalkFrom.show();
            },
            /*编辑确认*/
            sureEditTalk(val){
                let curr_list_one = this.curr_list_one;
                let uuid =curr_list_one.uuid;

                coneHttpShop.ManageShopCommunicateEdit({...val.form_data,uuid})
                    .then(res=>{
                        this.$message({
                            message: '修改成功',
                            type: 'success'
                        });

                        this.onRefreshIndex(curr_list_one._index-1)

                    })
                    .catch(err=>{
                        this.$message.error(err.msg);
                    })
            },
            /*删除沟通记录*/
            handleDel(){
                //删除数据之前要选择数据
                let curr_list_one = this.curr_list_one;//选中的数据

                let index = curr_list_one._index;

                if(index!==undefined){
                    this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        coneHttpShop.ManageShopCommunicateDel({uuid:curr_list_one.uuid})
                            .then(res=>{
                                this.list.splice(curr_list_one._index-1,1)
                                this.$message({
                                    type: 'success',
                                    message: '删除成功!'
                                });
                            })

                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消删除'
                        });
                    });

                }else {
                    this.$message({
                        message: '请先选中一条数据！',
                        type: 'warning'
                    });

                }
            }


            /*获取商户列表*/
            ,getShopList(val){

                coneHttpShop.getMemberShopList({name:val.value,page_num:1,page_len:utils.getConfig('select_page_len')})
                    .then(res=>{

                        let list =res.data.rows;
                        let options = list.map(v=>{
                            return {name:v.name,value:v.uuid}
                        })
                        utils.editObjField(val.from_des,{ field: 'shop_uuid'}, {options});

                    });



            }

            /*获取会员列表*/
            ,getManageMemberList(val){

                coneHttpMember.getMemberList({name:val.value,page_num:1,page_len:utils.getConfig('select_page_len')})
                .then(res=>{
                    let list =res.data.rows;
                    let options = list.map(v=>{
                        return {name:v.name,value:v.uuid}
                    })
                    utils.editObjField(val.from_des,{ field: 'member_uuid'}, {options});
                })
            }
        },
        mounted() {
            this.getList()
                .then(list=>{
                    this.list=list;
                })


            pageEvent.$off('getShopList',null);
            pageEvent.$on('getShopList',this.getShopList);
            pageEvent.$off('ManageMemberList',null);
            pageEvent.$on('ManageMemberList',this.getManageMemberList);

            // setTimeout(()=>{
            //     let obj=[{mame:'hxq',age:18,habit:'cc'}];
            //     console.log(obj)
            //     utils.editObjField(obj,{mame:'hxq',age:18},{habit:'ss'});
            //     console.log(obj)
            //     // console.log(utils.objInner({name:'hxq1'},{name:'hxq'}))
            // },1500)

        }
    }
</script>
<style lang="less" scoped>
    @import "../../css/common";

</style>