<template>
    <!--自定义表格组件 待分页-->
    <div class="hTbale">
<!--        <el-progress :percentage="50"></el-progress>-->
        <div class="table">
<!--          -->
            <el-table
                    mpty-text="暂无数据"
                    highlight-current-row
                    @current-change="handleCurrentChange"
                    @sort-change="sortChange"
                    v-loading="loading"
                    element-loading-text="拼命加载中"
                    element-loading-spinner="el-icon-loading"
                    element-loading-background="rgba(0, 0, 0, 0.8)"
                    :data="list"
                    size="mini"
                    :height="height - 60 - 46 - 48"
                    style="width: 100%">
<!--                <el-table-column-->
<!--                        type="index"-->
<!--                        width="50">-->
<!--                </el-table-column>-->


                <template   v-for="(item ,index) in table_des">
                    <template   >
<!--                        <el-table-column
                                :key="index"
                                :sortable="item.sortable"
                                :prop="item.prop"
                                :label="item.label"
                                :filters="item.filters?item.filters.arr:false"
                                :filter-multiple="item.filters?item.filters.multiple:undefined"
                                :filter-method="filterHandler"
                                :width="item.width">-->


<!--                        <div  :key="index">-->
                            <el-table-column

                                    :key="index"
                                    :sortable="item.sortable"
                                    :prop="item.prop"
                                    :label="item.label"

                                    :width="item.width">
                                <template slot-scope="scope" >
                                    <div v-if="item.render"      >
                                        <!--                                    {{item.render(scope)}}-->
                                        <!--                                    <div v-html="item.render(scope)"></div>-->

                                        <exSlot  :render="item.render" :row="scope.row" :index="scope.$index" :column="item"></exSlot>
                                    </div>



                                    <div v-else>{{  scope.row[item.prop] }}</div>


                                </template>







                            </el-table-column>
<!--                        </div>-->


                    </template>



                </template>





            </el-table>

        </div>
        <div class="paging posi-fixed posi-fixed-bottom   align-right"  v-if="total">
            <el-pagination

                    @current-change="currentChange"
                    :pageSize="page_len"
                    ayout="prev, pager, next, jumper"
                    :current-page.sync="page_num"
                    :total="total">
            </el-pagination>
        </div>

    </div>


</template>

<script>
    var exSlot = {
        functional: true,
        props: {
            row: Object,
            render: Function,
            index: Number,

            column: {
                type: Object,
                default: null
            }
        },
        render: (h, data) => {
            // const params = {
            //     row: data.props.row,
            //     index: data.props.index
            // }
            // if (data.props.column) params.column = data.props.column
            // console.log(data)
            return data.props.render(h,data.props)
        }
    }
    import store from "@/store";
     export default {
        name: "hTable",
        data() {
            return {
                page_num:1
            }

        },
        components: {  exSlot },

        props: {
            /*数组列表*/
            list: {
                type: Array,
                default() {
                    return []
                },
            },

            /*表格描述
            *  label:显示的标题
            *  prop:对应列内容的字段名，也可以使用 property 属性
            *  width:显示的标题
            *
            *     ,{sortable:false,  label:'商户状态'  ,   prop:'state'      ,     width:'',
                        filters:[
                            { text: '待审', value: '0' },
                            { text: '审核通过', value: '1' },
                            { text: '作废', value: '-1' },
                        ]
                    }
                    ,{sortable:'custom',  label:'商户名称'    ,   prop:'name'      ,    width:''}
            * */
            table_des: {
                type: Array,
                default() {
                    return [{}]
                },
            },
            total: {//表格总条数
                type: Number,
                default: 0,
            },
            page_len: { //表格数据条数
                type: Number,
                default: 10,
            },
            currentPage: {
                type:Number,
                default:1
            },
            loading: { //表格加载中·
                type: Boolean,
                default: false,
            },


        },
        computed:{

            height:() => {
                return store.state.AppData.height;
            },

        },
        methods: {

            /*分页事件触发*/
            currentChange(index) {


                this.page_num=index;

                this.$emit('currentChange', index)

            },
            /*选中table行*/
            handleCurrentChange(re) {
                this.$emit('tableChoose', re)
            },
            /*排序事件*/
            sortChange(val) {


                /*排序规则*/
                let rules = {
                    ascending: 'asc',
                    descending: 'desc',
                };
                let type = val.order; //排序方式 正序 descending 大到小 反序ascending
                let prop = val.prop;//要排序的字段名
                if (type) {
                    this.$emit('sortChange', {field: prop, type: rules[type]})
                }


            },
            /*数据筛选*/
            filterHandler(val){
                // console.log(val)

            }

        },
        mounted() {
            // console.log(this.currentPage)
            this.page_num = this.currentPage

        },
        updated() {
            this.page_num = this.currentPage


        }
    }
</script>

<style scoped lang="less">
    .hTbale{
        position: relative;
    }
    @import "../css/common";
</style>