<template>
    <div class="mmTransaction">
        <headMenus
                @onSearch="onSearch"
                @onRefresh="onRefresh"

        ></headMenus>


        <hTable
                :list="list"
                :total="total"
                :table_des="table_des"
                :page_len="params.page_len"
                :currentPage="params.page_num"
                @currentChange="currentChange"
                @tableChoose="tableChoose"
                @sortChange="sortChange"
                :loading="loading"
        ></hTable>
        <formEditModal
                ref="formEditModal"
                title="搜索"
                :form_des="form_des"
                @sure="sureSearch"
        ></formEditModal>


    </div>


</template>

<script>
    import headMenus from '../../_components/headMenus'
    import hTable from '../../_components/hTable'
    import formEditModal from '../../_components/formEditModal'
    import tableFromMix from '../../_commom/mixins/tableFromMix'


    import coneHttpDeal from '../../_commom/connectorHttp/coneHttpDeal'
    import pageEvent from "../../_commom/lib/pageEvent";
    import coneHttpMember from "../../_commom/connectorHttp/coneHttpMember";
    import utils from "../../_commom/lib/utils";

    export default {
        name: "mmTransaction",
        mixins: [tableFromMix],

        components: {headMenus, hTable, formEditModal},
        data() {
            return {
                params: {

                    member_uuid: '',
                    order_field: null,
                    order_sort: '',
                    page_num: 1,
                    page_len: 20,

                },
                table_des: [
                    {sortable: false, label: '#', prop: '_index', width: '50'},
                    {
                        sortable: false, label: '交易类型', prop: 'trade_type', width: '',
                        render(h, {column, index, row}) {

                            let obj = utils.getFieldDesConf('trade', 'trade_type');
                            // console.log(obj)
                            // return obj[row.operate_state + ''];
                            return h('div', (obj[row.trade_type + '']||{}).name)

                        }
                    }
                    , {sortable: false, label: '交易码', prop: 'trade_code', width: ''}
                    , {sortable: false, label: '交易金额/分', prop: 'trade_money', width: ''}
                    ,
                    {
                        sortable: false, label: '交易状态', prop: 'trade_state', width: '',
                        render(h, {column, index, row}) {


                            let obj = utils.getFieldDesConf('trade', 'trade_state');

                            let item = obj[row.trade_state + ''];

                            return h('el-tag',
                                {
                                    attrs:{
                                        type:item.tag_type,
                                        size:'small'
                                    }
                                },
                                item.name
                            )

                        }
                    }
                    , {sortable: false, label: '交易备注', prop: 'remark', width: ''}
                    , {sortable: 'custom', label: '添加时间', prop: 'add_time', width: 'auto'}

                ],
                /*搜索字段*/
                form_des: [
                    // {
                    //     field: 'trade_type', type: 'radio', label: '最大可用金额', value: '',
                    //     options: [
                    //                     {name:'全部',value:''},
                    //                     {name:'等待交易',value:'0'},
                    //                     {name:'交易成功',value:'10'},
                    //                     {name:'交易失败',value:'-10'},
                    //
                    //                 ]
                    // },
                    {
                        field: 'member_uuid', type: 'searchSelect', label: '会员', value: "",
                        handler:'ManageMemberList', //远程事件
                        placeholder:'请输入搜索',
                        options: [
                            // {name:'苹果',value:'1'},
                            // {name:'香蕉',value:'2'}
                        ]

                    },

                ],

            }

        },
        methods: {
            /*获取数据列表*/
            getList(params1) {


                this.loading = true;

                let params =params1|| this.params;
                return new Promise((resolve, reject) => {
                    coneHttpDeal.ManageMemberTradeList(params)
                        .then(res => {
                            let data = res.data;


                            this.total = data.total / 1;
                            let rows = data.rows;

                            let page_len = params.page_len,
                                page_num = params.page_num;
                            let list = this._disposeList(rows, page_num, page_len);
                            this.loading = false;


                            // console.log('列表数据', this.list)
                            resolve(list)
                        })

                })
            },


            mapReady({ BMap, map }) {
                // 选择一个经纬度作为中心点
                this.point = new BMap.Point(113.27, 23.13);
                map.centerAndZoom(this.point, 12);
            },



            click(val){
                console.log(val)

            }


            ,getManageMemberList(val){

                coneHttpMember.getMemberList({name:val.value,page_num:1,page_len:utils.getConfig('select_page_len')})
                    .then(res=>{
                        let list =res.data.rows;
                        let options = list.map(v=>{
                            return {name:v.name,value:v.uuid}
                        })
                        utils.editObjField(val.from_des,{ field: 'member_uuid'}, {options});
                    })
            }


        },
        mounted() {
            this.getList()
                .then(list => {
                    this.list = list
                })
            // this.$refs.formEditModal.show()
            pageEvent.$off('ManageMemberList',null);
            pageEvent.$on('ManageMemberList',this.getManageMemberList);
        }
    }
</script>
<style lang="less" scoped>
    @import "../../css/common";
    .map {
        width: 800px;
        height: 800px;
    }
</style>