<template>
    <!--每个内容区的头部菜单-->
    <div class="headMenus">
        <div v-wechat-title="$route.meta.title">
            <!-- 菜单 -->
            <div style="height: 46px; line-height: 46px; overflow: hidden;">
                <el-row>
                    <el-col :span="6">
                        <div style="padding-left:16px;">
                            <i class="el-icon-s-unfold"></i>
                            <span style="padding-left:9px;">
                            {{$store.state.AdminData.active_title}}
                        </span>
                        </div>
                    </el-col>

                    <el-col :span="18">
                        <div style="text-align: right; ">
                            <el-link @click="setParentEvent('onRefresh')" class="menu">刷新</el-link>
                            <el-link @click="setParentEvent('onSearch')" class="menu">搜索</el-link>

                            <el-link
                                    class="menu"
                                    @click="onSubMenu(item)"
                                    v-for="(item,index) in $store.state.AdminData.right_menus"
                                    :key="index">
                                {{item.name}}
                            </el-link>
                        </div>
                    </el-col>
                </el-row>
            </div>




        </div>
    </div>


</template>

<script>
    import store from "@/store";
    console.log(store)
    import util from "@/util.js";
    import lime from "@/lime.js";

    console.log('lime',lime)
    export default {
        name: "headMenus",
        data() {
            return {
                state:store
            }

        },

        methods: {
            /*触发父级对应事件 刷新 搜索*/
            setParentEvent(even,data={}){
                this.$emit(even)
            },

            /*点击菜单*/
            onSubMenu(item){
                console.info('当前菜单权限',item,item.font_url)
                // return false;
                util.submenu(item,this,lime.cookie_get('login_token'), false);
            },

            /*添加 方法名 后台返回*/
            handleAdd(){
                this.$emit('handleAdd',{})
            } ,
            /*删除*/
            handleDel(){
                this.$emit('handleDel',{})
            } ,
            /*编辑*/
            handleEdit(){
                this.$emit('handleEdit',{})
            } ,
            /*查*/
            handleDetail(){
                this.$emit('handleDetail',{})
            },

            /*商户模块 创建超级管理员*/
            handleAddSup(){
                this.$emit('handleAddSup',{})

            },
            /*商户模块 创建超级管理员*/
            handleExamine(){
                this.$emit('handleExamine',{})
            }   ,
            handleReset(){
                this.$emit('handleReset',{})
            }
        }
    }
</script>

<style scoped lang="less">
    .menu{
        display: inline-block;
        padding:0 16px;
        text-align: center;
    }

</style>