import lime from "../../../../lime";

import qs from 'qs'
console.log(qs,'---------------------------------------')
import util from "../lib/utils";




export default class Http {

    constructor() {

    }
    static loading  = null;

    /*结束loading*/
    static endLoad(){
        // this.loading.close();
    }

    static common_header = {
        // 'Content-Type' : 'application/x-www-form-urlencoded;charset=UTF-8'
    }; //公共的请求头部
    static common_data = {
        login_token:lime.cookie_get('login_token'),
        // 'Content-Type':'application/x-www-form-urlencoded'
    };   //公共的请求参数
    /**
     * @description 处理参数函数 对象的值为空时 会过滤掉
     * @param  params 传入的对象参数
     * @return Object 处理后的参数
     * */
    static _disposeParams(params = {}) {
        let new_params = {};
        for (let i in params) {

            let item = params[i];
            if (!(item === '' || item === undefined || item === null)) {
                if (typeof item ==="number" ){
                    item = item+''
                }
                new_params[i] = item;
            }

        }
        return new_params

    }


    /**
     * @description 对请求的封装加了错误处理
     * @param _module 请求模块
     * @param params 请求参数
     * @param headers 请求头部
     * */
    static httpPost(_module, params={}, headers = {}) {





        params = this._disposeParams(params);
        Object.assign(params,this.common_data);
        Object.assign(headers,this.common_header);
        console.info('请求信息', _module, params  , headers  )
        // console.info('请求信息', _module,qs.stringify(params)   , headers  )
        return new Promise((resolve, reject) => {
            // params = qs.stringify(params);
            // console.log('params',params)
            lime.req(_module, params, headers)
                .then(res => {
                    console.log('请求成功',res)
                    resolve(res)

                    this.endLoad()
                })
                .catch(err=>{
                    console.log('请求失败',err)


                    util.Message.error(JSON.stringify(err))
                    reject(err);

                })
        })



    }


}