/*
    计划模板
*/
<template>
    <div class="template">
        <!-- 菜单操作 -->
        <div style="height: 46px; line-height: 46px; overflow: hidden;">
            <el-row>
                <el-col :span="6">
                    <div style="padding-left:16px;">
                        <i class="el-icon-s-unfold"></i>
                        <span style="padding-left:9px;">
                            {{$store.state.AdminData.active_title}}
                        </span>
                    </div>
                </el-col>

                <el-col :span="18">
                    <div style="text-align: right; ">
                        <el-link @click="onSubMenu('onRefresh',true)" class="menu">刷新</el-link>
                        <el-link @click="onSubMenu('onSearch',true)" class="menu">搜索</el-link>

                        <el-link
                            class="menu" 
                            @click="onSubMenu(item)"
                            v-for="(item,index) in $store.state.AdminData.right_menus" 
                            :key="index">
                            {{item.name}}
                        </el-link>
                    </div>
                </el-col>
            </el-row>
        </div>
        <!-- 计划模板列表 -->
        <div style="border-top: solid 1px #f2f1f4;">
            <el-table 
                :data="rows"
                :height="height - 60 - 46 - 48"
                v-loading="loading"
                element-loading-text="拼命加载中"
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                @sort-change="onSortChange"
                :highlight-current-row="true"
                @current-change="onSelectRow"
                style="width: 100%" 
                size="mini">
                
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="real_name" label="姓名"></el-table-column>
                <el-table-column prop="mobile" label="登录手机"></el-table-column>
                <el-table-column prop="role_name" label="角色">
                    <template slot-scope="scope">
                        {{scope.row.role_name == '' ? '未设置' : scope.row.role_name}}
                    </template>
                </el-table-column>
                <el-table-column prop="add_time" label="添加时间" :sortable=true></el-table-column>
                <el-table-column 
                    prop="last_time" 
                    label="最近登录时间" 
                    align="center"
                    :sortable=true>
                    <template slot-scope="scope">
                        {{scope.row.last_time == 0 ? '还未登陆' : scope.row.last_time}}
                    </template>
                </el-table-column>
                <el-table-column prop="last_ip" label="最近登录IP" align="center">
                    <template slot-scope="scope">
                        {{scope.row.last_ip == 0 ? '-' : scope.row.last_ip}}
                    </template>
                </el-table-column>
            </el-table>
            
            <div class="page" :style="{width:width - 250 + 'px'}">
                <el-pagination
                    :current-page.sync="SearchFormData.page_num"
                    @current-change="onPageChange"
                    layout="prev, pager, next, jumper"
                    :total="total">
                </el-pagination>
            </div>
        </div>
        <!-- 菜单操作弹框 -->
        <!-- 搜索弹框 -->
        <el-dialog
            title="搜索"
            :visible.sync="search_show"
            width=400px>
            <el-form :model="SearchFormData" label-width="80px" size="small">
                <el-form-item label="">
                    <el-input v-model="SearchFormData"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="search_show = false" size="small">取 消</el-button>
                <el-button type="primary" @click="onSearchSubmit" size="small">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 编辑弹框 -->
        <el-dialog
            title="编辑"
            :visible.sync="edit_show"
            width=400px>
            <el-form :model="EditFormData" label-width="80px" size="small">
                <el-form-item label="">
                    <el-input v-model="EditFormData"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="EditFormData = false" size="small">取 消</el-button>
                <el-button type="primary" @click="onEditSubmit" size="small">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 添加弹框 -->
        <el-dialog
            title="添加"
            :visible.sync="add_show"
            width=400px>
            <el-form :model="AddFormData" rules="AddFormRule" label-width="80px" size="small">
                <el-form-item label="" prop="">
                    <el-input v-model="AddFormData"/>
                </el-form-item>
            </el-form>

            <span slot="footer">
                <el-button @click="AddFormData = false" size="small">取 消</el-button>
                <el-button type="primary" @click="onAddSubmit" size="small">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 详情弹框 -->
        <el-dialog
            title="详情"
            :visible.sync="detail_show"
            width=750px>
            <ul class="plann-template-ul">
                <li>
                    <p><span>计划名称：</span><span>{{}}</span></p>
                    <p><span>计划名称：</span><span>{{}}</span></p>
                </li>
                <li>
                    <p><span>计划名称：</span><span>{{}}</span></p>
                    <p><span>计划名称：</span><span>{{}}</span></p>
                </li>
                <li>
                    <p><span>计划名称：</span><span>{{}}</span></p>
                    <p><span>计划名称：</span><span>{{}}</span></p>
                </li>
                <li>
                    <p><span>计划名称：</span><span>{{}}</span></p>
                    <p><span>计划名称：</span><span>{{}}</span></p>
                </li>
            </ul>

            <span slot="footer">
                <!-- <el-button @click="DetailFormData = false" size="small">取 消</el-button> -->
                <el-button type="primary" @click="DetailFormData = false" size="small">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import store from '@/store';
    export default {
        data () {
            return {
                // 计划模板列表数据初始化
                rows:[],
                total:0,
                loading:false,
                curr_row:null,
                // 菜单操作
                // 搜索
                search_show: false,
                SearchFormData: {
                    page_num: 1,//当前页 默认第一页
                    page_len: 10,//每页数据条数
                    order_field:'add_time',//时间排序
                    order_sort:'desc'//排序规则
                },
                // 编辑
                edit_show: false,
                EditFormData: {

                },
                // 添加
                add_show: false,
                AddFormData: {

                },
                AddFormRule: {

                },
                // 详情
                detail_show: false
            }
        },
        created () {

        },
        computed:{
            width:() => {
                return store.state.AppData.width;
            },
            height:() => {
                return store.state.AppData.height;
            },
            upload_url:() => {
                return store.state.AppData.upload_url;
            },
            img_host:() => {
                return store.state.AppData.img_host;
            }
        },
        methods: {
            // 计划模板列表方法
            // 获取计划模板列表数据方法 init
            init () {

            },
            // 排序
            onSortChange (column) {
                this.SearchFormData.order_field = column.prop;
                if (column.order == 'ascending') {
                    this.SearchFormData.order_sort  = 'asc';
                } else {
                    this.SearchFormData.order_sort  = 'desc';
                }
                this.init();
            },
            // 选择行
            onSelectRow (row) {
                this.curr_row = row;
            },
            // 分页
            onPageChange (page) {
                this.SearchFormData.page_num = page;
            },

            // 菜单操作方法
            // 刷新
            onRefresh () {
                this.init();
            },
            // 搜索弹框显示
            onSearch () {
                this.search_show = true;
            },
            // 搜索弹框提交
            onSearchSubmit () {
                
            },
            // 编辑弹框显示
            // 编辑弹框提交
            onEditSubmit () {
                
            },
            // 添加弹框显示
            // 添加弹框提交
            onAddSubmit () {
                
            },
            // 删除
            handleDel () {
                if (util.empty(this.curr_row)) {
                    this.$message.error('请选择一条数据');
                    return;
                }
                this.$confirm('确定删除？','提示').then(() => {
                    // 调用删除接口
                })
            }
        }
    }
</script>

<style lang="less">
    .template{
        .menu{
            display: inline-block;
            padding:0 16px;
            text-align: center;
        }
        .page {
            height: 40px; 
            line-height: 40px; 
            text-align: right;
            position: fixed;
            bottom: 0;
            right:0;
            overflow: hidden;
        }
        .plann-template-ul{
            list-style: none;
            li{
                line-height: 40px;
                overflow: hidden;
                p:nth-of-type(1){
                    float: left;
                }
                p:nth-of-type(2){
                    float: right;
                }
                p{
                    span:nth-of-type(1){
                        width: 150px;
                        text-align: right;
                        display: inline-block;
                    }
                    span:nth-of-type(2){
                        width: 250px;
                        text-align: right;
                        display: inline-block;
                    }
                }
            }
        }
    }
</style>