/**
 * @description 所有会员相关的接口
 * @author hxq
 * */
import  Http from '../http/Http'
export default {
    /**
     * @description 获取会员列表
     * @param params.login_token required  (所有接口都要)
     * @param params.mobile   手机号
     * @param params.name   用户名
     * @param params.order_field  排序字段 add_time,last_time
     * @param params.order_sort  排序方法 asc desc
     * @param params.page_len  当前页码
     * @param params.page_num 每页条数
     * @return Promise
     * */
    getMemberList(params){
        return new Promise((resolve, reject) => {
            Http.httpPost('ManageMemberList',params)
                .then(res => {
                    resolve(res)

                })
                .catch(err=>{
                    reject(err)
                })
        })

    }  ,
    /**
     * @description 获取会员账户列表
     * @param params.login_token required  (所有接口都要)
     * @param params.mobile   手机号
     * @param params.name   用户名
     * @param params.order_field  排序字段 add_time,last_time
     * @param params.order_sort  排序方法 asc desc
     * @param params.page_len  当前页码
     * @param params.page_num 每页条数
     * @return Promise
     * */
    getMemberAccountList(params){
        return new Promise((resolve, reject) => {
            Http.httpPost('ManageMemberAccountList',params)
                .then(res => {
                    resolve(res)

                })
                .catch(err=>{
                    reject(err)
                })
        })

    }
    /*获取会员账户详情*/
    ,MemberAccountDetail(params){
        return new Promise((resolve, reject) => {
            Http.httpPost('MemberAccountDetail',params)
                .then(res => {
                    resolve(res)

                })
                .catch(err=>{
                    reject(err)
                })
        })

    }
}