<template>
    <div class="member_list h100 posi-relative">
        <headMenus
                @onSearch="onSearch"
                @onRefresh="onRefresh"
        ></headMenus>

        <hTable
                :list="list"
                :total="total"
                :table_des="table_des"
                :page_len="params.page_len"
                :currentPage="params.page_num"
                @currentChange="currentChange"
                @tableChoose="tableChoose"
                @sortChange="sortChange"
                :loading="loading"
        ></hTable>
        <formEditModal
                ref="formEditModal"
                title="搜索"
                :form_des="form_des"
                @sure="sureSearch"
        ></formEditModal>

    </div>


</template>

<script>
    import headMenus from '../../_components/headMenus'
    import hTable from '../../_components/hTable'
    import formEditModal from '../../_components/formEditModal'


    import coneHttpMember from '../../_commom/connectorHttp/coneHttpMember'
    import tableFromMix from '../../_commom/mixins/tableFromMix'
    import utils from "../../_commom/lib/utils";


    export default {
        name: "member_list",
        mixins: [tableFromMix],

        components: {headMenus,hTable,formEditModal},
        data() {

            return {
                params: {

                    name: undefined,
                    mobile: '',
                    order_field: null,
                    order_sort: '',
                    page_num: 1,
                    page_len:20,

                },
                table_des:[
                    {sortable: false, label: '#', prop: '_list_index', width: '50'}
                     // {sortable:false,  label:'用户id'  ,   prop:'uuid'      ,     width:''}
                    ,{sortable:false,  label:'用户名'    ,   prop:'name'      ,    width:''}
                    ,{sortable:false,  label:'角色名称'    ,   prop:'role_name'      ,    width:''}
                    ,{sortable:false,  label:'手机号'    ,   prop:'login_mobile'      ,    width:''}
                    ,{sortable:'custom' ,  label:'添加时间',   prop:'add_time'  ,    width:'auto'}

                    ],
                form_des:[
                    {field:'name',type:'text',label:'会员名称',value:''},

                    {field:'mobile',type:'tel',label:'手机号',value:'',
                        rules: [
                            {trigger: 'blur',   validator: utils.telCheck}
                        ]
                    },

                ],
                list:[],
                total:0,
                loading:true
            }

        },
        methods: {


            /*获取数据列表*/
            getList(params1) {


                this.loading = true;

                let params =params1|| this.params;
                return new Promise((resolve, reject) => {
                    coneHttpMember.getMemberList(params)
                        .then(res => {
                            let data = res.data;


                            this.total = data.total / 1;
                            let rows = data.rows;

                            let page_len = params.page_len,
                                page_num = params.page_num;
                            let list = this._disposeList(rows, page_num, page_len);
                            this.loading = false;


                            // console.log('列表数据', this.list)
                            resolve(list)
                        })

                })
            },




        },
        mounted() {
            this.getList()
            // this.$refs.formEditModal.show()
        }
    }
</script>

<style scoped lang="less">

    @import "../../css/common";

</style>