<template>
    <div class="hForm">
        <div class="wrap">
            <el-form
                    size="small"
                    ref="form"
                    :inline="false"

                    :label-position="'right'"
                    :model="getForm(form_des)"
                    :rules="getRules(form_des)"
                    label-width="auto">


                <el-form-item
                        v-for="(item,index) in form_des"
                        :key = "index"
                        :prop="item.field"
                        :label="item.label">
                    <!--{{item.field}}-->
                    <!--文本输入框-->
                    <el-input v-if="item.type==='text'" v-model="item.value" :placeholder="item.placeholder"></el-input>
                    <el-input v-if="item.type==='textarea'" type="textarea" v-model="item.value" :placeholder="item.placeholder"></el-input>

                    <!--手机号码-->
                    <template v-if="item.type==='tel'">
                        <hElInput @inputChange="(val)=>{
                            inputChange(form_des,item.field,val)
                        }" :type="item.type" :placeholder="item.placeholder" :value="item.value"></hElInput>
                    </template>
                    <!--富文本编辑器-->
                    <template v-if="item.type==='editor'">
                        <hwangeditor @editorChange="(val)=>{
                             inputChange(form_des,item.field,val)
                        }" :value="item.value"></hwangeditor>
                    </template>
                    <!--密码框-->
                    <el-input v-if="item.type==='password'" type="password" show-password v-model="item.value" :placeholder="item.placeholder"></el-input>
                     <!--数字框-->
                     <template v-if="item.type==='number'">
                        <hElInput @inputChange="(val)=>{
                            inputChange(form_des,item.field,val)
                        }" :type="item.type" :placeholder="item.placeholder" :value="item.value"></hElInput>
                    </template>
                    <template v-if="item.type==='number_positive'">
                        <hElInput @inputChange="(val)=>{
                            inputChange(form_des,item.field,val)
                        }" :type="item.type" :placeholder="item.placeholder" :value="item.value"></hElInput>
                    </template>
                     <!--下拉选择-->
                    <el-select v-if="item.type==='select'"
                               v-model="item.value"
                               :placeholder="item.placeholder">
                        <el-option
                                v-for="(it,ind) in item.options" :key="ind"
                                :label="it.name" :value="it.value"></el-option>

                    </el-select>
                    <!--上传-->
                    <template v-if="item.type==='upload'">


                        <hUpload @uploadSuccess="uploadSuccess" :value="item.value"></hUpload>
                    </template>


                    <!--搜索的下拉选择-->
                    <el-select v-if="item.type==='searchSelect'"
                               @change="selectChange"
                               v-model="item.value"

                               filterable
                               remote
                               reserve-keyword

                               @focus="searchSelectFocus(item.handler)"
                               :remote-method="(val)=>{

                                   remoteSearch(val,item.handler)
                               }"
                               :loading="false"
                               :placeholder="item.placeholder">
                        <el-option
                                v-for="(it,ind) in item.options" :key="ind"
                                :label="it.name" :value="it.value"></el-option>

                    </el-select>
                    <!--带选择和搜索的下拉选择-->
                    <el-select v-if="item.type==='searchChooseSelect'"
                               v-model="item.value"
                               multiple
                               filterable
                               remote
                               :reserve-keyword="false"
                               :remote-method="(val)=>{
                                   remoteEvent(val,item.handler)
                               }"
                               :placeholder="item.placeholder">
                        <el-option
                                v-for="(it,ind) in item.options"
                                :key="ind"
                                :label="it.label"
                                :value="it.value">
                        </el-option>
                    </el-select>
                    <!--单选-->
                    <template v-if="item.type==='radio'">
                        <el-radio v-model="item.value"
                                  :label="it.value"
                                  :key="ind"
                                  v-for="(it,ind) in item.options">{{it.name}}</el-radio>

                    </template>

                    <!--多选-->
                    <template v-if="item.type==='checkbox'">
                        <div id="loadmore-tags-wrap "  v-infinite-scroll="()=>{

                             remoteEvent('',item.handler)
                        }" class="checkbox  posi-relative">
                            <el-checkbox-group class="checkgroup" v-model="item.value" @change="checkboxChange">

                                <el-checkbox :label="it.value" border size="small"  v-for="(it,ind) in item.options" :key="ind">{{it.label}}</el-checkbox>
                                <!--                            <el-checkbox v-for="city in cityOptions" :label="city" :key="city">{{city}}</el-checkbox>-->
                            </el-checkbox-group>

<!--                            <div  v-if="item.options.length" class="align-center loadmore-tags posi-relative" @click="()=>{-->

<!--                            remoteEvent(item.value,item.handler) ;-->
<!--                        }">加载更多</div>-->
                        </div>



                    </template>

                    <!--时间范围-->
                    <template v-if="item.type==='datetimeRange'">
                        <el-date-picker
                                @change="datetimeRangeChange"
                                v-model="datetime_range"
                                type="datetimerange"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期">
                        </el-date-picker>

                    </template>
                    <!--地图-->
                    <template v-if="item.type==='map'">

                        <div class="map">
                            <hMap @pointChange="pointChange" :value="getMapVal()"></hMap>
                        </div>

                    </template>
                    <!--地址选择-->
                    <template v-if="item.type==='address'">


                        <areaChoose @areaChange="areaChange" :value="getAreaVal()"></areaChoose>


                    </template>
                </el-form-item>



            </el-form>
        </div>

        <div class="align-right">
            <el-button @click="reset">取 消</el-button>
            <el-button type="primary" @click="formSure('form')">确 定</el-button>
        </div>

    </div>


</template>

<script>
    import hMap from '../_components/hMap'
    import util from "../../../util";
    import areaChoose from '../_components/areaChoose'
    import hUpload from '../_components/hUpload'
    import pageEvent from "../_commom/lib/pageEvent";

    import hElInput from './form/hElInput'
    import hwangeditor from './form/wangeditor'
    export default {
        name: "hForm",
        data() {
            return {
                checkList: ['选中且禁用','复选框 A'],
                // cityOptions : ['上海', '北京', '广州', '深圳'],
                datetime_range:'',//时间
                map_value:{lng: 116.404, lat: 39.915}//地图左边
                // form_des:[
                //     {field:'name',type:'input',label:'姓名',value:'hxq'},
                //     {field:'food',type:'select',label:'食物',value:'1',options:
                //             [
                //                 {name:'苹果',value:'1'},
                //                 {name:'香蕉',value:'2'}
                //             ]
                //     }
                // ],

            }

        },
        components:{hMap,areaChoose,hUpload,hElInput,hwangeditor},
        props:{
            form_des:{
                type:Array,
                default(){
                    return [];
                }
            }
        },
        methods: {
            /*获取省市区初始化值*/
            getAreaVal(){
                let re = []
                let params = this.getForm(this.form_des)


                re[0] = params.prov_uuid||'';
                re[1] = params.city_uuid||'';
                re[2] = params.area_uuid||'';
                console.log('getAreaVal',re)
                return re;
            },
            /*获取地图初始化值*/
            getMapVal(){
                let re = {}
                let params = this.getForm(this.form_des)


                re.lng = params.lng||'116.403924';
                re.lat = params.lat||'39.915067';
                // console.log('getMapVal',re);
                return re;
            },
            /*处理数据格式 将表单描叙字段转换
      *   form: {name: '111',age: 18, }
      * */
            getForm(data){
                let re = {};
                data.forEach((v)=>{
                    let field_arr= v.field_arr;
                    let field_type= v.field_type;
                    if(field_arr && field_type==='composite'){ //多个字段一组

                        field_arr.forEach((v1 ,k1)=>{
                            re[v1.field] =  v1.value;

                        })


                    }else {
                        re[v.field] = v.value;
                    }

                });
                // console.log('getForm',re)
                return re;

            },
            /*将表单描叙字段转 获取规则*/
            getRules(data){
                // console.log('--------------------------');
                // console.log(data)
                let re = {};
                data.forEach((v)=>{
                    let field_type= v.field_type;
                    if(v.field_arr && field_type==='composite'){ //多个字段的校验

                        re[v.field] = v.field_arr[0].rules
                    }else {
                        if(v.rules){
                            re[v.field] = v.rules;
                        }

                    }

                });
                // console.log('getRules',re)
                return re;
            },
            /*重置*/
            reset(){
                this.$emit('cancel',this.form_des)
            },
            /*确定*/
            formSure(formName){
                this.$refs[formName].validate((valid) => {

                    let form_des = this.form_des;
                    let form_data = this.getForm(form_des);
                    let form_rules = this.getRules(form_des);

                    if (valid) {//数据校验通过
                        this.$emit('sure',{form_des,form_data,form_rules});
                    } else {
                        console.log('error submit!!');

                    }
                });

            },
            /*给表单描叙添加默认值*/
            addDataFrom(form_des, data){
                for(let i = 0,len=form_des.length;i<len;i++){
                    let field = form_des[i].field;
                    let field_type = form_des[i].field_type;

                    if( field_type==='composite'){
                        if(data[field] !==undefined){
                            form_des[i].value = data[field];
                        }
                        let field_arr = form_des[i].field_arr;


                        for(let j = 0,len=field_arr.length;j<len;j++){


                            let field1 = field_arr[j].field;
                            if(data[field1]!==undefined){
                                field_arr[j].value=data[field1]
                            }



                        }
                    }else {
                        if(data[field] !==undefined){
                            form_des[i].value = data[field];
                        }
                    }

                }


            },
            /*时间范围修改*/
            datetimeRangeChange(val){


                let start_time =  util.dateString(val[0],'yyyy-MM-dd HH:mm:ss');
                let end_time =  util.dateString(val[0],'yyyy-MM-dd HH:mm:ss');

                this.addDataFrom(this.form_des,{start_time,end_time});


                // this.$emit('datetimeRangeChange',[start_time,end_time])
            },
            /*坐标点修改*/
            pointChange(val){
                console.log(val)
                let lng=val.lng;
                let lat=val.lat;

                let point = {lng,lat};
                this.map_value=point
                this.addDataFrom(this.form_des,point);

            },

            /*选择位置*/
            areaChange(val){
                let prov_uuid = val[0]||'';
                let city_uuid = val[1]||'';
                let area_uuid = val[2]||'';

                this.addDataFrom(this.form_des,{prov_uuid,city_uuid,area_uuid});
            },
            // 文件上传成功
            uploadSuccess(val){
                pageEvent.$emit('uploadFaceSuccess',{value:val,from_des:this.form_des})

            },
            selectChange(val){
                console.log(val)

            },
            /*表单远程搜索*/
            remoteSearch(val,handler='getShopList'){

                console.log(handler)
                // return false;
                pageEvent.$emit(handler,{value:val,from_des:this.form_des})


            },
            searchSelectFocus(handler='getShopList'){


                console.log(handler)
                this.remoteSearch('',handler)
            },

            /*多选*/
            checkboxChange(val){
                console.log(val)

            },
            /*input 输入框值改变时*/
            inputChange(form_des,field,val){


                // console.log(form_des,field,val)
                let obj = {};
                obj[field] =val;
                this.addDataFrom(this.form_des,obj);
            },


            loadMore(){
              console.log('more')
            },
            /*跨页面事件绑定 绑定事件 在绑定handler的页面调用*/
            remoteEvent(val,handler){
                if(handler){
                    pageEvent.$emit(handler,{value:val,from_des:this.form_des});
                }
            }
        },
        mounted() {

        }
    }
</script>

<style scoped lang="less">

    @import "../css/common";
    .map{
        width: 100%;
        height: 400px;

    }
    .wrap{
        max-height: 100vh - 20px;
        overflow:auto ;
    }
    .el-input-number{
        width: 100%!important;
        text-align: left;

    }


    .checkgroup{
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;
        .el-checkbox{
            margin-left: 0!important;
            margin-right: 0!important;
            margin-bottom: 10px;
            /*width: 26%;*/

        }
        .el-checkbox__label{
            overflow: hidden!important;/*超出部分隐藏*/
            text-overflow:ellipsis!important;/* 超出部分显示省略号 */
            white-space: nowrap!important;/*规定段落中的文本不进行换行 */
        }
    }
</style>
<style >
    .el-input-number .el-input__inner{
        text-align: left!important;
    }
    .checkbox{
        max-height: 250px;
        overflow: auto;
    }
</style>