<!--提交弹窗弹出层模块-->

<template>
    <div class="formEditMadal" v-if="is_show">
        <el-dialog
                :title="title"
                :visible.sync="is_show"
                :width="width"
        >



            <div class="con">
                <hForm :form_des="form_des"
                       @sure="sure"
                       @cancel="cancel"
                       @pointChange="pointChange"
                       @datetimeRangeChange="datetimeRangeChange"></hForm>
            </div>
<!--            <span slot="footer" class="dialog-footer">-->
<!--                <el-button @click="cancel">取 消</el-button>-->
<!--                <el-button type="primary" @click="sure">确 定</el-button>-->
<!--            </span>-->
        </el-dialog>
    </div>


</template>

<script>
    import hForm from './hForm'
    export default {
        name: "formEditMadal",

        components:{hForm},
        data() {
            return {
                is_show: false,


            }

        },
        props: {
            title: {
                type: String,
                default: '标题'
            },
            width: {
                default: '30%'
            },
            form_des:{
                type:Array,
                default(){
                    return [];
                }
            }
        },
        methods: {

            /*展现*/
            show() {
                this.is_show = true;
            },
            /*隐藏*/
            hide() {
                this.is_show = false;
            },
            /*取消*/
            cancel() {
                this.hide()

            },
            /*确认*/
            sure(val) {

                this.hide();

                this.$emit('sure',val)

            },
            pointChange(val){


                this.$emit('pointChange',val)

            },
            /*时间选择*/

            datetimeRangeChange(val){
                // console.log(this.form_des)
                // this.$emit('datetimeRangeChange',val)
            }
        }
    }
</script>

