<template>
    <!--标签分组-->
    <div>
        <headMenus
                @onSearch="onSearch"
                @onRefresh="onRefresh"
                @handleAdd="handleAdd"
                @handleEdit="handleEdit"
                @handleDel="handleDel"
        ></headMenus>
        <hTable
                :list="list"
                :total="total"
                :table_des="table_des"
                :page_len="params.page_len"
                :currentPage="params.page_num"
                @currentChange="currentChange"
                @tableChoose="tableChoose"
                @sortChange="sortChange"
                :loading="loading"
        ></hTable>
        <formEditModal
                ref="formEditModal"
                title="搜索"
                :form_des="form_des"
                @sure="sureSearch"
        ></formEditModal>
        <formEditModal
                ref="addTags"
                title="添加"
                width="50%"
                :form_des="add_tags_from_des"
                @sure="sureAddTags"
        ></formEditModal>
        <formEditModal

                ref="editTags"
                title="编辑"
                width="50%"
                :form_des="edit_tags_from_des"
                @sure="sureEditTags"
        ></formEditModal>
    </div>


</template>

<script>
    import coneHttpTags from '../../_commom/connectorHttp/coneHttpTags'
    import headMenus from '../../_components/headMenus'
    import hTable from '../../_components/hTable'
    import formEditModal from '../../_components/formEditModal'
    import tableFromMix from '../../_commom/mixins/tableFromMix'
    import utils from "../../_commom/lib/utils";
    import pageEvent from "../../_commom/lib/pageEvent";

    export default {
        name: "tagGroup",
        mixins: [tableFromMix],
        components: {headMenus, hTable, formEditModal},
        data() {
            return {
                params: {


                    title: '',
                    order_field: null,
                    order_sort: '',
                    page_num: 1,
                    page_len: 20,

                },
                /*标签请求数据*/
                tags_params: {
                    page_num: 1,
                    page_len: 50,

                },
                /*标签列表*/
                tags_list:[],
                table_des: [
                    {sortable: false, label: '#', prop: '_list_index', width: '50'}
                    // {sortable:false,  label:'用户id'  ,   prop:'uuid'      ,     width:''}
                    , {sortable: false, label: '标签组名', prop: 'title', width: ''}
                    , {
                        sortable: false, label: '标签', prop: 'tag', width: '',

                        // render(scope) {
                        //
                        //     let row = scope.row;
                        //     let tag = row.tag;
                        //
                        //     let tags = tag.map(v=>{
                        //         return v.title
                        //     })
                        //     // return String(tags)
                        //     let str='';
                        //     if(tags.length){
                        //         str= '['+tags.join(' ] [')+']'
                        //     }else {
                        //         str='——'
                        //     }
                        //
                        //     return str
                        // }}
                        render: (h, {column, index, row}) => {

                            let tag = row.tag;
                            let hs=[]

                            tag.map(v=>{
                                hs.push(
                                    h('el-tag',
                                        {
                                            attrs:{
                                                size:'small'
                                            }
                                            ,
                                            style:{
                                                margin:'2px'
                                            }
                                        },
                                        v.title)
                                )

                            })


                            return h(    // {String | Object | Function}
                                // 一个 HTML 标签名、组件选项对象，或者
                                // resolve 了上述任何一种的一个 async 函数。必填项。
                                'div',

                                // {Object}
                                // 一个与模板中 attribute 对应的数据对象。可选。
                                {

                                },

                                // {String | Array}
                                // 子级虚拟节点 (VNodes)，由 `createElement()` 构建而成，
                                // 也可以使用字符串来生成“文本虚拟节点”。可选。
                                hs

                            )
                        }
                    }
                    , {sortable: false, label: '备注', prop: 'remark', width: ''}


                ],
                form_des: [
                    {field: 'title', type: 'text', label: '标签组名', value: '',},
                ],
                /*添加表单描叙*/
                add_tags_from_des: [
                    {
                        field: 'title', type: 'text', label: '标签组名', value: '',
                        rules: [
                            {trigger: 'blur', required: true, message: '标签组名不可为空'}
                        ]
                    },
                    {
                        field: 'tag_uuids', type: 'checkbox', label: '标签组名', value: [],
                        handler:'loadMoreTags',placeholder:'请输入搜索',
                        options:[

                        ]

                    },
                    {field: 'remark', type: 'textarea', label: '备注', value: '',}
                ],
                edit_tags_from_des: [
                    {
                        field: 'title', type: 'text', label: '标签组名', value: '',
                        rules: [
                            {trigger: 'blur', required: true, message: '标签组名不可为空'}
                        ]
                    },
                    {
                        field: 'tag_uuids', type: 'checkbox', label: '标签组名', value: [],
                        handler:'loadMoreTags',placeholder:'请输入搜索',
                        options:[

                        ]

                    },
                    {field: 'remark', type: 'textarea', label: '备注', value: '',}
                ]
            }

        },
        methods: {
            /*获取数据列表*/
            getList(params1) {


                this.loading = true;

                let params =params1|| this.params;
                return new Promise((resolve, reject) => {
                    coneHttpTags.ManageQuesGroupList(params)
                        .then(res => {
                            let data = res.data;


                            this.total = data.total / 1;
                            let rows = data.rows;

                            let page_len = params.page_len,
                                page_num = params.page_num;
                            let list = this._disposeList(rows, page_num, page_len);
                            this.loading = false;


                            // console.log('列表数据', this.list)
                            resolve(list)
                        })

                })
            },


            handleAdd() {
                this.$refs.addTags.show()

                if(!this.tags_list.length){

                    this.initTags()
                }
            },
            /*确认添加*/
            sureAddTags(val) {

                let form_data = val.form_data;
                coneHttpTags.ManageQuesGroupAdd(form_data)
                    .then(res => {
                        this.$message({
                            message: '添加成功',
                            type: 'success'
                        });
                        this.getList()
                            .then(list => {
                                this.list = list
                            })
                    })

            },
            /*编辑*/
            handleEdit() {
                let curr_list_one = this.curr_list_one || {};//选中的数据

                let re = this.checkChooseListOne();
                if (!re) {
                    return false;
                }
                // title: "1556"
                // uuid: "4d769c3cea7bc3161fc6efcd77a3d846"
                let tag_uuids = curr_list_one.tag.map(v => {
                    return v.uuid;
                });
                curr_list_one.tag_uuids = tag_uuids;
                this.addDataFrom( //初始化表单描叙默认值
                    this.edit_tags_from_des,
                    curr_list_one
                );

                this.$refs.editTags.show()
                if(!this.tags_list.length){

                    this.initTags()
                }

            },
            /*编辑确认*/
            sureEditTags(val) {
                let curr_list_one = this.curr_list_one;
                let uuid = curr_list_one.uuid;
                let form_data = val.form_data;
                coneHttpTags.ManageQuesGroupEdit({...form_data, uuid})
                    .then(res => {
                        this.$message({
                            message: '修改成功',
                            type: 'success'
                        });

                        this. onRefreshIndex(curr_list_one._index-1)
                    })
            },
            /*删除*/
            handleDel() {
                //删除数据之前要选择数据
                let curr_list_one = this.curr_list_one || {};//选中的数据
                let re = this.checkChooseListOne();
                if (!re) {
                    return false;
                }

                this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    coneHttpTags.ManageQuesGroupDel({uuid: curr_list_one.uuid})
                        .then(res => {
                            this.list.splice(curr_list_one._index - 1, 1)
                            this.$message({
                                type: 'success',
                                message: '删除成功!'
                            });
                        })

                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });

            }
            ,
            /*获取标签列表*/
            getTags(title='') {

                let tags_params = this.tags_params
                // coneHttpTags.ManageQuesTagList({title,page_len: utils.getConfig('select_page_len')})


                if(this.tags_list.length< ((tags_params.page_num-1)*tags_params.page_len)){
                    return false;
                }
                coneHttpTags.ManageQuesTagList(tags_params)
                    .then(res => {
                        let data = res.data;
                        let rows = data.rows;
                        rows = rows.map(v => {
                            return {...v, label: v.title, value: v.uuid}
                        });


                        let tags_list = [ ...this.tags_list,...rows];
                        // let tags_list = [ ...rows];
                        this.tags_list = tags_list;



                        utils.editObjField(this.add_tags_from_des, {field: 'tag_uuids'}, {options : tags_list});
                        utils.editObjField(this.edit_tags_from_des, {field: 'tag_uuids'}, {options : tags_list});
                        // loading.close()
                    })
            },
            /*初始化标签列表*/
            initTags(){
                this.tags_params.page_num = 1;
                this.tags_list=[];
                this.getTags();

            },
            /*加载更多标签*/
            loadMoreTags(val){
                this.tags_params.page_num++;
                this.getTags()


            }


        },
        mounted() {
            this.getList()
                .then(list => {
                    this.list = list;
                    this.initTags()
                })
            // this.$refs.formEditModal.show()

            /*绑定加载更多标签*/
            pageEvent.$off('loadMoreTags',)
            pageEvent.$on('loadMoreTags',(val)=>{

                this.loadMoreTags()


            })

        }
    }
</script>

<style scoped lang="less">

    @import "../../css/common";

</style>