import Vue from 'vue'
import VueRouter from 'vue-router'
import lime from "@/lime.js";

import operatorRouter from '../views/operator/operatorRouter';//运营端路由

const originalPush = VueRouter.prototype.push

VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
}
Vue.use(VueRouter)


import test from './../views/operator/pages/test/pageA'
// comm:frees 
const Login = () => import('../views/Login.vue');


// admin:logins levels
const Admin = () => import('../views/Admin.vue');

// admin 的内部页面
const Console = () => import('../views/Admin/Console.vue');
const Shop = () => import('../views/Admin/Shop.vue');
const Manage = () => import('../views/Admin/Manage.vue');
const Role   = () => import('../views/Admin/Role.vue');
const Work   = () => import('../views/Admin/Work.vue');


const routes = [
    // 登录页面
    {
        path: '/',
        component: Login,
        meta: {
            rbac: 'frees',
            title: '登录'
        }
    },
    {
        path: '/test',
        component: test,
        meta: {
            rbac: 'frees',
            title: 'test'
        }
    },
    {
        path: '/login',
        component: Login,
        meta: {
            rbac: 'frees',
            title: '登录'
        }
    },
    {
        path: '/admin',
        component: Admin,
        meta: {
            rbac: 'logins',
            title: '管理后台'
        },
        children: [
            {
                path: '/',
                component: Console,
                meta: {
                    rbac: 'logins',
                    title: '控制台'
                }
            },
            // {
            //     path: 'shop_list/:menu_uuid',
            //     component: Shop,
            //     meta: {
            //         rbac: 'logins',
            //         title: '企业商户'
            //     }
            // },
            // {
            //     path: 'manage/:menu_uuid',
            //     component: Manage,
            //     meta: {
            //         rbac: 'logins',
            //         title: '员工管理'
            //     }
            // },
            // {
            //     path: 'manage_role/:menu_uuid',
            //     component: Role,
            //     meta: {
            //         rbac: 'logins',
            //         title: '角色管理'
            //     }
            // },
            // {
            //     path: 'shop_list/:menu_uuid',
            //     component: Shop,
            //     meta: {
            //         rbac: 'logins',
            //         title: '企业商户'
            //     }
            // },
            // {
            //     path: 'manage_role/:menu_uuid',
            //     component: Role,
            //     meta: {
            //         rbac: 'logins',
            //         title: '角色管理'
            //     }
            // },
            // {
            //     path: 'work/:menu_uuid',
            //     component: Work,
            //     meta: {
            //         rbac: 'logins',
            //         title: '工作日清'
            //     }
            // },
            ...operatorRouter.Admin
        ]
    }
];

const router = new VueRouter({
    // mode: 'history', // 去除#号
    // base: process.env.BASE_URL,
    routes
});


// 路由守护处理,登录非登录的界面
router.beforeEach((to, from, next) => {
    let login_token = lime.cookie_get('login_token');

    if (lime.debug) {
        console.log(login_token);
    }

    if (login_token == null || typeof login_token == 'undefined') {
        lime.cookie_set('login_token', null, 0);
        if (to.meta.rbac != 'frees') {
            next('/login');
        } else {
            next();
        }
    } else {
        // 检测login_token是否在线
        lime.req('CheckLoginToken', {
            login_token: login_token,
            business_type: 'manage',
            login_type: 0
        }).then(res => {
            // 更新cookie的缓存,2小时
            lime.cookie_set('login_token', login_token, 7200);
            // 默认进入页面,则自动跳转到'/admin'
            if (to.path == '/') {
                next('/admin');
            } else {
                next();
            }
        }).catch(err => {
            lime.cookie_set('login_token', null, 0);
            // 检测失败,直接跳转到登录页面
            next('/login');
        })
    }
})

export default router

